<template>
    <div class="detail">
        <el-row :gutter="10">
            <div class="planMaintenanceTitle">
                <el-col :lg="6" :xl="4">
                    <el-breadcrumb separator=">">
                        <el-breadcrumb-item><i class="el-icon-s-home"></i>生产执行</el-breadcrumb-item>
                        <el-breadcrumb-item><span>工序详情</span></el-breadcrumb-item>
                    </el-breadcrumb>
                </el-col>
            </div>
            <el-col :span="18">
                <div class="ButtonTitle">
                    <el-button type="primary" size="mini">切换工序</el-button>
                    <el-button type="primary" size="mini" @click="">清线</el-button>
                    <el-button type="primary" size="mini" @click="">未装满箱</el-button>
                    <el-button type="warning" size="mini" @click="">箱合格证补打</el-button>
                    <el-button type="warning" size="mini">总成补打</el-button>
                    <el-button type="warning" size="mini" @click="">总成替换</el-button>
                    <el-button type="warning" size="mini">总成报废</el-button>
                </div>
            </el-col>

            <div class="content">
                <el-col :span="24">
                    <div class="perfromConRight">
                        <el-col :span="24">
                            <div class="qualified">
                                <div class="perfromConLeftTitle" style="position: relative;">
                                    <el-col :span="24">
                                        <h3 class="information">工单生产信息</h3>
                                        <p class="line">&nbsp;</p>
                                    </el-col>
                                </div>
                                <div class="forminput">
                                    <el-col :span="24" style="border-bottom: 1px #1950A2 solid;">
                                        <el-col :span="6">
                                            <el-form ref="form" label-width="95px" style="margin-top:1vh;">
                                                <el-form-item label="工厂：">
                                                    {{$ls.get('factory').fcName}}
                                                </el-form-item>
                                            </el-form>
                                        </el-col>
                                        <el-col :span="6">
                                            <el-form ref="form" label-width="160px" style="margin-top: 1vh;">
                                                <el-form-item label="车间：">
                                                    {{$ls.get('workshop').shopName}}</el-form-item>
                                            </el-form>
                                        </el-col>
                                        <el-col :span="6">
                                            <el-form ref="form" label-width="160px" style="margin-top: 1vh;">
                                                <el-form-item label="班次：">
                                                    {{$ls.get('shift').sfName}}
                                                </el-form-item>
                                            </el-form>
                                        </el-col>
                                        <el-col :span="6">
                                            <el-form ref="form" label-width="160px" style="margin-top: 1vh;">
                                                <el-form-item label="工序：">
                                                    {{procName}}
                                                </el-form-item>
                                            </el-form>
                                        </el-col>
                                        <el-col :span="6">
                                            <el-form ref="form" label-width="95px">
                                                <el-form-item label="工单：">{{poCode}}</el-form-item>
                                            </el-form>
                                        </el-col>

                                        <el-col :span="6">
                                            <el-form ref="form" label-width="160px">
                                                <el-form-item label="产品简码：">{{prodCode}}</el-form-item>
                                            </el-form>
                                        </el-col>
                                        <el-col :span="6">
                                            <el-form ref="form" label-width="160px">
                                                <el-form-item label="实际/计划：">
                                                </el-form-item>
                                            </el-form>
                                        </el-col>
                                        <el-col :span="6">
                                            <el-form ref="form" label-width="160px">
                                                <el-form-item label="计划时间：">
                                                </el-form-item>
                                            </el-form>
                                        </el-col>
                                    </el-col>
                                    <el-col :span="6" class="rfid">
                                        <el-form ref="form" label-width="95px" style="margin-top: 2vh;">
                                            <el-form-item label="RFID:">
                                                <el-input style="width:200px"></el-input>
                                            </el-form-item>
                                        </el-form>
                                    </el-col>
                                    <el-col :span="6" class="rfid">
                                        <el-form ref="form" label-width="160px" style="margin-top: 2vh;">
                                            <el-form-item label="成品扫描:">
                                                <el-input style="width:200px" :autofocus="true"></el-input>
                                            </el-form-item>
                                        </el-form>
                                    </el-col>
                                    <el-col :span="6">
                                        <el-form ref="form" label-width="100px"
                                            style="margin-top: 2vh;margin-left: 4.5vh;">
                                            <el-form-item>
                                                <span style="color: rgb(228, 228, 228);"> | </span>错误提示</el-form-item>
                                        </el-form>
                                    </el-col>
                                    <el-col :span="6">
                                        <div class="qualifiedIcon clear" style="margin-top:1vh">
                                            <i class="el-icon-success"></i>
                                            <span style="line-height: 5vh;">合格</span>
                                        </div>
                                    </el-col>
                                    <el-col :span="6" style="display:none">
                                        <div class="qualifiedIcon clear" style="margin-top:1vh;">
                                            <i class="el-icon-error"></i>
                                            <span style="line-height: 5vh;">不合格</span>
                                        </div>
                                    </el-col>
                                </div>
                            </div>
                        </el-col>
                        <el-col :span="12" style="height: 65vh;">
                            <el-col :span="24" style=" padding-left: 0px;">
                                <el-col class="ListingbarBox" style="height:30vh">
                                    <p style="color: rgb(253, 190, 69);padding: 10px;font-weight: bold">待结箱清单栏</p>
                                    <el-table style="width: 100%;height:28vh;overflow: scroll">
                                        <el-table-column label="总成号"></el-table-column>
                                        <el-table-column label="装箱时间"></el-table-column>
                                        <el-table-column label="操作">
                                            <template>
                                                <el-button type="warning" size="mini">跳 过</el-button>
                                            </template>
                                        </el-table-column>
                                    </el-table>
                                </el-col>
                            </el-col>
                            <el-col :span="24" style="margin-top: 2vh;">
                                <div class="ListingbarBox">
                                    <p style="color: rgb(253, 190, 69);padding: 10px;font-weight: bold">待装箱栏</p>
                                    <el-table style="width: 100%;height:28vh;overflow: scroll">
                                        <el-table-column label="首件总成号"></el-table-column>
                                        <el-table-column label="跳过时间"></el-table-column>
                                        <el-table-column label="操作">
                                            <template>
                                                <el-button type="success" size="mini">待装箱</el-button>
                                            </template>
                                        </el-table-column>
                                    </el-table>
                                </div>
                            </el-col>
                        </el-col>
                        <el-col :span="12">
                            <el-col :span="24">
                                <el-col class="ListingbarBox" style="height:64vh">
                                    <p style="color: rgb(253, 190, 69);padding: 10px;font-weight: bold">已结箱清单栏</p>
                                    <el-table style="width: 100%;height:60vh;overflow: scroll">
                                        <el-table-column label="箱条码号"></el-table-column>
                                        <el-table-column label="数量"></el-table-column>
                                        <el-table-column label="结箱时间">
                                        </el-table-column>
                                    </el-table>
                                </el-col>
                            </el-col>
                        </el-col>
                    </div>
                </el-col>
            </div>
        </el-row>
    </div>
</template>
<script>
    export default {
        data() {
            return {
                //routeData
                lineId: '',
                procCode: '',
                procName: '',
                poCode: '',
                prodCode: '',

            }
        },
        created: function () {
            this.lineId = this.$route.query.lineId
            this.procCode = this.$route.query.procCode;
            this.procName = this.$route.query.procName;
            this.poCode = this.$route.query.poCode;
            this.prodCode = this.$route.query.prodCode;
        },
        methods: {}
    }

</script>

<style>

</style>


