<template>
<div>
    <el-dialog :close-on-click-modal="false" title="箱合格证补打" :visible.sync="dialogShow" width="30%" class="dialogtitleTL" @open="onDialogOpen">
        <el-col :span="24" style="padding:4vh;border-bottom: 1px solid #2B6EAB;">
            <el-col :sm="20" :lg="20">
                <el-form label-width="120px" @submit.native.prevent>
                    <el-form-item label="箱合格证：">
                        <el-input placeholder="请扫描录入"  ref="boxcode" :autofocus="true" v-model="scanValue" v-on:keyup.enter.native="onScanEnter()" class="input100w"></el-input>
                        <!-- <p v-if="errMsg.length>0" class="p_red">{{errMsg}}</p> -->
                    </el-form-item>
                </el-form>
            </el-col>

            <el-col :sm="4" :lg="2">
                <el-form label-width="20px">
                    <el-form-item>
                        <span>
                            <img src="@/assets/images/sm.png" />
                        </span>
                    </el-form-item>
                </el-form>
            </el-col>
            <el-col :sm="19" :lg="14" class="tishi" v-if="errMsg.length > 0">
                <el-form>
                    <el-form-item><span style="color: red;">{{errMsg}}</span></el-form-item>
                </el-form>
            </el-col>
        </el-col>
        <div class="appdialog" style="text-align: center; padding: 10px;  background: #12599A;">
            <el-col :span="24" style="margin-bottom: 1.5vh;padding-right:1vh;" class="nextButton">
                <el-button type="warning" size="mini" @click="boxReprint">箱合格证补打</el-button>
            </el-col>
        </div>
    </el-dialog>
</div>
</template>

<script>
export default {
    props: ["show", "order", "boxInfo", "boxLog", "isMatBox"],
    name: "OrderPackageAuto",
    data() {
        return {
            dialogShow: this.show,
            fpBox: {},
            scanValue: "",
            errMsg: ""
        };
    },
    watch: {
        show: function (newValue, oldValue) {
            this.dialogShow = newValue;
        },
        dialogShow: function (newValue, oldValue) {
            if (this.show != this.dialogShow)
                this.$emit("update:show", this.dialogShow);
        }
    },
    methods: {
        onDialogOpen: function () {
            console.log(this.boxLog)
            if(!this.boxLog){
                this.errMsg = ''
                var self = this
                //调用装箱接口
                this.$http.request({
                    url: '/api/package/' + this.order.poCode,
                    method: 'POST',
                    showLoading: true,
                    params: {
                        stockResource: 'FP_COMP',
                        box_quan: this.boxInfo.bifCount
                    }
                }).then(function (resp) {
                    if (resp.status == 100) {
                        self.fpBox = resp.data
                        self.boxReprint();
                        self.$emit("packageDone");
                    } else {
                        self.errMsg = resp.message
                    }
                    self.$refs.boxcode.focus();
                })
            }
            else{
                this.fpBox = this.boxLog
                this.boxReprint();
                this.$refs.boxcode.focus();
            }
        },
        onScanEnter: function () {
            this.errMsg = ''
            var code = this.scanValue;
            this.scanValue = ""
            //判断是否扫描是获取到的箱合格证
            if (code !== this.fpBox.boxBarcode) {
                this.errMsg = "合格证扫码不正确！"
                return
            }
            //扫描确认
            var self = this
            this.$http.request({
                url: '/api/package/box_confirm/' + this.fpBox.boxBarcode,
                method: 'POST',
                showLoading: true
            }).then(function (resp) {
                if (resp.status == 100) {
                    self.dialogShow = false
                } else {
                    self.errMsg = resp.message
                }
            })
        },
        boxReprint: function () {
            if (!this.fpBox.boxBarcode)
                return
            if(this.isMatBox)
                this.$printer.printMatBoxLog(this.fpBox);
            else
                this.$printer.printBoxLog(this.fpBox);
            this.$refs.boxcode.focus();
        }
    }
};
</script>


