<template>
    <div>
        <el-row>
            <el-col :span="1">
                <ul class="menu_ul">
                    <li @click="jump('MachExecution')" v-bind:class="{ menu_li_Ced: activeMenu=='execution' }"><img
                            src="@/assets/icon/CV2.png">
                        <p>生产执行</p>
                    </li>
                    <li @click="jump('MaUnqualified')" v-bind:class="{ menu_li_Ced: activeMenu=='MaUnqualified' }">
                        <img src="@/assets/icon/CV3.png">
                        <p>不合格品</p>
                    </li>
                    <li @click="jump('back')" class="menu_li_back"><img src="@/assets/icon/back.png">
                        <p>返回</p>
                    </li>
                </ul>
            </el-col>
            <el-col :span="23" style="padding-left: 10px;">
                <keep-alive>
                    <router-view v-if="$route.meta.keepAlive"></router-view>
                </keep-alive>
                <router-view v-if="!$route.meta.keepAlive"></router-view>
            </el-col>
        </el-row>
    </div>
</template>

<script>
    import './index.css';
    export default {
        data() {
            return {
                activeMenu: ''
            };
        },
        watch: {
            $route: function (val, oldVal) {
                this.judgeMenu()
            }
        },
        created: function () {
            this.judgeMenu()
        },
        methods: {
            judgeMenu: function () {
                var urlPath = this.$route.path
                if (/\/machining\/execution/.test(urlPath))
                    this.activeMenu = 'execution'
                else if (/\/machining\/processdetail/.test(urlPath))
                    this.activeMenu = 'execution'
                else if (/\/machining\/MaUnqualified/.test(urlPath))
                    this.activeMenu = 'MaUnqualified'
                else if (/\/machining\/MaUnqualified_details/.test(urlPath))
                    this.activeMenu = 'MaUnqualified'
            }
        }
    }
</script>
<style scoped>

</style>


