<template>
  <div class="workadd">
    <el-row :gutter="10">
      <el-col :span="24">
        <div class="ButtonTitle">
          <el-col :span="24">
            <el-button type="warning" style="width:80px;" size="mini">提交</el-button>
            <el-button type="primary" style="width:80px;" size="mini" @click="back">返回</el-button>
          </el-col>
        </div>
      </el-col>

      <el-col :span="24" class="whead">
        <el-col :span="8">
          <el-form ref="form" label-width="160px" style="margin-top:2vh;">
            <el-form-item label="产品简码：">
              <el-select v-model="value" clearable placeholder="请选择" popper-class="select01">
                <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-form>
        </el-col>

        <el-col :span="8">
          <el-form ref="form" label-width="160px" style="margin-top:2vh;">
            <el-form-item label="发生工序：">
              <el-select v-model="value" clearable placeholder="请选择" popper-class="select01">
                <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-form>
        </el-col>

        <el-col :span="8">
          <el-form ref="form" label-width="160px" style="margin-top:2vh;">
            <el-form-item label="返工人员：">
              <el-input v-model="renyuan" placeholder="请输入"></el-input>
            </el-form-item>
          </el-form>
        </el-col>

        <el-col :span="8">
          <el-form ref="form" label-width="160px">
            <el-form-item label="时间：">
              <el-select v-model="value" clearable placeholder="请选择" popper-class="select01">
                <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-form>
        </el-col>

        <el-col :span="8">
          <el-form ref="form" label-width="160px">
            <el-form-item label="总成条码：">
              <el-input v-model="tiaoma" placeholder="请扫描（非必填项）"></el-input>
            </el-form-item>
          </el-form>
        </el-col>
      </el-col>

      <el-col :span="24">
        <div>
          <p style="color:#DAA651;margin-top: 1vh;float:left" class="m_label">零件列表</p>
        </div>

        <el-col :span="24">
          <div class="qualified">
            <el-table stripe style="width: 100%;display:none;" height="680">
              <el-table-column label="零件名称">
              </el-table-column>
              <el-table-column label="零件简码">
              </el-table-column>
              <el-table-column label="消耗数量">
                <template>
                  <el-input-number v-model="num" :min="1" :max="10" label="描述文字"></el-input-number>
                </template>
              </el-table-column>
            </el-table>

            <el-table :data="tableData" style="width: 100%">
              <el-table-column prop="date" label="日期" width="180">
              </el-table-column>
              <el-table-column prop="name" label="姓名" width="180">
              </el-table-column>
              <el-table-column prop="address" label="地址">
              </el-table-column>
              <el-table-column label="消耗数量">
                <template>
                  <el-input-number v-model="num" :min="1" :max="10" label="描述文字"></el-input-number>
                </template>
              </el-table-column>
            </el-table>
          </div>
        </el-col>
      </el-col>

    </el-row>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        bianhao: '',
        valuetime: '',
        jianma: '',
        renyuan: '',
        tiaoma: '',
        num: 1,
        options: [{
          value: '选项1',
          label: '黄金糕'
        }, {
          value: '选项2',
          label: '双皮奶'
        }, {
          value: '选项3',
          label: '蚵仔煎'
        }, {
          value: '选项4',
          label: '龙须面'
        }, {
          value: '选项5',
          label: '北京烤鸭'
        }],
        value: '',
         tableData: [{
            date: '2016-05-02',
            name: '王小虎',
            address: '上海市普陀区金沙江路 1518 弄'
          }, {
            date: '2016-05-04',
            name: '王小虎',
            address: '上海市普陀区金沙江路 1517 弄'
          }, {
            date: '2016-05-01',
            name: '王小虎',
            address: '上海市普陀区金沙江路 1519 弄'
          }, {
            date: '2016-05-03',
            name: '王小虎',
            address: '上海市普陀区金沙江路 1516 弄'
          }]
      }
    },
    mounted: function () {
    },
    methods: {
      back() {
        this.$router.go(-1); //返回上一层
      },
    }
  }

</script>
<style>

</style>



