<template>
    <div>
        <el-dialog :close-on-click-modal="false" title="清线" :visible.sync="dialogShow" width="60%" @open="onDialogOpen"
            @close="exit">
            <el-col :span="24" class="dialogtitleSL">
                <!-- 步骤条   s_finish为高亮 -->
                <el-col class="stepBox">
                    <span class="stepLine s_finish"></span>
                    <div class="stepNum s_finish">
                        <p>1</p><span>盘点</span>
                    </div>
                    <span class="stepLine s_finish"></span>
                    <div v-bind:class="{ s_finish: activeStep>0}" class="stepNum">
                        <p>2</p><span>扫描验证</span>
                    </div>
                    <span v-bind:class="{ s_finish: activeStep>0}" class="stepLine"></span>
                </el-col>
            </el-col>
            <el-col :span="24" style="margin-bottom: 2vh;height:2vh;background:#0f4c8b;"></el-col>
            <el-col :span="24" class="dialogbodySL">
                <div v-if="activeStep == 0" class="clear">
                    <el-table style="width: 100%" height="330px" :data="stockLogs">
                        <el-table-column label="物料简码" prop="matCode"></el-table-column>
                        <el-table-column label="箱条码编号" prop="boxBarcode"></el-table-column>
                        <el-table-column label="物料数量" prop="boxQuan"></el-table-column>
                        <el-table-column label="物料剩余数量" prop="boxQuanLeft"></el-table-column>
                        <el-table-column label="操作">
                            <template slot-scope="scope">
                                <el-button type="warning" size="mini" @click="DeleteDialog(scope.$index)">
                                    修改
                                </el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </div>
                <div v-if="activeStep == 1">
                    <el-col :span="11">
                        <el-form ref="form" label-width="140px" @submit.native.prevent>
                            <el-form-item label="扫描箱合格证：">
                                <el-input placeholder="请扫描录入" v-model="boxBarcodeInput"
                                    v-on:keyup.enter.native="onBoxBarcodeScan"></el-input>
                                <span v-if="errMsg.length>0" class="p_red">{{errMsg}}</span>
                            </el-form-item>
                        </el-form>
                    </el-col>
                    <el-col :span="2">
                        <el-form ref="form" label-width="40px">
                            <el-form-item>
                                <span>
                                    <img src="@/assets/images/sm.png" />
                                </span>
                            </el-form-item>
                        </el-form>
                    </el-col>

                    <el-col :span="24" style="position: relative;padding-bottom: 10px;">
                        <h3
                            style="position: absolute; top: 2vh; left: 5%; background: #12599A; color: rgb(253, 190, 69);font-size: 16px;">
                            物料信息</h3>
                        <p
                            style="height: 1px; float: left; margin-top: 3vh; width: 100%; border: 0.5px dashed rgb(253, 190, 69); box-sizing: border-box;">
                            &nbsp;</p>
                    </el-col>

                    <el-col :span="24" class="scanning">
                        <el-table style="width: 100%" height="230px" :data="cleanLogs"
                            :row-class-name="tableRowClassName">
                            <el-table-column label="物料简码" prop="matCode"></el-table-column>
                            <el-table-column label="原箱条码编号" prop="boxBarcodeOld"></el-table-column>
                            <el-table-column label="物料数量" prop="clRestCount"></el-table-column>
                            <el-table-column label="新箱条码编号" prop="boxBarcodeNew"></el-table-column>
                            <el-table-column label="是否已确认">
                                <template slot-scope="scope">{{scope.row.isConfirmed ? '是':'否'}}</template>
                            </el-table-column>
                            <el-table-column label="操作">
                                <template slot-scope="scope">
                                    <img src="@/assets/icon/refresh.png" />
                                    <span @click="rePrint(scope.row)">补打箱合格证</span>
                                </template>
                            </el-table-column>
                        </el-table>
                    </el-col>
                </div>

                <div class="appdialog" style="float:right;padding: 10px;">
                    <span slot="footer" class="dialog-footer" v-if="activeStep == 0">
                        <el-button type="warning" size="mini" @click="onNext">下一步</el-button>
                    </span>
                    <span slot="footer" class="dialog-footer" v-if="activeStep == 1">

                        <el-button type="primary" size="mini" @click="dialogVisible2 = true">工单冻结</el-button>
                        <el-button type="warning" size="mini" @click="onConfirm">确认关闭工单</el-button>
                    </span>
                </div>
            </el-col>
        </el-dialog>
        <!--工单冻结弹出框-->
        <div>
            <el-dialog title="工单冻结" :visible.sync="dialogVisible2" width="30%" class="dialogtitleTL">
                <el-col :span="24" style="padding:4vh;border-bottom: 1px solid #2B6EAB;">
                    <el-col :span="24">
                        <el-form ref="form" label-width="120px">
                            <el-form-item label="冻结原因：">
                                <el-select v-model="ChoseReason" placeholder="请选择" popper-class="select01">
                                    <el-option v-for="item in options" :key="item.value" :label="item.label"
                                        :value="item.value"></el-option>
                                </el-select>
                            </el-form-item>
                        </el-form>
                    </el-col>
                </el-col>
                <div class="appdialog" style="text-align: center;padding: 10px;  background: #12599A;">
                    <span slot="footer" class="dialog-footer">
                        <el-button style=" margin-top: 1vh;" type="warning" @click="frozenConfirm">确 定</el-button>
                        <el-button style=" margin-top: 1vh;" type="info" @click="dialogVisible2 = false">取 消</el-button>
                    </span>
                </div>
            </el-dialog>
            <inputCard :show.sync="ifcardShow" @pass="cardCallBack"></inputCard>
        </div>
    </div>
</template>

<script>
    import inputCard from "@/components/card_input.vue";
    export default {
        components: {
            inputCard: inputCard,
        },
        props: ["show", "order", "initCleanLogs", "skipPageOne"],
        name: 'OrderStockCline',
        data: function () {
            return {
                dialogShow: this.show,
                stockLogs: [],
                scanValue: "",
                errMsg: '',
                activeStep: 0,
                cleanLogs: [],
                boxBarcodeInput: '',
                dialogVisible2: false,
                ChoseReason: '',
                options: [{
                    value: "1",
                    label: "物料不足"
                },
                {
                    value: "2",
                    label: "工装不足"
                },
                {
                    value: "3",
                    label: "换班"
                },
                {
                    value: "4",
                    label: "其他"
                }
                ],
                ifcardShow: false,
                userInfo: [],
                ifUpdateBoxQuanLeft: false,//是否修改过剩余数量
            };
        },
        watch: {
            show: function (newValue, oldValue) {
                this.dialogShow = newValue;
            },
            dialogShow: function (newValue, oldValue) {
                if (this.show != this.dialogShow)
                    this.$emit("update:show", this.dialogShow);
            }
        },
        mounted: function () {

        },
        methods: {
            onDialogOpen: function () {
                this.ifcardShow = false
                if (this.skipPageOne) {
                    this.onNext()
                }
                else {
                    this.activeStep = 0
                    var self = this

                    // 查询工单库存
                    this.$http.request({
                        url: '/api/po_stack/' + this.order.poCode,
                        method: 'GET',
                        params: {
                            is_cleaned: false
                        }
                    }).then(function (resp) {
                        if (resp.data instanceof Array) {
                            self.stockLogs = resp.data
                            //如果没有剩余物料，直接进入清线确认界面
                            if (self.stockLogs.length == 0) {
                                self.onSecondPage()
                            }
                        }
                    })
                }
            },
            onNext: function () {
                if (this.ifUpdateBoxQuanLeft) {
                    //修改过数量 需刷卡
                    this.ifcardShow = true
                } else {
                    // 调用清线接口
                    var self = this
                    this.$http.request({
                        url: '/api/po_stack/' + this.order.poCode + '/clean',
                        method: 'POST',
                        showLoading: true,
                        params: {
                            confirmPersonName: this.userInfo.userName,
                            confirmPersonNumber: this.userInfo.userAccount
                        },
                        data: this.stockLogs
                    }).then(function (resp) {
                        if (resp.status == 100) {
                            self.onSecondPage()
                            self.errMsg = ''
                        } else {
                            self.$notify.error({
                                title: '错误',
                                message: resp.message
                            });
                        }
                    })
                }

            },
            onSecondPage: function () {
                var self = this;
                //查询有无未做完的清线
                this.$http.request({
                    url: '/api/po_stack/cleanlog?order_code=' + self.order.poCode + '&is_confirmed=false',
                    method: 'GET'
                }).then(function (resp) {
                    self.cleanLogs = resp.data.list;
                    if (self.cleanLogs.length > 0) {
                        for (var i = 0; i < self.cleanLogs.length; i++) {
                            self.$printer.printCleanLog(self.cleanLogs[i])
                        }
                    }
                    self.activeStep = 1
                })
            },
            onBoxBarcodeScan: function () {
                var boxBarcodeInputTmp = this.boxBarcodeInput
                this.boxBarcodeInput = ''

                var cleanlog = null
                for (var i = 0; i < this.cleanLogs.length; i++) {
                    if (this.cleanLogs[i].boxBarcodeNew == boxBarcodeInputTmp) {
                        cleanlog = this.cleanLogs[i]
                        break;
                    }
                }
                if (cleanlog == null) {
                    //提示错误
                    this.$notify.error({
                        title: '错误',
                        message: '没有匹配的合格证!'
                    });
                } else {
                    var self = this
                    this.$http.request({
                        url: '/api/po_stack/clean_confirm/' + cleanlog.boxBarcodeNew,
                        method: 'POST'
                    }).then(function (resp) {
                        if (resp.status == 100) {
                            cleanlog.isConfirmed = true
                        } else {
                            self.errMsg = resp.message
                        }
                    })
                }

            },
            onConfirm: function () {
                this.$confirm('关闭工单将无法恢复，是否确认关闭?', '提示', {
                    confirmButtonClass: 'el-button--danger',
                    confirmButtonText: '确定关闭',
                    cancelButtonClass: 'el-button--warning',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    //检查是否都确认过
                    for (var i = 0; i < this.cleanLogs.length; i++) {
                        if (!this.cleanLogs[i].isConfirmed) {
                            this.$notify.error({
                                title: '错误',
                                message: '需要扫描确认所有合格证!'
                            });
                            return;
                        }
                    }
                    //关闭工单
                    var self = this
                    this.$http.request({
                        url: '/api/order/' + this.order.poCode + '/close',
                        method: 'POST',
                        showLoading: true
                    }).then(function (resp) {
                        if (resp.status == 100) {
                            //关闭模态框
                            self.dialogShow = false
                            self.$emit("orderClosed")
                        } else {
                            self.$notify.error({
                                title: '错误',
                                message: resp.message
                            });
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消'
                    });
                });

            },
            rePrint: function (data) {
                this.$printer.printCleanLog(data)
            },
            frozenConfirm: function () {
                if (this.ChoseReason == null || this.ChoseReason == '') {
                    this.$notify.error({
                        title: '错误',
                        message: '请先选择冻结原因'
                    });
                    return;
                }
                var self = this;
                var reasonName = this.options.find((item) => {
                    return item.value === self.ChoseReason;
                })

                this.$http.request({
                    url: '/api/order/' + this.order.poCode + '/freeze?freeze_reason=' + reasonName.label + '&freeze_id=' + this.ChoseReason,
                    method: 'POST',
                }).then(function (resp) {
                    if (resp.status != 100) {
                        self.$notify.error({
                            title: '错误',
                            message: '工单：' + self.order.poCode + '冻结失败，原因：' + resp.message
                        });
                    } else {
                        self.$notify({
							title: '成功',
							message: '工单已冻结',
							type: 'success'
						});
                    }
                    self.dialogVisible2 = false;
                    self.exit();
                })

            },
            tableRowClassName: function ({
                row,
                rowIndex
            }) {
                if (row.isConfirmed) {
                    return 'bgGreenRow';
                }
                return '';
            },
            DeleteDialog: function (index) {
                var self = this
                this.$prompt('请修改剩余数量', '修改', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    closeOnClickModal: false,
                    inputValue: this.stockLogs[index].boxQuanLeft,
                }).then(({ value }) => {
                    if (this.stockLogs[index].boxQuanLeft != value)
                        this.ifUpdateBoxQuanLeft = true
                    this.stockLogs[index].boxQuanLeft = value
                })
            },
            //刷卡回调
            cardCallBack: function (userInfo) {
                if (userInfo) {
                    this.userInfo = userInfo
                    this.ifUpdateBoxQuanLeft = false
                    this.onNext()
                }
            },
            exit() {
                this.$emit("orderClosed")
            }
        }
    };
</script>


