<template>
    <div class="app2" style="height: 90vh;">
        <el-row :gutter="10">
            <el-col :span="24">
                <div class="ButtonTitle">
                    <el-button type="primary" size="mini" @click="save">暂存</el-button>
                    <el-button type="primary" size="mini" @click="commit">提交</el-button>
                    <el-button type="warning" size="mini" @click="cancel">检测取消</el-button>
                </div>
            </el-col>

            <div class="content">
                <el-col :span="24">
                    <div class="perfromConRight">
                        <el-col :span="8" style="height: 81vh;">
                            <el-col style="height:30vh;" class="qualified slbox1">
                                <el-col style="margin-top: 2vh;">
                                    <el-form ref="form" label-width="100px">
                                        <el-form-item label="扫描箱号:">
                                            <el-input v-model="inputBoxCode" v-on:keyup.enter.native="querySafeLunch" style="width:200px"></el-input>
                                        </el-form-item>
                                    </el-form>
                                </el-col>
                                <el-col>
                                    <el-form ref="form" label-width="100px">
                                        <el-form-item label="简码:">
                                            {{boxInfo.matNumber}}
                                        </el-form-item>
                                    </el-form>
                                </el-col>
                                <el-col>
                                    <el-form ref="form" label-width="100px">
                                        <el-form-item label="名称:">
                                            {{boxInfo.matName}}
                                        </el-form-item>
                                    </el-form>
                                </el-col>
                                <el-col>
                                    <el-form ref="form" label-width="100px">
                                        <el-form-item label="批号:">
                                            {{boxInfo.matBatchNo}}
                                        </el-form-item>
                                    </el-form>
                                </el-col>
                                <el-col>
                                    <el-form ref="form" label-width="100px">
                                        <el-form-item label="数量:">
                                            {{boxInfo.boxQuan}}
                                        </el-form-item>
                                    </el-form>
                                </el-col>
                            </el-col>
                            <el-col class="qualified" style="height:51vh; margin-top: 2vh;padding: 0px;">
                                <el-table style="width: 100%;height:51vh" class="sltabel01" :data="fpArr"
                                    :stripe="true">
                                    <el-table-column type="index" label="序号"></el-table-column>
                                    <el-table-column label="总成号" prop="fpBarcode"></el-table-column>
                                </el-table>
                            </el-col>
                        </el-col>
                        <el-col :span="16" style="height: 81vh;">
                            <el-col style="height: 11vh;padding-top: 2.5vh;" class="qualified">
                                <el-col class="rfid">
                                    <el-form ref="form" label-width="100px">
                                        <el-form-item label="扫描总成号:">
                                            <el-input v-model="pfbarcode" style="width:200px" v-on:keyup.enter.native="onBarCodeScan"></el-input>
                                        </el-form-item>
                                    </el-form>
                                </el-col>
                            </el-col>
                            <el-col style="height: 70vh;">
                                <el-col class="qualified" style="height:70vh; margin-top: 2vh;padding: 0;">
                                    <el-table style="width: 100%;height:70vh" class="sltabel01" :data="safelunchOrder.safelunchProductLogPOs"
                                        :stripe="true">
                                        <el-table-column label="序号" type="index"></el-table-column>
                                        <el-table-column label="总成号条码" prop="fpBarcode"></el-table-column>
                                        <el-table-column label="录入时间" prop="createTime"></el-table-column>
                                        <el-table-column label="是否合格">
                                            <template slot-scope="scope">
                                                    <el-switch v-model="scope.row.isPass"
                                                    active-color="#13ce66" inactive-color="#ff4949"></el-switch>
                                            </template>
                                        </el-table-column>
                                    </el-table>
                                </el-col>
                            </el-col>
                        </el-col>
                    </div>
                </el-col>
            </div>
        </el-row>
    </div>
</template>
<script>

export default {
    data() {
        return {
            safelunchOrder: {
                safelunchProductLogPOs: []
            },
            inputBoxCode: '',
            pfbarcode: '',
            boxInfo:{},
            fpArr:[],
            errMsg: '',
            pfInfo:[],
        };
    },
    created: function () {
    },
    methods: {
        querySafeLunch: function() {
            var self=this;
            this.$http.request({
                url:'/api/safelunch/order',
                method:'GET',
                showLoading: true,
                params: {
                    boxBarcode: this.inputBoxCode
                }
            }).then(function(resp){
                if(resp.data.length > 0) {
                    var safelunchOrder = resp.data[0]
                    if(safelunchOrder.isCommit) {
                        self.$notify.error({
                            title: '失败',
                            message: '该箱已做过SafeLunch,不能重做!'
                        });
                        self.inputBoxCode = ''
                        return
                    }
                    self.safelunchOrder = safelunchOrder
                    self.queryHUInfo(false)
                }
                else {
                    self.queryHUInfo(true)
                }
            })
        },
        queryHUInfo: function(buildOrder){
            var self=this;
            this.$http.request({
                url:'/api/package/'+this.inputBoxCode,
                method:'GET',
                showLoading: true
            }).then(function(resp){
                if(resp.status == 100 && resp.data.length > 0){
                    self.boxInfo = resp.data[0];
                    // 构造Safelunch工单
                    if(buildOrder)
                        self.buildSafeLunchOrder()

                    self.queryFPInfo()
                }
                else {
                    self.errMsg = "找不到该箱合格证信息！"
                }
            })
        },
        queryFPInfo:function(){
            var self=this;
            this.$http.request({
                url:'/api/finished_product?box_barcode='+this.inputBoxCode,
                showLoading: true,
                method:'GET',
            }).then(function(resp){
                if(resp.status == 100 && resp.data.list.length > 0){
                    self.fpArr = resp.data.list;
                }
                else {
                    self.errMsg = '找不到总成信息，请确认箱合格证是否正确'
                }
            })
        },
        buildSafeLunchOrder: function() {
            this.safelunchOrder.saflineId = this.$ls.get('safeluncLine').saflineId
            this.safelunchOrder.boxBarcode = this.boxInfo.matHuident
            this.safelunchOrder.productCount = this.boxInfo.boxQuan
            this.safelunchOrder.passCount = this.boxInfo.boxQuan
            this.safelunchOrder.failedCount = 0
            this.safelunchOrder.isPass = true
            this.safelunchOrder.isCommit = false
            this.safelunchOrder.checkPersonNumber = this.$ls.get('user').empNumber
            this.safelunchOrder.checkPersonName = this.$ls.get('user').userName
            this.safelunchOrder.safelunchProductLogPOs = []
        },
        onBarCodeScan:function(){
            var code = this.pfbarcode;
            this.pfbarcode = '';
            this.errMsg = ''

            for(var i = 0; i < this.safelunchOrder.safelunchProductLogPOs.length; i++) {
                if(this.safelunchOrder.safelunchProductLogPOs[i].fpBarcode === code) 
                    return
            }

            for(var i = 0; i < this.fpArr.length; i++) {
                if(this.fpArr[i].fpBarcode === code) {
                    var SafelunchProductLogPO = {
                        'fpBarcode': code,
                        'boxBarcode': this.safelunchOrder.boxBarcode,
                        'isPass': true,
                        'createTime': this.$moment().format('YYYY-MM-DD HH:mm:SS')
                    }
                    this.safelunchOrder.safelunchProductLogPOs.push(SafelunchProductLogPO)
                    return
                }
            }

            this.errMsg = '未找到该成品条码';
        },
        save: function() {
            var self = this
            this.$http.request({
                url:'/api/safelunch/order/save',
                method:'POST',
                showLoading: true,
                data: this.safelunchOrder
            }).then(function(resp){
                if(resp.status == 100){
                    self.$notify.success({
                        title: '成功',
                        message: '保存成功'
                    });
                    self.cancel()
                }
                else {
                    self.errMsg = resp.message
                }
            })
        },
        cancel: function() {
            this.safelunchOrder = {
                safelunchProductLogPOs: []
            }
            this.boxInfo = {}
            this.fpArr = []
            this.inputBoxCode = ''
        },
        commit: function() {
            //内容检测
            if(this.safelunchOrder.safelunchProductLogPOs.length != this.fpArr.length){
                this.$notify.error({
                    title: '失败',
                    message: '该箱成品没有全部检测完成,不能提交!'
                });
                return
            }

            var passCount = 0
            var failedCount = 0
            for(var i = 0; i < this.safelunchOrder.safelunchProductLogPOs.length; i++) {
                if(this.safelunchOrder.safelunchProductLogPOs[i].isPass)
                    passCount++
                else
                    failedCount++
            }
            this.safelunchOrder.passCount = passCount
            this.safelunchOrder.failedCount = failedCount
            this.safelunchOrder.isPass = failedCount == 0;

            //内容确认
            this.$confirm('本次检验结束，通过数量：'+ passCount +'，失败数量：'+ failedCount 
                +'，确认提交？', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.commitToServer()
            })
        },
        commitToServer: function() {
            var self = this
            this.$http.request({
                url:'/api/safelunch/order/commit',
                method:'POST',
                showLoading: true,
                data: this.safelunchOrder
            }).then(function(resp){
                if(resp.status == 100){
                    self.$notify.success({
                        title: '成功',
                        message: '提交成功'
                    });
                    self.cancel()
                }
                else {
                    self.errMsg = resp.message
                }
            })
        }
    }
};
</script>

<style>
</style>


