<template>
    <div>
        <el-row>
            <el-col :span="1">
                <ul class="menu_ul">
                    <li
                        @click="jump('CvPreparation')"
                        v-bind:class="{ menu_li_Ced: activeMenu=='Preparation' }"
                    >
                        <img src="@/assets/icon/CV1.png" />
                        <p>装配准备</p>
                    </li>
                    <li
                        @click="jump('CvExecution')"
                        v-bind:class="{ menu_li_Ced: activeMenu=='Execution' }"
                    >
                        <img src="@/assets/icon/CV2.png" />
                        <p>装配执行</p>
                    </li>
                    <li
                        @click="jump('CvUnqualified')"
                        v-bind:class="{ menu_li_Ced: activeMenu=='CvUnqualified' }"
                    >
                        <img src="@/assets/icon/CV3.png" />
                        <p>不合格品</p>
                    </li>
                    <li @click="jump('back')" class="menu_li_back">
                        <img src="@/assets/icon/back.png" />
                        <p>返回</p>
                    </li>
                </ul>
            </el-col>
            <el-col :span="23" style="padding-left: 10px;">
                <router-view></router-view>
            </el-col>
        </el-row>
    </div>
</template>

<script>
import "./index.css";
export default {
    name: "CvIndex",
    data() {
        return {
            activeMenu: ''
        };
    },
    watch: {
        $route: function(val, oldVal){
            this.judgeMenu()
        }
    },
    created: function() {
        this.judgeMenu()
    },
    methods: {
        judgeMenu: function(){
            var urlPath = this.$route.path
            if(/\/cv_assy\/preparation/.test(urlPath))
                this.activeMenu = 'Preparation'
            else if(/\/cv_assy\/execution/.test(urlPath))
                this.activeMenu = 'Execution'
            else if(/\/cv_assy\/CvUnqualified/.test(urlPath))
                this.activeMenu = 'CvUnqualified'
            else if(/\/cv_assy\/CvUnqualified_details/.test(urlPath))
                this.activeMenu = 'CvUnqualified'
        }
    }
};
</script>
<style scoped>
</style>



