<template>
    <div class="unqualified">
        <el-row :gutter="10">
            <el-col :span="24">
                <div class="content">
                    <el-col :span="24">
                        <div class="perfromConLeft">
                            <el-col :span="20">
                                <el-col :span="8">
                                    <el-form ref="form" label-width="100px" style="margin-top: 2vh;">
                                        <el-form-item label="SafeLunch线：">
                                            <el-select v-model="safelunchLineSelected" placeholder="请选择">
                                                <el-option v-for="item in safelunchLineArr" :key="item.saflineId"
                                                    :label="item.saflineName" :value="item">
                                                </el-option>
                                            </el-select>
                                        </el-form-item>
                                    </el-form>
                                </el-col>

                                <el-col :span="8">
                                    <el-form ref="form" label-width="100px" style="margin-top: 2vh;">
                                        <el-form-item label="日期：">
                                            <el-date-picker v-model="queryDate" type="date" placeholder="选择日期">
                                            </el-date-picker>
                                        </el-form-item>
                                    </el-form>
                                </el-col>

                                <el-col :span="8">
                                    <el-form ref="form" label-width="100px" style="margin-top: 2vh;">
                                        <el-form-item label="箱号：">
                                            <el-input v-model="queryBoxbarcode"></el-input>
                                        </el-form-item>
                                    </el-form>
                                </el-col>

                                <el-col :span="8">
                                    <el-form ref="form" label-width="100px" style="margin-top: 2vh;">
                                        <el-form-item label="提交装：">
                                            <el-select v-model="queryConfirmed" placeholder="请选择">
                                                <el-option label="已提交" :value="true"></el-option>
                                                <el-option label="未提交" :value="false"></el-option>
                                            </el-select>
                                        </el-form-item>
                                    </el-form>
                                </el-col>
                            </el-col>
                            <el-col :span="4">
                                <el-col>
                                        <el-form ref="form" label-width="100px" style="margin-top: 2vh;">
                                            <el-form-item>
                                                    <el-button type="warning" size="mini" @click="safelaunchQuery">
                                                        查询
                                                    </el-button>
                                            </el-form-item>
                                        </el-form>
                                    </el-col>
                            </el-col>

                            <el-col :span="24">
                                <div style="float:left;margin-top: 0.5vh;margin-bottom: 0.5vh;">
                                    SafeLaunch记录
                                </div>
                                <div class="qualified mhtabel">
                                    <el-table stripe :data="safelunchOrderArr" style="width: 100%;height:67vh">
                                        <el-table-column prop="boxBarcode" label="合格证号">
                                        </el-table-column>
                                        <el-table-column prop="productCount" label="总成数量">
                                        </el-table-column>
                                        <el-table-column prop="passCount" label="通过数量">
                                        </el-table-column>
                                        <el-table-column prop="failedCount" label="不合格品数量">
                                        </el-table-column>
                                        <el-table-column label="是否提交">
                                            <template slot-scope="scope">
                                                {{scope.row.isCommit ? '已提交' : '未提交'}}
                                            </template>
                                        </el-table-column>
                                        <el-table-column prop="checkPersonName" label="检验人">
                                        </el-table-column>
                                        <el-table-column prop="createTime" label="检验日期">
                                        </el-table-column>
                                        <el-table-column  label="操作">
                                            <template slot-scope="scope" v-if="scope.row.boxPass && scope.row.boxPass!=''">
                                                <el-button type="warning" size="mini" @click="boxBarcodeReprint(scope.row)">合格证补打</el-button>
                                            </template>
                                         </el-table-column>
                                    </el-table>
                                </div>
                            </el-col>
                        </div>
                    </el-col>
                </div>
            </el-col>
        </el-row>
    </div>
</template>


<script>
    export default {
        data() {
            return {
                safelunchLineArr: [],
                safelunchLineSelected: {},
                queryDate: '',
                queryBoxbarcode: '',
                queryConfirmed: false,
                
                safelunchOrderArr: []
            };
        },
        created: function() {
            //获取产线结构
            var self = this
            this.$http.request({
                url: '/api/safelunch/workline',
                method: 'GET',
                showLoading: true,
                params: {
                    fcId: this.$ls.get('factory').fcId
                }
            }).then(function(resp){
                self.safelunchLineArr = resp.data.list
            })
        },
        methods: {
            safelaunchQuery: function(){
                var queryParams = {}
                if(this.safelunchLineSelected.saflineId)
                    queryParams.saflineId = this.safelunchLineSelected.saflineId
                if(this.queryBoxbarcode.length > 0)
                    queryParams.boxBarcode = this.queryBoxbarcode
                queryParams.isCommit = this.queryConfirmed

                var self=this;
                this.$http.request({
                    url:'/api/safelunch/order',
                    method:'GET',
                    showLoading: true,
                    params: queryParams
                }).then(function(resp){
                    self.safelunchOrderArr = resp.data.list
                })
            },
            boxBarcodeReprint: function(safelunchOrder){
                var self = this
                if(safelunchOrder.boxPass != ''){
                    this.$http.request({
                        url: '/api/package/' + safelunchOrder.boxPass,
                        method: 'GET',
                        showLoading: true
                    }).then(function (resp) {
                        var pckgs = resp.data
                        if (pckgs && pckgs.length > 0) {
                            self.$printer.printPackage(pckgs[0])
                        }
                    })
                }
                if(safelunchOrder.boxFailed != ''){
                    this.$http.request({
                        url: '/api/package/' + safelunchOrder.boxFailed,
                        method: 'GET',
                        showLoading: true
                    }).then(function (resp) {
                        var pckgs = resp.data
                        if (pckgs && pckgs.length > 0) {
                            self.$printer.printPackage(pckgs[0])
                        }
                    })
                }
            }
        }
    };

</script>

<style>
    .unqualified .el-select .el-input.is-focus .el-input__inner {
        border-color: #C0C4CC;
    }

    .unqualified .el-input__inner {
        background-color: transparent;
        color: #fff;
        height: 3.5vh;
    }

    .unqualified .el-select .el-input__inner:focus {
        border-color: #C0C4CC;
    }

    .unqualified .button .el-form-item__content {
        line-height: 15vh;
    }

    .unqualified .el-button--mini,
    .unqualified .button .el-button--mini.is-round {
        padding: 7px 25px;
    }

    .unqualified .el-table {
        border: 1px solid #5a92c7;
        background-color: #0B3E7C;
    }

    .unqualified .el-table th>.cell {
        background-color: #12599A;
        line-height: 4vh;
    }

    .unqualified .el-table--striped .el-table__body tr.el-table__row--striped td {
        background-color: #0F4C8B !important;
    }

    .unqualified .el-button--primary {
        color: #FFF;
        background-color: #2C77BC;
        border-color: #2C77BC;
    }

    .unqualified .el-table .el-button--mini {
        padding: 5px 20px;
    }

    .unqualified .ButtonTitle {
        top: 0.5vh;
    }
</style>


