<template>
    <el-dialog
        title="工单上料"
        :close-on-click-modal="false"
        :visible.sync="dialogShow"
        width="70%"
        @open="queryFeedingLog"
    >
        <el-tabs v-model="activeName">
            <el-tab-pane label="上料" name="上料">
                <el-col class="dialogtitleSL">
                    <el-col :span="8">
                        <el-form ref="form" label-width="100px">
                            <el-form-item label="工单号：">{{order.poCode}}</el-form-item>
                        </el-form>
                    </el-col>
                    <el-col :span="8">
                        <el-form ref="form">
                            <el-form-item label="完工简码：">{{order.prodCode}}</el-form-item>
                        </el-form>
                    </el-col>

                    <el-col :span="8">
                        <el-form ref="form">
                            <el-form-item label=" ">-</el-form-item>
                        </el-form>
                    </el-col>

                    <el-col :span="12">
                        <el-form ref="form" @submit.native.prevent>
                            <el-form-item label="条码扫描：" label-width="100px">
                                <el-input
                                    ref="boxCode"
                                    v-model="scanValue"
                                    placeholder="请扫描条码"
                                    @keyup.enter.native="onScan"
                                ></el-input>
                            </el-form-item>
                        </el-form>
                    </el-col>
                    <el-col :sm="4" :lg="8">
                        <el-form ref="form" label-width="20px">
                            <el-form-item>
                                <span>
                                    <img src="@/assets/images/sm.png" />
                                </span>
                                <span
                                    v-if="errMsg.length>0"
                                    class="p_red"
                                    style="margin-left:5px;display:  inline-block;"
                                >{{errMsg}}</span>
                            </el-form-item>
                        </el-form>
                    </el-col>
                    <!-- <el-col :span="8" v-if="errMsg.length != ''">
                <el-form ref="form" label-width="20px">
                    <el-form-item label=""><span style="color: red;">{{errMsg}}</span></el-form-item>
                </el-form>
                    </el-col>-->
                </el-col>
                <el-col :span="24" style="margin-top: 5vh;height:2vh;background:#0f4c8b;"></el-col>

                <el-col :span="24" class="dialogbodySL">
                    <el-table style="width: 100%" height="330px" :data="stockLogs">
                        <el-table-column label="序号" type="index" :index="indexMethod"></el-table-column>
                        <el-table-column label="物料简码" prop="matCode"></el-table-column>
                        <el-table-column label="物料名称" prop="matName"></el-table-column>
                        <el-table-column label="剩余数量" prop="boxQuanLeft"></el-table-column>
                        <el-table-column label="原有数量" prop="boxQuan"></el-table-column>
                        <el-table-column label="批次" prop="matBatchNo"></el-table-column>
                        <el-table-column label="炉号" prop="matFurnaceNo"></el-table-column>
                        <el-table-column label="箱合格证号" prop="boxBarcode"></el-table-column>
                    </el-table>
                    <div class="appdialog" style="text-align: center;">
                        <span slot="footer" class="dialog-footer">
                            <el-col
                                :span="24"
                                style="margin-bottom: 1.5vh;padding-right:1vh;"
                                class="nextButton"
                            >
                                <el-button type="warning" size="mini" @click="exitmodal">关 闭</el-button>
                            </el-col>
                        </span>
                    </div>
                </el-col>
            </el-tab-pane>
            <el-tab-pane label="上料统计" name="上料统计">
                <el-col :span="24" class="dialogbodySL">
                    <el-table
                        style="width: 100%;height:calc(15vh + 345px);overflow: scroll"
                        :data="stockBomArr"
                    >
                        <el-table-column label="物料号" prop="materialCode"></el-table-column>
                        <el-table-column label="名称" prop="materialName"></el-table-column>
                        <el-table-column label="上料数量" prop="materialNumLeft"></el-table-column>
                        <el-table-column label="差额" prop="materialNumLack"></el-table-column>
                    </el-table>
                    <div class="appdialog" style="text-align: center;">
                        <span slot="footer" class="dialog-footer">
                            <el-col
                                :span="24"
                                style="margin-bottom: 1.5vh;padding-right:1vh;"
                                class="nextButton"
                            >
                                <el-button type="warning" size="mini" @click="exitmodal">关 闭</el-button>
                            </el-col>
                        </span>
                    </div>
                </el-col>
            </el-tab-pane>
        </el-tabs>
    </el-dialog>
</template>

<script>
export default {
    props: ["show", "order"],
    name: "OrderFeeding",
    data() {
        return {
            dialogShow: this.show,
            stockLogs: [],
            scanValue: "",
            errMsg: "",
            activeName: "上料",
            stockBomArr: []
        };
    },
    watch: {
        show: function(newValue, oldValue) {
            this.dialogShow = newValue;
        },
        dialogShow: function(newValue, oldValue) {
            if (this.show != this.dialogShow)
                this.$emit("update:show", this.dialogShow);
        }
    },
    methods: {
        queryFeedingLog: function() {
            this.errMsg = "";
            //查询上料记录
            var self = this;
            this.$http
                .request({
                    url: "/api/po_stack/" + this.order.poCode,
                    method: "GET",
                    params: {
                        is_cleaned: false
                    }
                })
                .then(function(resp) {
                    if (resp.data instanceof Array) {
                        self.stockLogs = resp.data;
                    }

                    self.$refs.boxCode.focus();
                });
            this.queryOrderStockBomView();
        },
        //关闭时触发
        exitmodal() {
            this.dialogShow = false;
        },
        onScan: function () {
            var scanValueTmp = this.scanValue
            this.scanValue = ''
            if (scanValueTmp.indexOf("|") > 0){
                scanValueTmp=scanValueTmp.split('|')[0];
            }

            this.errMsg = ''

            var self = this
            //获取HU信息
            this.$http
                .request({
                    url: "/api/package/" + scanValueTmp,
                    method: "GET",
                    showLoading: true
                })
                .then(function(resp) {
                    var pckgs = resp.data;
                    if (resp.status != 100) {
                        self.errMsg = resp.message;
                        return;
                    }
                    if (resp.data == null) {
                        self.errMsg = "未查询到合格证信息";
                        return;
                    }

                    self.$http
                        .request({
                            url: "api/po_stack/" + self.order.poCode,
                            method: "POST",
                            showLoading: true,
                            data: pckgs
                        })
                        .then(function(resp2) {
                            if (resp2.status == 100) {
                                self.queryFeedingLog();

                                self.$emit("feeded");
                            } else {
                                self.errMsg = resp2.message;
                            }
                        });
                });
        },
        queryOrderStockBomView: function() {
            var self = this;
            this.$http
                .request({
                    url: "/api/po_stack/statistic/" + self.order.poCode,
                    method: "GET"
                })
                .then(function(resp) {
                    if (resp.data) {
                        self.stockBomArr = resp.data;
                    }
                });
        },
        indexMethod: function(index) {
            return ++index;
        }
    }
};
</script>



