<template>
    <div class="appep">
        <el-row :gutter="10">
            <el-col :span="24">
                <div class="perfromConLeft">
                    <div class="perfromConLeftContent">
                        <el-col style="margin-top: 1vh">
                            <div class="perfromConLeftTitle" style="position: relative;">
                                <el-col :span="24">
                                    <h3 class="information">工单信息</h3>
                                    <p class="line">&nbsp;</p>
                                </el-col>
                            </div>
                            <el-col :span="6">
                                <el-form ref="form" label-width="100px" style="margin-top: 1vh;">
                                    <el-form-item label="工单：">{{nowOrder.poCode}}</el-form-item>
                                </el-form>
                            </el-col>
                            <el-col :span="6">
                                <el-form ref="form" label-width="100px" style="margin-top: 1vh;">
                                    <el-form-item label="产品简码：">{{nowOrder.prodCode}}</el-form-item>
                                </el-form>
                            </el-col>
                            <el-col :span="6">
                                <el-form ref="form" label-width="100px" style="margin-top: 1vh;">
                                    <el-form-item label="产品名称：">{{nowOrder.prodName}}</el-form-item>
                                </el-form>
                            </el-col>
                            <el-col :span="6">
                                <el-form ref="form" label-width="100px" style="margin-top: 1vh;">
                                    <el-form-item label="图号：">{{nowOrder.prodTuhao}}</el-form-item>
                                </el-form>
                            </el-col>
                        </el-col>
                        <el-col style="margin-top: 1vh">
                            <el-col :span="6">
                                <el-form ref="form" label-width="100px" style="margin-top: 1vh;">
                                    <el-form-item label="总投料数：">{{nowOrder.touliaoSum}}</el-form-item>
                                </el-form>
                            </el-col>
                            <el-col :span="6">
                                <el-form ref="form" label-width="100px" style="margin-top: 1vh;">
                                    <el-form-item label="总产出数：">{{nowOrder.xialiaoSum}}</el-form-item>
                                </el-form>
                            </el-col>
                            <el-col :span="6">
                                <el-form ref="form" label-width="100px" style="margin-top: 1vh;">
                                    <el-form-item label="计划数量：">{{nowOrder.poCount}}</el-form-item>
                                </el-form>
                            </el-col>
                            <el-col :span="6">
                                <el-form ref="form" label-width="100px" style="margin-top: 1vh;">
                                    <el-form-item label="开始时间：">{{nowOrder.poStartDatetime}}</el-form-item>
                                </el-form>
                            </el-col>
                        </el-col>
                    </div>
                </div>

                <el-col :span="24">
                    <div>
                        <p style="color:#DAA651;margin: 1vh;font-weight: bold; float:left;font-size: 18px !important"
                            class="m_label">工序列表</p>
                    </div>
                    <el-col :span="24" class="QDLtbale">
                        <el-table style="width: 100%;height: 60vh;overflow-y:scroll" :data="procList">
                            <el-table-column label="工序号" prop="procCode">
                            </el-table-column>
                            <el-table-column label="工序" prop="procName">
                            </el-table-column>
                            <el-table-column label="工单号" prop="prodInfo.orderCode">
                            </el-table-column>
                            <el-table-column label="物料号" prop="prodInfo.prodCode">
                            </el-table-column>
                            <el-table-column label="工序线上库存" prop="prodInfo.opUpStockNum">
                            </el-table-column>
                            <el-table-column label="工序线下库存" prop="prodInfo.opSideStockNum">
                            </el-table-column>
                            <el-table-column label="不合格数量" prop="prodInfo.unqualifyNum">
                            </el-table-column>
                            <el-table-column width="190" label="操作">
                                <template slot-scope="scope">
                                    <el-button type="primary" size="mini" @click="goDetails(scope.row)">详情
                                    </el-button>
                                    <el-button type="warning" size="mini" @click="">不合格品
                                    </el-button>
                                </template>
                            </el-table-column>
                        </el-table>
                    </el-col>

                </el-col>

            </el-col>
        </el-row>
    </div>
</template>
<script>
    export default {
        data() {
            return {
                poCode: '10000183',

                //APIdata
                nowOrder: '',
                procList: [],
            };
        },
        mounted() {
            this.queryOrder()
        },
        methods: {
            go() {
                this.$router.push({ path: 'epExecution_details' })
            },
            //查询工单
            queryOrder: function () {
                var self = this
                this.$http.request({
                    url: '/api/order',
                    method: 'GET',
                    params: {
                        order_code: this.poCode,
                        // line_id: this.$ls.get('line').lineId,
                        // status: JSON.stringify(['Executing']),
                    }
                }).then(function (resp) {
                    self.nowOrder = resp.data.list[0];
                    self.queryProcessList()
                });
            },
            //查询工序
            queryProcessList() {
                var self = this
                this.$http.request({
                    url: '/api/process_info/' + this.nowOrder.prodCode,
                    method: 'GET',
                    params: {
                        line_id: this.nowOrder.lineId
                    }
                }).then(function (resp) {
                    if (resp.status == 100) {
                        self.procList = resp.data
                        self.queryStock(self.procList[0].procCode)//第一道工序查询上料总数
                        self.queryProcPackage(self.procList[self.procList.length - 1].procCode)//最后一道工序查询下料总数
                        self.queryProcess()
                    } else {
                        self.$notify.error({
                            title: '错误',
                            message: resp.message
                        });
                    }
                })
            },
            //工序详情
            queryProcess() {
                var self = this
                for (var i = 0; i < self.procList.length; i++) {
                    (function (index) {
                        self.$http.request({
                            url: '/api/prod_status/process/' + self.nowOrder.poCode + '/' + self.nowOrder.lineId + '/' + self.procList[index].procCode,
                            method: 'GET',
                        }).then(function (resp) {
                            if (resp.status == 100) {
                                self.procList[index]['prodInfo'] = resp.data
                                self.$set(self.procList, index, self.procList[index])
                            } else {
                                self.$notify.error({
                                    title: '错误',
                                    message: resp.message
                                });
                            }
                        })
                    })(i);
                }
            },
            //详情
            goDetails: function (val) {
                if (val.procCode != this.procList[this.procList.length - 1].procCode)
                    var routeName = 'epExecution_details'
                else
                    var routeName = 'epExecution_report'
                this.$router.push({
                    name: routeName,
                    query: {
                        lineId: this.nowOrder.lineId,
                        procCode: val.procCode,
                        procName: val.procName,
                        poCode: this.nowOrder.poCode,
                        prodCode: this.nowOrder.prodCode
                    }
                })
            },

            //上料总数查询
            queryStock: function (procCode) {
                var self = this;
                this.$http.request({
                    url: '/api/proc_stock/' + self.nowOrder.poCode + '/' + self.nowOrder.lineId + '/' + procCode,
                    method: 'GET',
                }).then(function (resp) {
                    self.nowOrder.touliaoSum = 0
                    for (var i = 0; i < resp.data.length; i++) {
                        self.nowOrder.touliaoSum += resp.data[i].boxQuan
                    }
                })
            },
            //下料总数查询
            queryProcPackage: function (procCode) {
                var self = this
                this.$http.request({
                    url: '/api/package/boxlog',
                    method: 'GET',
                    params: {
                        line_id: this.nowOrder.lineId,
                        order_code: this.nowOrder.poCode,
                        op_code: procCode,
                    }
                }).then(function (resp) {
                    if (resp.status == 100) {
                        self.nowOrder.xialiaoSum = 0
                        for (var i = 0; i < resp.data.list.length; i++) {
                            self.nowOrder.xialiaoSum += resp.data.list[i].boxQuan
                        }
                    }
                })
            },
        }
    };

</script>

<style>

</style>


