<template>
    <div>
        <el-row>
            <el-col :span="1">
                <ul class="menu_ul">
                    <li
                        class="h9"
                        @click="jump('SafelaunchImp')"
                        v-bind:class="{ menu_li_Ced: activeMenu=='SafelaunchImp' }"
                    >
                        <img src="@/assets/icon/CV2.png" />
                        <p>
                            Safelaunch
                            <br />执行
                        </p>
                    </li>
                    <li
                        class="h9"
                        @click="jump('SafelaunchQuery')"
                        v-bind:class="{ menu_li_Ced: activeMenu=='SafelaunchQuery' }"
                    >
                        <img src="@/assets/icon/CV2.png" />
                        <p>
                            Safelaunch
                            <br />查询
                        </p>
                    </li>
                    <li @click="jump('back')" class="menu_li_back">
                        <img src="@/assets/icon/back.png" />
                        <p>返回</p>
                    </li>
                </ul>
            </el-col>
            <el-col :span="23" style="padding-left: 10px;">
                <router-view></router-view>
            </el-col>
        </el-row>
    </div>
</template>
  
<script>
import "./index.css";
export default {
    name: 'SafelaunchIndex',
    data() {
        return {
            activeMenu: ""
        };
    },
    watch: {
        $route: function(val, oldVal) {
            this.judgeMenu();
        }
    },
    mountod: function() {},
    created: function() {
        this.judgeMenu();
    },
    methods: {
        judgeMenu: function() {
            var urlPath = this.$route.path;
            if (/\/safelaunch\/safelaunch_imp/.test(urlPath))
                this.activeMenu = "SafelaunchImp"
            
            if (/\/safelaunch\/safelaunch_query/.test(urlPath))
                this.activeMenu = "SafelaunchQuery"
        }
    }
};
</script>

<style scoped>
.h9 {
    padding-bottom: 0.5vh;
}
</style>
  


