<template>
<div class="modal">
    <el-dialog :close-on-click-modal="false" :visible.sync="dialogShow" title="防错验证" width="70%" @open="onDialogOpen">
        <el-col :span="24" class="dialogtitle" style="height:100px;margin:15px 0 0 0;">
            <!-- 步骤条   s_finish为高亮 -->
            <el-col class="stepBox2">
                <span class="stepLine s_finish"></span>
                <div class="stepNum s_finish"><p>1</p><span>工装量具刀具防错</span></div>
                <span class="stepLine s_finish"></span>
                <div v-bind:class="{ s_finish: activeStep>0}" class="stepNum"><p>2</p><span>人员防错</span></div>
                <span v-bind:class="{ s_finish: activeStep>0}" class="stepLine"></span>
                <div v-bind:class="{ s_finish: activeStep>1}" class="stepNum"><p>3</p><span>设备防错验证</span></div>
                <span v-bind:class="{ s_finish: activeStep>1}" class="stepLine"></span>
            </el-col>
        </el-col>

        <el-col :span="24" class="dialogbody" style="padding:10px;">
            <div class="daoju" v-if="activeStep == 0">
                <el-table style="width: 100%" height="400px">
                    <el-table-column type="selection" width="55"></el-table-column>
                    <el-table-column label="类型"></el-table-column>
                    <el-table-column label="设备"></el-table-column>
                    <el-table-column label="工序要求"></el-table-column>
                    <el-table-column label="名称"></el-table-column>
                    <el-table-column label="防错验证"></el-table-column>
                    <el-table-column label="使用二维码"></el-table-column>
                    <el-table-column label="使用授权时间"></el-table-column>
                </el-table>
            </div>

            <div class="renyuan" v-if="activeStep == 1">
                <el-table style="width: 100%" height="400px" :data="skillVeifyLog" @selection-change="handleSelectionChange" ref="personSkill">
                    <el-table-column type="selection" width="55"></el-table-column>
                    <!-- <el-table-column label="设备"></el-table-column> -->
                    <el-table-column label="工序" prop="svProcedure"></el-table-column>
                    <el-table-column label="工序要求熟练度" prop="svSkillNeed"></el-table-column>
                    <el-table-column label="上岗人员" prop="svPersonName"></el-table-column>
                    <el-table-column label="防错时间" prop="createTime"></el-table-column>
                    <el-table-column label="防错验证" prop="svResult"></el-table-column>
                    <el-table-column label="授权人" prop="svAuthorizer"></el-table-column>
                    <el-table-column label="授权时间" prop="svAuthTime"></el-table-column>
                    <el-table-column label="刷卡验证">
                        <template slot-scope="scope">
                            <img src="@/assets/icon/card.png" @click="cardScanShowModal(scope.$index)" />
                        </template>
                    </el-table-column>
                </el-table>
            </div>

            <div class="renyuan" v-if="activeStep == 2">
                <el-table style="width: 100%" height="400px" :data="deviceVerifyLog" @selection-change="handleSelectionChange">
                    <el-table-column type="selection" width="55"></el-table-column>
                    <el-table-column label="防错设备" prop="dvDevice"></el-table-column>
                    <el-table-column label="防错检项" prop="dvItem"></el-table-column>
                    <el-table-column label="防错要求" prop="dvRequirement"></el-table-column>
                    <el-table-column label="防错时间" prop="createTime"></el-table-column>
                    <!-- <el-table-column label="防错验证"></el-table-column> -->
                    <el-table-column label="YES" prop="dvResult"></el-table-column>
                    <el-table-column label="授权人" prop="dvAuthorizer"></el-table-column>
                    <el-table-column label="授权时间" prop="dvAuthTime"></el-table-column>
                </el-table>
            </div>
            <el-col :span="24" class="nextButton">
                <el-col :span="12">
                    <el-button type="warning" style="float: left;margin-right:1vh;" size="mini" @click="ignoreBtn()">
                        <img src="@/assets/icon/ignore.png" />忽略
                    </el-button>
                    <!-- icon="el-icon-document-delete"-->
                    <div v-if=" activeStep==0 || activeStep==2">
                        <el-button type="success" style="float: left;" size="mini" @click="deviceYes()">
                            <img src="@/assets/icon/success.png" />YES
                        </el-button>
                    </div>
                </el-col>

                <el-col :span="12" v-if="activeStep < 2">
                    <el-button type="primary" @click="nextStep" size="mini">下一步</el-button>
                </el-col>

                <el-col :span="12" v-if="activeStep == 2">
                    <el-button type="primary" size="small" @click="onFinish">完成</el-button>
                </el-col>
            </el-col>
        </el-col>
    </el-dialog>

    <inputCard :show.sync="cardScanShow" @pass="cardCallBack"></inputCard>
</div>
</template>

<style>
</style>

<script>
import inputCard from "@/components/card_input.vue";
import moment from "moment";
export default {
    components: {
        inputCard: inputCard //声明组件
    },
    props: ["show", "order", "lineId"],
    name: "PreparationVerify",
    data() {
        return {
            dialogShow: this.show,
            activeStep: 0,
            skillVeifyLog: [],
            deviceVerifyLog: [],
            cardScanShow: false,

            checked: true,
            checked1: true,
            checked2: true,
            ifcardShow: false,
            ifGcard: Boolean,
            selectIndex: '',
            selectCollect: [],
            //刷卡类型，1、防错刷卡 2、人员忽略刷卡  3、设备忽略 
            cardType: '',
            //是否做过防错
            skillIsNeed: false,
        };
    },
    watch: {
        show: function (newValue, oldValue) {
            this.dialogShow = newValue;
        },
        dialogShow: function (newValue, oldValue) {
            if (this.show != this.dialogShow)
                this.$emit("update:show", this.dialogShow);
        }
    },
    methods: {
        onDialogOpen: function () {
            this.activeStep = 1;
            var self = this;

            //查询是否做过人员防错
            this.$http
                .request({
                    url: "/api/skill_verify/" + this.order.poCode + "/isneeded",
                    method: "POST"
                })
                .then(function (resp) {
                    if (!resp.data) {
                        self.activeStep = 2;
                    }
                });

            //查询是否做过设备防错
            this.$http
                .request({
                    url: "/api/device_verify/" + this.order.poCode + "/isneeded",
                    method: "POST"
                })
                .then(function (resp) {
                    if (!resp.data) {
                        self.$emit("pass", true)

                        //加提示已经做过防错

                        self.dialogShow = false;
                    }
                });

            //查询人员防错要求
            this.$http
                .request({
                    url: "/api/skill_verify/" + this.order.poCode,
                    method: "GET"
                })
                .then(function (resp) {
                    self.skillVeifyLog = resp.data;
                });

            //查询设备防错要求
            this.$http
                .request({
                    url: "/api/device_verify/" + this.$ls.get('line').lineId,
                    method: "GET"
                })
                .then(function (resp) {
                    self.deviceVerifyLog = resp.data;
                });
        },
        cardScanShowModal: function (index) {
            this.cardType = 1;
            this.selectIndex = index;
            this.cardScanShow = true;
        },
        onFinish: function () {
            var self = this;
            //防错完成
            for (var i = 0; i < this.deviceVerifyLog.length; i++) {
                if (this.deviceVerifyLog[i].dvResult == "") {
                    return false;
                }
            }
            this.$http.request({
                url: '/api/device_verify/' + this.$ls.get("line").lineId,
                method: 'POST',
                data: this.deviceVerifyLog
            }).then(function (resp) {
                if (resp.status == 100) {
                    self.$emit("pass", true)
                    self.dialogShow = false;
                }
            })

        },

        //刷卡回调
        cardCallBack: function (userData) {
            if (userData == null) {
                this.$notify.error({
                    title: '错误',
                    message: '没有查到此人'
                });
                return;
            }
            if (this.cardType == 1) {
                this.recordFaile(userData);
            } else if (this.cardType == 2) {
                this.ignoreSkill(userData);
            } else if (this.cardType == 3) {
                this.ignoreDevice(userData);
            }
        },

        //人员防错记录
        recordFaile: function (userData) {
            this.skillVeifyLog[this.selectIndex].svSkillReal = "L";
            this.skillVeifyLog[this.selectIndex].svResult = "Pass";
            this.skillVeifyLog[this.selectIndex].shiftDate = moment().format("YYYY-MM-DD");
            this.skillVeifyLog[this.selectIndex].shiftId = this.$ls.get("shift").sfId;
            this.skillVeifyLog[this.selectIndex].orderCode = this.order.poCode;
            this.skillVeifyLog[this.selectIndex].svPersonName = userData.userName;
            this.skillVeifyLog[this.selectIndex].createTime = moment().format("YYYY-MM-DD HH:mm:ss");

        },

        nextStep: function () {
            var self = this;
            if (this.activeStep == 0) {
                //TODO 工装刀具防错校验
                this.activeStep = 1;
            } else if (this.activeStep == 1) {
                //人员技能防错校验
                for (var i = 0; i < this.skillVeifyLog.length; i++) {
                    if (this.skillVeifyLog[i].svResult == "") {
                        //加提示
                        return false;
                    }
                }
                this.$http.request({
                    url: '/api/skill_verify/' + this.$ls.get("line").lineId,
                    method: 'POST',
                    data: this.skillVeifyLog
                }).then(function (resp) {
                    if (resp.status == 100) {

                        self.activeStep = 2;
                    }
                })
            } else {
                this.activeStep = 0;
            }
        },
        ignoreBtn: function () {
            this.cardType = this.activeStep + 1;
            this.cardScanShow = true;
        },
        //人员忽略
        ignoreSkill: function (userData) {
            var time = moment().format("YYYY-MM-DD HH:mm:ss");
            //循环选择的对象
            for (var i = 0; i < this.skillVeifyLog.length; i++) {
                for (var j = 0; j < this.selectCollect.length; j++) {
                    if (this.skillVeifyLog[i] == this.selectCollect[j]) {
                        //this.skillVeifyLog[i].svSkillReal = "L";
                        this.skillVeifyLog[i].svResult = "Pass";
                        this.skillVeifyLog[i].shiftDate = moment().format("YYYY-MM-DD");
                        this.skillVeifyLog[i].shiftId = this.$ls.get("shift").sfId;
                        this.skillVeifyLog[i].orderCode = this.order.poCode;
                        this.skillVeifyLog[i].svAuthorizer = userData.userName;
                        this.skillVeifyLog[i].svAuthTime = time;
                    }
                }
            }
        },

        //设备忽略
        ignoreDevice: function (userData) {
            var time = moment().format("YYYY-MM-DD HH:mm:ss");
            for (var i = 0; i < this.deviceVerifyLog.length; i++) {
                for (var j = 0; j < this.selectCollect.length; j++) {
                    if (this.deviceVerifyLog[i] == this.selectCollect[j]) {
                        this.deviceVerifyLog[i].dvResult = "忽略";
                        this.deviceVerifyLog[i].shiftDate = moment().format("YYYY-MM-DD");
                        this.deviceVerifyLog[i].shiftId = this.$ls.get("shift").sfId;
                        this.deviceVerifyLog[i].orderCode = this.order.poCode;
                        this.deviceVerifyLog[i].dvAuthorizer = userData.userName;
                        this.deviceVerifyLog[i].dvAuthTime = time;
                    }
                }
            }

        },

        //设备YES
        deviceYes: function (userData) {
            var time = moment().format("YYYY-MM-DD HH:mm:ss");
            var userData = this.$ls.get('user');
            for (var i = 0; i < this.deviceVerifyLog.length; i++) {
                for (var j = 0; j < this.selectCollect.length; j++) {
                    if (this.deviceVerifyLog[i] == this.selectCollect[j]) {
                        //this.skillVeifyLog[i].svSkillReal = "L";
                        this.deviceVerifyLog[i].dvResult = "YES";
                        this.deviceVerifyLog[i].shiftDate = moment().format("YYYY-MM-DD");
                        this.deviceVerifyLog[i].shiftId = this.$ls.get("shift").sfId;
                        this.deviceVerifyLog[i].orderCode = this.order.poCode;
                        this.deviceVerifyLog[i].dvAuthorizer = userData.userName;
                        this.deviceVerifyLog[i].dvAuthTime = time;
                    }
                }
            }
        },

        handleSelectionChange: function (selectCollect) {
            this.selectCollect = selectCollect;
        },
        exitCard() {
            this.ifcardShow = false;
        }
    }
};
</script>
<style>
</style>



