<template>
  <div>
    <el-row>
      <el-col :span="1">
        <ul class="menu_ul">
          <li @click="jump('epPreparation')" v-bind:class="{ menu_li_Ced: activeMenu=='epPreparation' }"><img
              src="@/assets/icon/CV2.png">
            <p>生产准备</p>
          </li>
          <li @click="jump('epExecution')" v-bind:class="{ menu_li_Ced: activeMenu=='epExecution' }"><img
              src="@/assets/icon/CV2.png">
            <p>生产执行</p>
          </li>
          <li @click="jump('eprework')" v-bind:class="{ menu_li_Ced: activeMenu=='eprework' }">
            <img src="@/assets/icon/CV3.png" />
            <p>在线返工</p>
          </li>
          <li @click="jump('back')" class="menu_li_back">
            <img src="@/assets/icon/back.png" />
            <p>返回</p>
          </li>
        </ul>
      </el-col>
      <el-col :span="23" style="padding-left: 10px;">
        <keep-alive>
          <router-view v-if="$route.meta.keepAlive"></router-view>
        </keep-alive>
        <router-view v-if="!$route.meta.keepAlive"></router-view>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  import './index.css';
  export default {
    data() {
      return {
        activeMenu: ''
      };
    },
    watch: {
      $route: function (val, oldVal) {
        this.judgeMenu()
      }
    },
    created: function () {
      this.judgeMenu()
    },
    methods: {
      judgeMenu: function () {
        var urlPath = this.$route.path
        if (/\/ep_assy\/epPreparation/.test(urlPath))
          this.activeMenu = 'epPreparation'
        else if (/\/ep_assy\/epExecution/.test(urlPath))
          this.activeMenu = 'epExecution'
        else if (/\/ep_assy\/epExecution_details/.test(urlPath))
          this.activeMenu = 'epExecution'
        else if (/\/ep_assy\/exEcution_report/.test(urlPath))
          this.activeMenu = 'epExecution'
        else if (/\/ep_assy\/eprework/.test(urlPath))
          this.activeMenu = 'eprework'
        else if (/\/ep_assy\/eprework_details/.test(urlPath))
          this.activeMenu = 'eprework'
      }
    }
  }

</script>
<style scoped>

</style>


