<template>
  <div>
    <el-row>
      <el-col :span="1">
        <ul class="menu_ul">
          <li  @click="jump('SectIndex')" v-bind:class="{ menu_li_Ced: activeMenu=='execution' }"><img src="@/assets/icon/CV2.png"><p>生产执行</p></li>
          <li @click="jump('SeUnqualified')" v-bind:class="{ menu_li_Ced: activeMenu=='SeUnqualified' }"><img src="@/assets/icon/CV3.png"><p>不合格品</p></li>
          <li @click="jump('back')"  class="menu_li_back"><img src="@/assets/icon/back.png"><p>返回</p></li>
        </ul>
      </el-col>
      <el-col :span="23" style="padding-left: 10px;">
        <router-view></router-view>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import './index.css';
export default {
  name: 'SectIndex',
  data() {
        return {
            activeMenu: ''
        };
    },
    watch: {
        $route: function(val, oldVal){
            this.judgeMenu()
        }
    },
    created: function() {
        this.judgeMenu()
    },
    methods: {
        judgeMenu: function(){
            var urlPath = this.$route.path
            if(/\/sect_assy\/execution/.test(urlPath))
                this.activeMenu = 'execution'
            else if(/\/sect_assy\/SeUnqualified_details/.test(urlPath))
                this.activeMenu = 'SeUnqualified'
            else if(/\/sect_assy\/SeUnqualified/.test(urlPath))
                this.activeMenu = 'SeUnqualified'
        }
    }
}
</script>
<style scoped>

</style>



