<template>
    <div>
        <header id="header">
            <img src="@/assets/icon/sds.png" />
            <div class="H_box">
                <p>
                    <img src="@/assets/icon/time.png" />
                    <span>{{curDate}}</span> |
                </p>
                <p>
                    <img src="@/assets/icon/user.png" />
                    <span>{{$ls.get('user').userName}}</span> |
                </p>
                <p @click="jump('Cardlogin')">
                    <img src="@/assets/icon/signout.png" />
                    <span>退出</span>
                </p>
            </div>
        </header>
        <div id="main">
            <router-view />
        </div>
    </div>
</template>

<script>
import moment from 'moment'
export default {
    name: "Home",
    data() {
        return {
            curDate:moment().format("YYYY-MM-DD"),
        };
    },
    methods: {}
};
</script>
<style scoped>
#header {
    border-bottom: 2px solid #ffbf44;
    box-shadow: 0px 3px 3px #1b222c;
    background-color: #12599a;
    height: 6vh;
    width: 100%;
    margin-bottom: 5px;
}

#header img {
    line-height: 6vh;
    margin: 1.5vh;
    max-height: 3vh;
}

#main {
    background-image: url("../assets/images/allBg.png");
    background-size: 100% 100%;
}

.H_box {
    float: right;
}

.H_box p {
    cursor: pointer;
    margin-left: 10px;
    line-height: 7vh;
    display: inline-block;
    color: white;
}

.H_box p span {
    font-size: 15px;
    margin-left: 10px;
    margin-right: 10px;
}

.H_box p img {
    position: relative;
    bottom: -4px;
    margin: 0px !important;
    max-width: 20px;
}

.white a {
    color: white;
}
</style>


