<template>
        <div class="appgx">
            <el-row :gutter="10">
    
                <el-col :span="24">
                    <div class="planMaintenanceTitle">
                        <el-col :span="20">
                            <el-breadcrumb separator=">">
                                <el-breadcrumb-item><i class="el-icon-s-home"></i><span @click="jump('ProcExecution')">生产执行</span>
                                </el-breadcrumb-item>
                                <el-breadcrumb-item><span @click="jump('ProcExecution')">工序详情</span></el-breadcrumb-item>
                            </el-breadcrumb>
                        </el-col>
                        <div>
                            <el-col :span="4" style="text-align: right">
                                <el-button type="primary" style="width:90px;" size="mini" @click="ifchargingt = true">工序上料
                                </el-button>
                                <el-button type="primary" v-if="procUpData.isLastProc && ifLoadingCpl" style="width:90px;" size="mini"
                                    @click="orderPkgManuShow=true">手工装箱
                                </el-button>
                                <el-button type="primary" v-if="!procUpData.isLastProc && ifLoadingCpl" style="width:90px;" size="mini"
                                    @click="ifProcPackage=true">工序下料
                                </el-button>
                                <!-- <el-button type="primary" style="width:110px;" size="mini">不合格品录入</el-button> -->
                            </el-col>
                        </div>
                    </div>
                </el-col>
                <div class="content">
                    <el-col :lg="24" :xl="24">
                        <div class="perfromConLeft">
                            <div class="perfromConLeftTitle" style="position: relative;">
                                <el-col :span="24">
    
                                    <h3 class="information">
                                        车间基本信息</h3>
                                    <p class="line">
                                        &nbsp;</p>
                                </el-col>
                            </div>
    
                            <div class="perfromConLeftContent">
    
                                <el-col :span="4">
                                    <el-form ref="form" label-width="80px" style="margin-top: 1vh;">
                                        <el-form-item label="工厂：">
                                            {{$ls.get('factory').fcName}}
                                        </el-form-item>
                                    </el-form>
                                </el-col>
                                <el-col :span="5">
                                    <el-form ref="form" label-width="80px" style="margin-top: 1vh;">
                                        <el-form-item label="车间：">
                                            {{$ls.get('workshop').shopName}}
                                        </el-form-item>
                                    </el-form>
                                </el-col>
                                <el-col :span="5">
                                    <el-form ref="form" label-width="80px" style="margin-top: 1vh;">
                                        <el-form-item label="生产线：">
                                            {{$ls.get('line').lineName}}
                                        </el-form-item>
                                    </el-form>
    
                                </el-col>
    
                                <el-col :span="5">
                                    <el-form ref="form" label-width="80px" style="margin-top: 1vh;">
                                        <el-form-item label="班次：">
                                            {{$ls.get('shift').sfName}}
                                        </el-form-item>
                                    </el-form>
    
                                </el-col>
                                <el-col :span="5">
                                    <el-form ref="form" label-width="80px" style="margin-top: 1vh;">
                                        <el-form-item label="线长：">
                                            {{$ls.get('user').userName}}
                                        </el-form-item>
                                    </el-form>
    
                                </el-col>
                            </div>
                        </div>
                    </el-col>
                </div>
                <el-col :lg="24" :xl="24" class="tableBox">
                    <p style="color: rgb(253, 190, 69);margin-top: 1vh;">{{procName}}</p>
                    <div class="perfromConRight">
    
                        <el-col :span="24" class="shebei">
                            <el-col :span="24">
                                <el-col :span="5">
                                    <el-form ref="form" label-width="80px">
                                        <el-form-item label="工序号：">
                                            {{procCode}}
                                        </el-form-item>
                                    </el-form>
                                </el-col>
    
                                <el-col :span="5">
                                    <el-form ref="form" label-width="120px">
                                        <el-form-item label="产品简码：">
                                            {{procUpData.prodCode}}
    
                                        </el-form-item>
                                    </el-form>
                                </el-col>
    
                                <el-col :span="5">
                                    <el-form ref="form" label-width="120px">
                                        <el-form-item label="投料数量：">
                                            {{procUpData.materialNum}}
    
                                        </el-form-item>
                                    </el-form>
                                </el-col>
    
                                <el-col :span="5">
                                    <el-form ref="form" label-width="120px">
                                        <el-form-item label="产出数量：">
                                            {{procUpData.outputNum}}
    
                                        </el-form-item>
                                    </el-form>
                                </el-col>
    
                                <el-col :span="4">
                                    <el-form ref="form" label-width="120px">
                                        <el-form-item label="不合格品：">
                                            <span class="xcolor">{{procUpData.unqualifyNum}}</span>
    
                                        </el-form-item>
                                    </el-form>
                                </el-col>
    
                            </el-col>
                        </el-col>
    
                        <div class="QDLTbale">
    
                            <el-col :span="10" class="QDLtbale" style="padding: 2vh;">
                                <p style="margin-bottom: 1vh;margin-left:1vh;color:#F4B746;">上料记录</p>
                                <el-table style="width: 100%" height="400" :data="ProdProcessUp">
                                    <el-table-column label="上料时间" prop="createTime">
                                    </el-table-column>
                                    <el-table-column label="物料名称" prop="matName">
                                    </el-table-column>
                                    <el-table-column label="原箱合格证" prop="boxBarcode">
                                    </el-table-column>
                                    <el-table-column label="箱合格证" prop="matHuident">
                                    </el-table-column>
                                    <el-table-column label="上料数量" prop="boxQuan">
                                    </el-table-column>
                                    <el-table-column label="剩余数量" prop="boxQuanLeft">
                                    </el-table-column>
                                </el-table>
    
                            </el-col>
    
                            <el-col :span="14" class="QDLtbale" style="padding: 2vh;">
                                <p style="margin-bottom: 1vh;margin-left:1vh;color:#F4B746;">下料记录</p>
                                <el-table style="width: 100%" height="380" :data="packages">
                                    <el-table-column label="下料时间" prop="createTime">
                                    </el-table-column>
                                    <el-table-column label="物料名称" prop="prodName">
                                    </el-table-column>
                                    <el-table-column label="箱合格证" prop="boxBarcode">
                                    </el-table-column>
                                    <el-table-column label="炉号" prop="prodFurnaceNo">
                                    </el-table-column>
                                    <el-table-column label="档位" prop="prodGlevel">
                                    </el-table-column>
                                    <el-table-column label="下料数量" prop="boxQuan">
                                    </el-table-column>
                                    <el-table-column width="140" label="操作">
                                        <template slot-scope="scope">
                                            <el-button v-if="scope.row.isWip" type="primary" size="mini" @click="GCMreprint(scope.row)">过程码补打
                                            </el-button>
                                            <el-button v-else type="primary" size="mini" @click="CPWLXreprint(scope.row)">箱合格证补打
                                            </el-button>
                                        </template>
                                    </el-table-column>
                                </el-table>
    
                            </el-col>
    
                        </div>
                    </div>
                </el-col>
            </el-row>
    
            <!--工序上料弹出框-->
            <charging :show.sync="ifchargingt" :poCode="poCode" :procCode="procCode" :lineId="nowLineId" @exitcharging="refreshStatus">
            </charging>
    
            <!--未满装箱弹出框 -->
            <OrderPackageManu :show.sync="orderPkgManuShow" :order="order" stockResource="PROC_SUB" :opCode="procCode"
                :boxLog="boxToConfirm" @packageDone="refreshStatus" :isMatBox="true" :lineId="nowLineId"></OrderPackageManu>
    
            <!--工序下料弹出框 -->
            <ProcPackage :show.sync="ifProcPackage" :poCode="poCode" :procCode="procCode" :matBatchNo="matBatchNo"
                @exitblanking="refreshStatus" :boxLog="boxToConfirm" :lineId="nowLineId">
            </ProcPackage>
    
        </div>
    </template>
    
    <script>
        import charging from "@/components/proc_feeding.vue";
        import proc_package from "@/components/proc_package.vue";
        import OrderPackageManu from "@/components/order_package_manu.vue"
        export default {
            name: 'Processdetails',
            data() {
                return {
                    order: '',
                    ifchargingt: false,
                    ifProcPackage: false,
                    devInfo: {},
                    productInfo: [],
                    packages: [],
                    orderPkgManuShow: false,
                    procUpData: [],
                    ProdProcessUp: [],
                    nowLineId: '',      //过程码产线ID
                    procCode: '',
                    procName: '',
                    poCode: '',
                    matBatchNo: '',
                    prodCode: '',
                    boxToConfirm: null,
                    ifLoadingCpl:false,//是否加载完成
                };
            },
            components: {
                charging: charging,
                ProcPackage: proc_package,
                OrderPackageManu
            },
            created: function () {
                this.procCode = this.$route.query.procCode;
                this.procName = this.$route.query.procName;
                this.poCode = this.$route.query.poCode;
                this.prodCode = this.$route.query.prodCode
                this.nowLineId = this.$route.query.lineId
                this.queryOrder()
            },
            methods: {
                // 下料记录查询
                queryProcPackage: function () {
                    var self = this
                    var lineId = this.$ls.get('line').lineId
                    if(this.nowLineId)
                        lineId = this.nowLineId
                    self.boxToConfirm = null
                    this.$http.request({
                        url: '/api/package/boxlog',
                        method: 'GET',
                        params: {
                            line_id: lineId,
                            order_code: this.poCode,
                            op_code: this.procCode,
                            //is_wip: true
                        }
                    }).then(function (resp) {
                        if (resp.data) {
                            self.packages = resp.data.list
                            for (var i = 0; i < self.packages.length; i++) {
                                if (!self.packages[i].isConfirmed) {
                                    self.boxToConfirm = self.packages[i]
                                    if (self.procUpData == null)
                                        return
                                    if (self.procUpData.isLastProc) {
                                        self.orderPkgManuShow = true
                                    } else {
                                        self.ifProcPackage = true
                                    }
                                    break;
                                }
                            }
    
                        }
                    })
                },
                //查询加工状态
                queryProcStatus() {
                    var self = this
                    var lineId= this.$ls.get('line').lineId
                    if(this.nowLineId)
                        lineId = this.nowLineId
                    self.$http.request({
                        url: '/api/prod_status/process/' + self.poCode + '/' + lineId + '/' + self.procCode,
                        method: 'GET',
                    }).then(function (resp) {
                        if (resp.status == 100) {
                            self.procUpData = resp.data
                            self.ifLoadingCpl=true
                            self.queryProcPackage()
                        } else {
                            self.$notify.error({
                                title: '错误',
                                message: resp.message
                            });
                        }
                    })
                },
                //查询工单
                queryOrder() {
                    var self = this
                    self.$http.request({
                        url: '/api/order',
                        method: 'GET',
                        params: {
                            order_code: this.poCode,
                        }
                    }).then(function (resp) {
                        if (resp.status == 100) {
                            self.order = resp.data.list[0]
                            self.queryProcStatus()
                            self.queryStock()
                        } else {
                            self.$notify.error({
                                title: '错误',
                                message: resp.message
                            });
                        }
                    })
                },
                // 上料记录
                queryStock: function () {
                    var self = this;
                    var lineId= this.$ls.get('line').lineId
                    if(this.nowLineId)
                        lineId = this.nowLineId
                    this.$http.request({
                        url: '/api/proc_stock/' + self.poCode + '/' + lineId + '/' + this.procCode,
                        method: 'GET',
                    }).then(function (resp) {
                        self.ProdProcessUp = resp.data
                        self.matBatchNo = self.ProdProcessUp[0].matBatchNo
                    })
                },
                refreshStatus() {
                    this.queryProcStatus()
                    this.queryStock()
                },
                // 过程码
                GCMreprint: function (boxlog) {
                    this.$printer.printWipBox(boxlog);
                },
                //成品物料箱
                CPWLXreprint: function (boxlog) {
                    this.$printer.printMatBoxLog(boxlog);
                },
            }
        }
    </script>


