<template>
    <div>
        <el-dialog :close-on-click-modal="false" title="手工装箱" :visible.sync="dialogShow" width="50%"
            class="dialogtitleZX" :before-close="handleClose" @open="onDialogOpen">
            <el-col :span="24">
                <el-col :span="24" class="dialogtitleSL" style="height: 13vh !important">
                    <!-- 步骤条   s_finish为高亮 -->
                    <el-col class="stepBox">
                        <span class="stepLine s_finish"></span>
                        <div class="stepNum s_finish">
                            <p>1</p><span>扫描箱条码</span>
                        </div>
                        <span class="stepLine s_finish"></span>
                        <div v-bind:class="{ s_finish: activeStep>1}" class="stepNum">
                            <p>2</p><span>扫描验证</span>
                        </div>
                        <span v-bind:class="{ s_finish: activeStep>1}" class="stepLine"></span>
                    </el-col>
                </el-col>
                <el-col :span="24" style="height: auto;border-bottom: 1px solid #2B6EAB;">
                    <div class="Boxing" v-if="activeStep==1">
                        <el-col :span="18">
                            <el-form label-width="125px" @submit.native.prevent>
                                <el-form-item label="装箱数量：">
                                    <el-input ref="count" placeholder="请输入" v-model="count"></el-input>
                                </el-form-item>
                            </el-form>
                        </el-col>

                        <el-col :span="18">
                            <el-form label-width="125px" @submit.native.prevent>
                                <el-form-item label="档位：">
                                    <el-select v-model="dangwei" placeholder="请选择" class="select_pla">
                                        <el-option label="蓝" value="蓝"></el-option>
                                        <el-option label="绿" value="绿"></el-option>
                                        <el-option label="紫" value="紫"></el-option>
                                        <el-option label="金" value="金"></el-option>
                                        <el-option label="白" value="白"></el-option>
                                        <el-option label="无" value=""></el-option>
                                    </el-select>
                                    <el-input v-model="dangwei" class="select_pla" style="margin-left: 8%">
                                    </el-input>
                                </el-form-item>
                            </el-form>
                        </el-col>
                        <!-- <el-col :span="18">
                            <el-form label-width="125px" @submit.native.prevent>
                                <el-form-item label="炉号：">
                                    <el-input placeholder="请输入" v-model="luhao"></el-input>
                                </el-form-item>
                            </el-form>
                        </el-col> -->
                        <!-- <el-col :sm="19" :lg="14" class="tishi">
            <el-form label-width="220px">
            <el-form-item label="！提示信息">
            </el-form-item>
            </el-form>

                        </el-col>-->
                    </div>

                    <div v-if="activeStep==2">
                        <el-col :span="18">
                            <el-form label-width="125px" @submit.native.prevent>
                                <el-form-item label="扫描箱合格证：">
                                    <el-input ref="boxCodes" placeholder="请扫描录入" v-model="scanValue"
                                        v-on:keyup.enter.native="onScanEnter"></el-input>
                                    <p v-if="errMsg.length>0" class="p_red" style="margin-left: 25px">
                                        {{errMsg}}</p>
                                </el-form-item>
                            </el-form>
                        </el-col>

                        <el-col :span="2">
                            <el-form label-width="40px">
                                <el-form-item>
                                    <span>
                                        <img src="@/assets/images/sm.png" />
                                    </span>
                                </el-form-item>
                            </el-form>
                        </el-col>
                        <el-col :span="24" style="position: relative;padding-bottom: 10px;">
                            <h3
                                style="position: absolute; top: 2vh; left: 5%; background: #12599A; color: rgb(253, 190, 69);font-size: 16px;">
                                物料信息</h3>
                            <p
                                style="height: 1px; float: left; margin-top: 3vh; width: 100%; border: 0.5px dashed rgb(253, 190, 69); box-sizing: border-box;">
                                &nbsp;</p>
                        </el-col>

                        <el-col :span="24" style="height: 70px;" class="Boxing1">
                            <el-col :span="8">
                                <el-form label-width="140px">
                                    <el-form-item label="物料名称：">{{fpBox.prodName}}</el-form-item>
                                </el-form>
                            </el-col>
                            <el-col :span="8">
                                <el-form label-width="130px">
                                    <el-form-item label="物料简码：">{{fpBox.prodCode}}</el-form-item>
                                </el-form>
                            </el-col>
                            <el-col :span="8">
                                <el-form label-width="140px">
                                    <el-form-item label="物料数量：">{{fpBox.boxQuan}}</el-form-item>
                                </el-form>
                            </el-col>
                        </el-col>

                        <el-col :span="12" class="Boxing2">
                            <el-form label-width="20px">
                                <el-form-item>
                                    <img src="@/assets/icon/refresh.png" />
                                    <span @click="boxReprint">补打箱合格证</span>
                                </el-form-item>
                            </el-form>
                        </el-col>
                    </div>
                </el-col>
            </el-col>
            <div class="appdialog" style="text-align: center; padding: 10px;  background: #12599A;">
                <el-col :span="24" style="margin-bottom: 1.5vh;padding-right: 1vh;" class="nextButton"
                    v-if="activeStep==1">
                    <el-button v-if="ifallow_over_submit" type="primary" @click="ifconfirm=true,allow_over_submit=true"
                        size="mini">超库存报交
                    </el-button>
                    <el-button type="warning" @click="ifconfirm=true,allow_over_submit=false" size="mini">打印箱条码
                    </el-button>
                </el-col>
            </div>
            <el-dialog class="ifconfirmdialog" :close-on-click-modal="false" width="40%" title="请确定填写是否有误"
                :visible.sync="ifconfirm" append-to-body>
                <el-col class="bigerfont">
                    <el-col :span="24">
                        <el-form label-width="140px">
                            <el-form-item label="装箱数量：">{{count}}</el-form-item>
                        </el-form>
                    </el-col>
                    <el-col :span="24">
                        <el-form label-width="140px">
                            <el-form-item label="档    位：">{{dangwei}}</el-form-item>
                        </el-form>
                    </el-col>
                    <el-col :span="24">
                        <el-form label-width="140px">
                            <el-form-item label="炉    号：">{{luhao}}</el-form-item>
                        </el-form>
                    </el-col>
                </el-col>
                <div class="appdialog" style="text-align: center; padding: 10px;  background: #12599A;">
                    <el-col :span="24" style="margin-bottom: 1.5vh;padding-right: 1vh;" class="nextButton">
                        <el-button type="info" @click="ifconfirm=false" size="small"> 取 消 </el-button>
                        <el-button type="warning" @click="beforePrintBoxCode" size="small">打印箱条码</el-button>
                    </el-col>
                </div>
            </el-dialog>
        </el-dialog>
        <inputCard :show.sync="cardScanShow" @pass="cardCallBack"></inputCard>
    </div>
</template>

<script>
    import inputCard from "@/components/card_input.vue";
    export default {
        props: ["show", "order", "stockResource", "opCode", "boxLog", "isMatBox", "lineId"],
        name: "OrderPackageManu",
        components: {
            inputCard: inputCard //刷卡
        },
        data() {
            return {
                cardScanShow: false,//刷卡
                activeStep: 1,
                dialogShow: this.show,
                stockLogs: [],
                scanValue: "",
                errMsg: "",
                count: '',
                dangwei: '',
                luhao: '',
                fpBox: '',
                scanBoxCode: '',
                ifconfirm: false,
                ifallow_over_submit: false,
                allow_over_submit: false,
                userData:[],//刷卡用户信息
            };
        },
        watch: {
            show: function (newValue, oldValue) {
                this.dialogShow = newValue;
            },
            dialogShow: function (newValue, oldValue) {
                if (this.show != this.dialogShow)
                    this.$emit("update:show", this.dialogShow);
            }
        },
        methods: {
            onDialogOpen: function () {
                if (this.boxLog) {
                    this.fpBox = this.boxLog
                    this.activeStep = 2;
                    this.boxReprint()
                }
                else {
                    this.ifallow_over_submit=''
                    this.count = '';
                    this.luhao = '';
                    this.dangwei = '';
                    this.activeStep = 1;
                }
                var self = this;
            },
            printBoxCode: function () {
                //装箱提交
                var self = this
                var lineId= this.$ls.get('line').lineId
                if(this.lineId)
                    lineId = this.lineId
                var params = {
                    line_id: lineId,
                    box_quan: this.count,
                    glevel: this.dangwei,
                    furnace_no: this.luhao,
                    stockResource: this.stockResource,
                    allow_over_submit: this.allow_over_submit,
                    allow_person_number: this.userData.userAccount,
                    allow_person_name: this.userData.userName
                };
                if (this.opCode) {
                    params['op_code'] = this.opCode
                }
                this.$http.request({
                    url: '/api/package/' + this.order.poCode,
                    method: 'POST',
                    showLoading: true,
                    params: params,
                }).then(function (resp) {
                    if (resp.status == 100) {
                        self.fpBox = resp.data
                        self.activeStep = 2;
                        self.boxReprint();
                        self.$emit("packageDone");
                    } else if (resp.status == 601) {
                        self.ifallow_over_submit = true
                        self.$notify.error({
                            title: '错误',
                            message: '原因:' + resp.message
                        });
                    } else {
                        self.errMsg = resp.message
                        self.$notify.error({
                            title: '错误',
                            message: resp.message
                        });
                    }
                    // self.$refs.boxCodes.focus();
                })
                self.ifconfirm = false
            },
            beforePrintBoxCode: function () {
                this.errMsg = ''
                //获取箱合格证
                if (this.count == null || this.count < 1) {
                    this.$notify.error({
                        title: '错误',
                        message: '请填写装箱数量'
                    });
                    return;
                }
                if (this.allow_over_submit) {
                    this.userData=[]
                    this.cardScanShow = true
                } else {
                    this.printBoxCode()
                }
            },
            onScanEnter: function () {
                this.errMsg = ''
                var code = this.scanValue;
                if (code.indexOf("|") > 0) {
                    code = code.split('|')[0];
                }
                this.scanValue = ""
                //判断是否扫描是获取到的箱合格证
                if (code !== this.fpBox.boxBarcode) {
                    this.errMsg = "合格证扫码不正确！"
                    return
                }
                //扫描确认
                var self = this
                this.$http.request({
                    url: '/api/package/box_confirm/' + this.fpBox.boxBarcode,
                    method: 'POST',
                    showLoading: true
                }).then(function (resp) {
                    if (resp.status == 100) {
                        self.$emit("packageDone");
                        self.dialogShow = false
                    } else {
                        self.errMsg = resp.message
                        self.$notify.error({
                            title: '错误',
                            message: '原因:' + resp.message
                        });
                    }
                })
            },
            boxReprint: function () {
                if (!this.fpBox)
                    return
                if (this.isMatBox)
                    this.$printer.printMatBoxLog(this.fpBox);
                else
                    this.$printer.printBoxLog(this.fpBox);
            },
            handleClose() {
                this.dialogShow = false
                this.$emit("packageDone");
            },
            //工单关闭刷卡回调
            cardCallBack: function (userData) {
                if (userData == null) {
                    this.$notify.error({
                        title: '错误',
                        message: '没有查到此人'
                    });
                    return;
                }
                this.userData=userData
                this.printBoxCode()
            },
        },

    };
</script>


