<template>
    <div class="appzx">
        <el-row :gutter="10">
            <el-col style="text-align: right">
                <el-button type="primary" size="mini" @click="machFeedingShow=true">过程码扫描</el-button>
                <!-- <el-button type="warning" size="mini">退料</el-button> -->
            </el-col>
            <div class="content">
                <el-col :span="24">
                    <div class="perfromConLeft">
                        <div class="perfromConLeftTitle" style="position: relative;">
                            <el-col>
                                <h3 class="information">
                                    车间基本信息</h3>
                                <p class="line">
                                    &nbsp;</p>
                            </el-col>
                        </div>

                        <div class="perfromConLeftContent">
                            <el-col :span="5">
                                <el-form ref="form" label-width="120px">
                                    <el-form-item label="工厂：">
                                        {{$ls.get('factory').fcName}}
                                    </el-form-item>
                                </el-form>
                            </el-col>
                            <el-col :span="5">
                                <el-form ref="form" label-width="120px">
                                    <el-form-item label="车间：">
                                        {{$ls.get('workshop').shopName}}
                                    </el-form-item>
                                </el-form>
                            </el-col>
                            <el-col :span="5">
                                <el-form ref="form" label-width="120px">
                                    <el-form-item label="生产线：">
                                        {{$ls.get('line').lineName}}<span style="color: rgb(228, 174, 94)">
                                            (公用道序)</span>
                                    </el-form-item>
                                </el-form>
                            </el-col>

                            <el-col :span="5">
                                <el-form ref="form" label-width="120px">
                                    <el-form-item label="班次：">
                                        {{$ls.get('shift').sfName}}
                                    </el-form-item>
                                </el-form>
                            </el-col>

                            <el-col :span="4">
                                <el-form ref="form" label-width="120px">
                                    <el-form-item label="工序号：">
                                        {{$ls.get('opNo')}}
                                    </el-form-item>
                                </el-form>
                            </el-col>

                        </div>
                    </div>
                </el-col>
            </div>

            <el-col :span="10">
                <p style="margin-bottom: 1.2vh;height: 4vh;color:#DAA651;margin-top: 1vh;" class="m_label">工单列表
                </p>
                <div class="GDtable">
                    <!-- tbative = 高亮class -->
                    <div class="GDtables" v-for="data in orderArr" @click="changeOrder(data)"
                        v-bind:class="{ tbative: data.poCode==iftbative }">
                        <el-col :span="24" class="ative">
                            <p class="tbNative_p" style="margin-top: 1.5vh;margin-left: 2vh;">工单号：{{data.poCode}}
                                <span v-if="data.poStatus=='Executing'">执行</span>
                                <span v-else-if="data.poStatus=='Frozen'">冻结</span>
                                <span v-else-if="data.poStatus=='Completed'">完成</span>
                                <span v-else-if="data.poStatus=='Assigned'">下发</span>
                                <span v-else>{{data.poStatus}}</span>
                            </p>
                            <p class="tbline">
                                &nbsp;</p>
                        </el-col>
                        <el-col :span="24">
                            <el-col :span="12">
                                <el-form ref="form">
                                    <el-form-item label="产品简码：" style="margin-top: .5vh;margin-left: 2vh;">
                                        {{data.prodCode}}
                                    </el-form-item>
                                </el-form>
                            </el-col>
                            <el-col :span="12">
                                <el-form ref="form">
                                    <el-form-item label="产品图号：" style="margin-top: .5vh;margin-left: 2vh;">
                                        {{data.prodTuhao}}
                                    </el-form-item>
                                </el-form>
                            </el-col>
                            <el-col :span=" 12">
                                <el-form ref="form">
                                    <!-- <el-form-item label="实际/计划：" style="margin-top: .5vh;margin-left: 2vh;">
                                        <span class=" shiji">0</span>/{{data.poCount}}
                                    </el-form-item> -->
                                    <el-form-item label="计划数量：" style="margin-top: .5vh;margin-left: 2vh;">
                                        {{data.poCount}}
                                    </el-form-item>
                                </el-form>
                            </el-col>
                            <el-col :span="12">
                                <el-form ref="form">
                                    <el-form-item label="计划开始时间：" style="margin-top: .5vh;margin-left: 2vh;">
                                        {{data.poStartDatetime}}
                                    </el-form-item>
                                </el-form>
                            </el-col>
                        </el-col>
                        <el-col :span=" 24">
                            <el-form ref="form" label-width="80px">
                                <el-form-item style="float: right;margin-right: 4vh;">
                                    <el-button v-if="data.poStatus == 'Executing'" class="tbNative_b" type="primary"
                                        size="mini" @click="confirm(data)">工单关闭
                                    </el-button>
                                    <el-button v-else class="tbNative_b" type="primary" size="mini"
                                        @click="startOrder(data)">工单开始</el-button>
                                </el-form-item>
                            </el-form>
                        </el-col>
                    </div>
                </div>
            </el-col>
            <el-col :span="14" class="tableBox">
                <p style="color:#DAA651;margin-top: 1vh;" class="m_label">工序列表</p>
                <div class="perfromConRight">
                    <div class="QDLTbale">
                        <el-col :span="24" class="QDLtbale" style="height: 7vh;border-bottom:1px solid #6780AA">
                            <el-form label-width="30px">
                                <el-col :span="6">
                                    <el-form-item>
                                        工单号：<span style="color:#D9A63E;font-weight: bold">{{iftbative}}</span>
                                    </el-form-item>
                                </el-col>
                                <el-col :span="6">
                                    <el-form-item>
                                        零件名称：{{nowOrder.prodName}}
                                    </el-form-item>
                                </el-col>
                                <el-col :span="6">
                                    <el-form-item>
                                        总投料数：{{nowOrder.touliaoSum}}
                                    </el-form-item>
                                </el-col>
                                <el-col :span="6">
                                    <el-form-item>
                                        总产出数：{{nowOrder.xialiaoSum}}
                                    </el-form-item>
                                </el-col>
                            </el-form>
                        </el-col>
                        <el-col :span="24" class="QDLtbale" style="height: 65vh !important">
                            <el-table style="width: 100%;height: 60vh;overflow-y:scroll" :data="procList">
                                <el-table-column label="工序号" prop="procCode">
                                </el-table-column>
                                <el-table-column label="工序" prop="procName">
                                </el-table-column>
                                <el-table-column label="物料号" prop="prodInfo.prodCode">
                                </el-table-column>
                                <el-table-column label="工序线上库存" prop="prodInfo.opUpStockNum">
                                </el-table-column>
                                <el-table-column label="工序线下库存" prop="prodInfo.opSideStockNum">
                                </el-table-column>
                                <el-table-column label="不合格数量" prop="prodInfo.unqualifyNum">
                                </el-table-column>
                                <el-table-column width="190" label="操作">
                                    <template slot-scope="scope">
                                        <el-button type="primary" size="mini" @click="lookInfo(scope.row)">详情
                                        </el-button>
                                        <el-button type="warning" size="mini" @click="unqualified(scope.row)">不合格品
                                        </el-button>
                                    </template>
                                </el-table-column>
                            </el-table>
                        </el-col>
                    </div>
                </div>
            </el-col>
        </el-row>
        <!-- 过程码扫描 -->
        <MachFeeding :show.sync="machFeedingShow" :orders="orderArr" :ifInsert="false" @exitmodal="exitmodal">
        </MachFeeding>
    </div>
</template>

<script>
    import MachFeeding from "@/components/mach_feeding.vue";
    export default {
        components: {
            MachFeeding: MachFeeding,
        },
        data() {
            return {
                iftbative: '',//工单选中
                nowOrder: '',
                orderArr: [],
                procList: [],
                machFeedingShow: false,//过程码扫描
            };
        },
        activated: function () {
            this.exitmodal()
        },
        watch: {
            '$route'() {
                if (this.$router.history.current.name == 'ProcExecution') {
                    this.exitmodal()
                }
            }
        },
        methods: {
            //工单改变
            changeOrder(data) {
                this.nowOrder=data
                this.iftbative = data.poCode//高亮
                var self = this
                this.$http.request({
                    url: '/api/process_info/' + data.prodCode,
                    method: 'GET',
                    params: {
                        line_id: data.nowLineId
                    }
                }).then(function (resp) {
                    if (resp.status == 100) {
                        self.procList = resp.data
                        self.queryProcess(data)
                        self.queryStock(self.procList[0].procCode)//第一道工序查询上料总数
                        self.queryProcPackage(self.procList[self.procList.length - 1].procCode)//最后一道工序查询下料总数
                        document.getElementById(self.iftbative).scrollIntoView({ behavior: "smooth" });
                    } else {
                        self.$notify.error({
                            title: '错误',
                            message: resp.message
                        });
                    }
                })
            },
            //上料总数查询
            queryStock: function (procCode) {
                var self = this;
                this.$http.request({
                    url: '/api/proc_stock/' + self.nowOrder.poCode + '/' + self.nowOrder.lineId + '/' + procCode,
                    method: 'GET',
                }).then(function (resp) {
                    self.nowOrder.touliaoSum = 0
                    for (var i = 0; i < resp.data.length; i++) {
                        self.nowOrder.touliaoSum += resp.data[i].boxQuan
                    }
                })
            },
            //下料总数查询
            queryProcPackage: function (procCode) {
                var self = this
                this.$http.request({
                    url: '/api/package/boxlog',
                    method: 'GET',
                    params: {
                        line_id: this.nowOrder.lineId,
                        order_code: this.nowOrder.poCode,
                        op_code: procCode,
                    }
                }).then(function (resp) {
                    if (resp.status == 100) {
                        self.nowOrder.xialiaoSum = 0
                        for (var i = 0; i < resp.data.list.length; i++) {
                            self.nowOrder.xialiaoSum += resp.data.list[i].boxQuan
                        }
                    }
                })
            },
            queryProcess(data) {
                var self = this
                var lineId = data.nowLineId
                for (var i = 0; i < self.procList.length; i++) {
                    (function (index) {
                        self.$http.request({
                            url: '/api/prod_status/process/' + data.poCode + '/' + lineId + '/' + self.procList[index].procCode,
                            method: 'GET',
                        }).then(function (resp) {
                            if (resp.status == 100) {
                                self.procList[index]['prodInfo'] = resp.data
                                self.$set(self.procList, index, self.procList[index])
                            } else {
                                self.$notify.error({
                                    title: '错误',
                                    message: resp.message
                                });
                            }
                        })
                    })(i);
                }
            },
            confirm(data){
                //关闭工单
                this.$message({
                    message: '暂未开启关闭通道',
                    type: 'info'
                });
                // this.curtOrder = data;
                // this.cardScanShow = true
            },
            //过程码model关闭
            exitmodal(nowOrder, returnPkg) {
                this.machFeedingShow = false;
                if (nowOrder) {
                    nowOrder.nowLineId = returnPkg.lineId
                    this.nowOrder = nowOrder
                    this.iftbative = nowOrder.poCode
                    for (var i = 0; i < this.orderArr.length; i++) {
                        if (this.orderArr[i].poCode == this.nowOrder.poCode) {
                            this.changeOrder(nowOrder)
                            return
                        }
                    }
                    this.orderArr[this.orderArr.length] = this.nowOrder
                    this.changeOrder(this.nowOrder)
                } else {
                    if (!this.nowOrder)
                        return
                    this.changeOrder(this.nowOrder)
                }

            },
            // 查看详情
            lookInfo(info) {
                this.$router.push({
                    name: 'ProcProcessDetail',
                    query: {
                        procCode: info.procCode,
                        procName: info.procName,
                        poCode: this.iftbative,
                        prodCode: this.nowOrder.prodCode,
                        lineId: this.nowOrder.nowLineId
                    }
                })
            },
            //不合格品录入
            unqualified: function (data) {
                this.$router.push({
                    name: 'ProcUnqualified_details',
                    params: {
                        lineId: this.nowOrder.lineId,
                        order: this.nowOrder,
                        poCode: this.iftbative,
                        procCode: data.procCode,
                        procName: data.procName
                    }
                })
            }
        }
    }
</script>


