<template>
	<div>
		<el-dialog title="过程码上料" @open="clearData" :visible.sync="dialogShow" width="35%" class="dialogtitleTL"
			:before-close="exitmodal" :close-on-click-modal="false">
			<el-col :span="24" style="padding-top:4vh;">
				<el-col :sm="20" :lg="20">
					<el-form ref="form" label-width="120px" @submit.native.prevent>
						<el-form-item label="扫描过程码：">
							<el-input v-model="scanValue" placeholder="请扫描录入" @keyup.enter.native="onScan"></el-input>
							<p v-if="errMsg.length>0" class="p_red">{{errMsg}}</p>
						</el-form-item>
					</el-form>
				</el-col>
				<el-col :sm="4" :lg="2">
					<el-form ref="form" label-width="20px">
						<el-form-item>
							<span>
								<img src="@/assets/images/sm.png" />
							</span>
						</el-form-item>
					</el-form>
				</el-col>
			</el-col>
			<el-col :span="24" style="position: relative;padding-bottom: 10px;">
				<h3 class="tbinformation">工单信息</h3>
				<p class="line">&nbsp;</p>
			</el-col>

			<el-col :span="24" class="Boxing1">
				<el-col :span="12">
					<el-form ref="form" label-width="140px">
						<el-form-item label="工单编号：">{{nowOrder.poCode}}</el-form-item>
					</el-form>
				</el-col>
				<el-col :span="12">
					<el-form ref="form" label-width="140px">
						<el-form-item label="产品图号：">{{nowOrder.prodTuhao}}</el-form-item>
					</el-form>
				</el-col>
				<el-col :span="12">
					<el-form ref="form" label-width="140px">
						<el-form-item label="计划数量：">{{nowOrder.poCount}}</el-form-item>
					</el-form>
				</el-col>
				<el-col :span="12">
					<el-form ref="form" label-width="140px">
						<el-form-item label="计划完成时间：">{{nowOrder.poEndDatetime}}</el-form-item>
					</el-form>
				</el-col>
			</el-col>

			<el-col :span="24" style="position: relative;padding-bottom: 10px;">
				<h3 class="tbinformation">物料信息</h3>
				<p class="line">&nbsp;</p>
			</el-col>

			<el-col :span="24" class="Boxing1">
				<el-col :span="12">
					<el-form ref="form" label-width="140px">
						<el-form-item label="物料名称：">{{Process.matName}}</el-form-item>
					</el-form>
				</el-col>
				<el-col :span="12">
					<el-form ref="form" label-width="140px">
						<el-form-item label="物料简码：">{{Process.matCode}}</el-form-item>
					</el-form>
				</el-col>
				<el-col :span="12">
					<el-form ref="form" label-width="140px">
						<el-form-item label="物料数量：">{{Process.boxQuan}}</el-form-item>
					</el-form>
				</el-col>
			</el-col>

			<el-col :span="24" style="position: relative;padding-bottom: 10px;">
				<h3 class="tbinformation">工序信息</h3>
				<p class="line">&nbsp;</p>
			</el-col>

			<el-col :span="24" class="Boxing1">
				<el-col :span="12">
					<el-form ref="form" label-width="140px">
						<el-form-item label="下料工序：">{{Process.opDesc}}</el-form-item>
					</el-form>
				</el-col>
				<el-col :span="12" class="feeding_color">
					<el-form ref="form" label-width="140px">
						<el-form-item label="当前上料工序：">{{noewFeedingName}}</el-form-item>
					</el-form>
				</el-col>
			</el-col>

			<div class="appdialog" style="text-align: center;padding: 10px;  background: #12599A;">
				<span slot="footer" class="dialog-footer">
					<!-- <el-button style="float: left;margin-top: 2vh;"  type="primary" size="mini" @click="exitmodal">定位到当前工单
					</el-button> -->
					<el-button v-if='ifhaveOrder' style=" margin-top: 2vh;" type="warning" size="mini"
						@click="insertOrder">上 料</el-button>
					<el-button v-else style="margin-top: 2vh;" type="warning" size="mini" @click="insertOrder">插 入
					</el-button>
					<el-button style=" margin-top: 2vh;" type="success" size="mini" @click="exitmodal">定位到当前工单</el-button>
				</span>
			</div>
		</el-dialog>
	</div>
</template>

<script>
	export default {
		props: ["show", "orders", "ifInsert"],
		name: "Modal",
		data() {
			return {
				dialogShow: this.show,
				errMsg: "",
				scanValue: "",
				noewFeedingCode: '',
				noewFeedingName: '',
				ifhaveOrder: false,

				Process: [], //过程码扫描
				nowOrder: '', //当前工单信息
				ProcessArr: [], //当前工序
			};
		},
		watch: {
			show: function (newValue, oldValue) {
				this.dialogShow = newValue;
			},
			dialogShow: function (newValue, oldValue) {
				if (this.show != this.dialogShow)
					this.$emit("update:show", this.dialogShow);
			}
		},

		methods: {
			onScan: function () {
				var scanValueTmp = this.scanValue
				if (scanValueTmp.indexOf("|") > 0) {
					scanValueTmp = scanValueTmp.split('|')[0];
				}
				this.scanValue = ''
				this.errMsg = ''
				var self = this
				//获取HU信息
				this.$http.request({
					url: '/api/package/' + scanValueTmp,
					method: 'GET',
					showLoading: true
				}).then(function (resp) {
					if (resp.status != 100) {
						self.errMsg = resp.message
						return
					}
					self.Process = resp.data[0];
					if (self.Process.poCode == '') {
						self.errMsg = '请扫描过程码'
						return
					}
					self.queryOrder(self.Process.poCode)
					self.ifhave(self.Process.poCode)
				});
			},
			//查询工单
			queryOrder: function (poCode) {
				var self = this
				this.$http.request({
					url: '/api/order',
					method: 'GET',
					params: {
						order_code: poCode
					}
				}).then(function (resp) {
					if (resp.status != 100) {
						self.errMsg = resp.message
						return
					}
					self.nowOrder = resp.data.list[0]
					self.nowOrder.lineId=self.Process.lineId
					self.queryProcess()
				});
			},
			//查询工序
			queryProcess: function () {
				var self = this
				this.$http.request({
					url: '/api/process_info/' + self.nowOrder.prodCode,
					method: 'GET',
					showLoading: true,
					params: {
						line_id: this.nowOrder.lineId
					}
				}).then(function (resp) {
					//错误提示
					if (resp) {
						self.ProcessArr = resp.data
						for (var i = 0; i < self.ProcessArr.length; i++) {
							if (self.Process.opNo == self.ProcessArr[i].procCode) {
								self.noewFeedingCode = self.ProcessArr[i + 1].procCode
								self.noewFeedingName = self.ProcessArr[i + 1].procName
								self.Process.opDesc = self.ProcessArr[i].procName
							}
						}
					} else {
						self.$notify.error({
							title: '错误',
							message: '查询工序：原因：' + resp.message
						})
					}
				})
			},
			//是否存在
			ifhave: function (poCode) {
				for (var i = 0; i < this.orders.length; i++) {
					if (this.orders[i].poCode == poCode) {
						this.ifhaveOrder = true
						return;
					}
				}
			},
			//插入工单
			insertOrder: function () {
				if (!this.noewFeedingName)
					return
				this.errMsg = ''
				var self = this
				if (!this.ifhaveOrder) {
					this.$http.request({
						url: '/api/order/' + this.nowOrder.poCode + '/line_insert',
						method: 'POST',
						params: {
							line_id: this.$ls.get('line').lineId
						}
					}).then(function (resp) {
						if (resp.status != 100) {
							self.errMsg = resp.message
							return
						} else {
							self.$notify({
								title: '成功',
								message: '插入工单成功',
								type: 'success',
							});
							self.deviceStockConfirm()
						}
					});
				} else {
					self.deviceStockConfirm()
				}
			},
			//上料
			deviceStockConfirm: function () {
				var self = this;
				var lineId =  this.nowOrder.lineId
				this.$http.request({
					url: '/api/proc_stock/' + self.nowOrder.poCode + "/" + lineId + '/' + self.noewFeedingCode,
					method: 'POST',
					showLoading: true,
					data: [this.Process]
				}).then(function (resp) {
					if (resp.status == 100) {
						self.exitmodal()
					}
					else {
						self.errMsg = resp.message
					}
				})
			},
			//关闭时触发
			exitmodal() {
				this.$emit("exitmodal", this.nowOrder, this.Process)
			},
			clearData() {
				if (this.ifInsert)
					this.ifhaveOrder = false
				else
					this.ifhaveOrder = true
				this.errMsg = ''
				this.Process = []
				this.nowOrder = ''
				this.noewFeedingName = ''
			}
		}
	};
</script>

<style>
</style>


