import axios from 'axios'
const URLPROTOCOL = 'mes://printer/'
const BASEURL = 'http://localhost:9599'

const hrefUrlTrigger = (url) => {
    let aTag = document.createElement('a');
    aTag.href = url
    document.body.append(aTag)
    aTag.click()
    document.body.removeChild(aTag)
}

const dateFormat = (dateStr) => {
    if(dateStr && dateStr.length > 10)
        dateStr = dateStr.slice(0, 10)
    return dateStr
}

const printer = {
    // 总成条码打印
    printFPBarcode: (prodCode, customerCode, lineId, fcId) => {
        let printMsg = {
            cmd: 'fpBarcode',
            data: {
                'prodCode': prodCode,
                'customerCode': customerCode,
                'lineId': lineId,
                'fcId':  fcId
            },
            timestamp: new Date().getTime()
        }

        axios({
            baseURL: BASEURL,
            url: '/mes_printer/print/fpBarcode',
            method: 'post',
            data: encodeURIComponent(JSON.stringify(printMsg))
        }).then((resp) => {
            console.log(resp)
        })
    },

    // 物料箱合格证打印
    printPackage: (pkg) => {
        let printMsg = {
            cmd: 'matbox',
            data: {
                boxBarcode: pkg.boxBarcode,
                sapCode: pkg.matCode,
                prodName: pkg.matName,
                prodCode: pkg.matNumber,
                tuhao: pkg.matTuhao,
                customerMtCode: pkg.customerMatCode,
                batchNo: pkg.matBatchNo,
                luhao: pkg.matFurnaceNo,
                glevel: pkg.matGlevel,
                count: pkg.boxQuan,
                checkPerson: pkg.boxInspector,
                date: dateFormat(pkg.prodDate),
            },
            timestamp: new Date().getTime()
        }

        axios({
            baseURL: BASEURL,
            url: '/mes_printer/print/matbox/' + pkg.boxBarcode,
            method: 'post',
            data: encodeURIComponent(JSON.stringify(printMsg))
        }).then((resp) => {
            console.log(resp)
        })
    },

    //成品物料箱打印
    printMatBoxLog: (boxlog) => {
        let printMsg = {
            cmd: 'matbox',
            data: {
                boxBarcode: boxlog.boxBarcode,
                sapCode: boxlog.prodCode,
                prodName: boxlog.prodName,
                prodCode: boxlog.prodNumber,
                tuhao: boxlog.prodTuhao,
                customerMtCode: boxlog.customerMatcode,
                batchNo: boxlog.prodBatchCode,
                luhao: boxlog.prodFurnaceNo,
                glevel: boxlog.prodGlevel,
                count: boxlog.boxQuan,
                checkPerson: boxlog.empName,
                date: dateFormat(boxlog.createTime),
            },
            timestamp: new Date().getTime()
        }
    
        axios({
            baseURL: BASEURL,
            url: '/mes_printer/print/fpbox/' + boxlog.boxBarcode,
            method: 'post',
            data: encodeURIComponent(JSON.stringify(printMsg))
        }).then((resp) => {
            console.log(resp)
        })
    },

    // 成品总成箱打印
    printBoxLog: (boxlog) => {
        let printMsg = {
            cmd: 'fpbox',
            data: {
                boxBarcode: boxlog.boxBarcode,
                sapCode: boxlog.prodCode,
                prodName: boxlog.prodName,
                prodCode: boxlog.prodNumber,
                tuhao: boxlog.prodTuhao,
                customerMtCode: boxlog.customerMatcode,
                batchNo: boxlog.prodBatchCode,
                luhao: boxlog.prodFurnaceNo,
                glevel: boxlog.prodGlevel,
                count: boxlog.boxQuan,
                checkPerson: boxlog.empName,
                date: dateFormat(boxlog.createTime),
            },
            timestamp: new Date().getTime()
        }
    
        axios({
            baseURL: BASEURL,
            url: '/mes_printer/print/fpbox/' + boxlog.boxBarcode,
            method: 'post',
            data: encodeURIComponent(JSON.stringify(printMsg))
        }).then((resp) => {
            console.log(resp)
        })
    },

    // 不合格品打印
    printNonconform: (nonconform) => {
        let printMsgs = []
        for(let i = 0 ; i < nonconform.nonconformProductMaterialPOs.length; i++){
            let nplmat = nonconform.nonconformProductMaterialPOs[i]
            let msg = {
                cmd: 'unqualify',
                data: {
                    barcode: nplmat.nplmBarcode,                    //不合格证条码
                    sapCode: nplmat.materialCode,                   //SAP物料号
                    code: nplmat.materialNumber,                    //零件简码
                    tuhao: nplmat.materialTuhao,                    //图号
                    desc1: nplmat.materialName,                     //零件描述
                    defectCount: nplmat.materialQty,                //缺陷数量
                    batchNo: nplmat.materialBatchCode,              //生产批号
                    luhao: nplmat.materialFurnaceNo,                //炉号
                    defectCode: nonconform.nplDefect,               //缺陷代码
                    prodDate: dateFormat(nonconform.createTime),    //生产日期
                    procedure: nonconform.nplFindOp,                //发现工序
                    workPerson: nonconform.createPersonName,        //操作人
                    supplier: "",                                   //供应商
                },
                timestamp: new Date().getTime()
            }

            axios({
                baseURL: BASEURL,
                url: '/mes_printer/print/unqualify/' + nonconform.nplBarcode,
                method: 'post',
                data: encodeURIComponent(JSON.stringify(msg))
            }).then((resp) => {
                console.log(resp)
            })
        }
    },

    // 过程码打印
    printWipBox: (boxlog) => {
        let printMsg = {
            cmd: 'wipbox',
            data: {
                boxBarcode: boxlog.boxBarcode,                  //合格证条码
                sapCode: boxlog.prodCode,                       //SAP号
                prodName: boxlog.prodName,                      //物料名称
                prodCode: boxlog.prodNumber,                    //物料简码
                tuhao: boxlog.prodTuhao,                        //图号
                batchNo: boxlog.prodBatchCode,                  //生产批号
                luhao: boxlog.prodFurnaceNo,                    //炉号
                glevel: boxlog.prodGlevel,                      //档位
                count: boxlog.boxQuan,                          //数量
                checkPerson: boxlog.empName,                    //校验人
                date: dateFormat(boxlog.createTime),            //日期
                orderCode: boxlog.poCode,                       //工单号
                opNo: boxlog.opNo,                              //下线工序号
                opDesc: boxlog.opDesc                           //下线工序名称
            },
            timestamp: new Date().getTime()
        }
    
        axios({
            baseURL: BASEURL,
            url: '/mes_printer/print/wipbox/' + boxlog.boxBarcode,
            method: 'post',
            data: encodeURIComponent(JSON.stringify(printMsg))
        }).then((resp) => {
            console.log(resp)
        })
    },

    //打印清线合格证
    printCleanLog: (cleanlog) => {
        let printMsg = {
            cmd: cleanlog.is_extra ? 'dybox' : 'matbox',
            data: {
                boxBarcode: cleanlog.boxBarcodeNew,
                sapCode: cleanlog.matCode,
                prodName: cleanlog.matName,
                prodCode: cleanlog.matNumber,
                tuhao: cleanlog.matTuhao,
                customerMtCode: '',
                batchNo: cleanlog.matBatchNo,
                luhao: cleanlog.matFurnaceNo,
                glevel: cleanlog.matGlevel,
                count: cleanlog.clRestCount,
                checkPerson: cleanlog.clPersonName,
                date: dateFormat(cleanlog.createTime),
            },
            timestamp: new Date().getTime()
        }
    
        axios({
            baseURL: BASEURL,
            url: '/mes_printer/print/fpbox/' + cleanlog.boxBarcodeNew,
            method: 'post',
            data: encodeURIComponent(JSON.stringify(printMsg))
        }).then((resp) => {
            console.log(resp)
        })
    }
}

export default printer


