import Vue from 'vue'
import Router from 'vue-router'
import routes from './routers'

Vue.use(Router)
const router = new Router({
    routes
})

const LOGIN_PAGE_NAME = ['Cardlogin', 'Textlogin']
const isLoginPage = function(pageName) {
    for(var i = 0; i < LOGIN_PAGE_NAME.length; i++){
        if(LOGIN_PAGE_NAME[i] == pageName)
            return true
    }
    return false
}

router.beforeEach((to, from, next) => {
    const token = localStorage.authToken
    if (!token && !isLoginPage(to.name)) {
        // 未登录且要跳转的页面不是登录页
        next({
            name: LOGIN_PAGE_NAME[0] // 跳转到登录页
        })
    }else {
        next() // 跳转
    }
})

export default router



