<template>
    <div class="app2">
        <el-row :gutter="10">
            <el-col :span="24">
                <div class="ButtonTitle">
                    <div class="offLinePrinting">
                        <span v-bind:class="{ ifoffLine: !offLinePrinting }" @click="offLinePrinting=false">在线打印</span>
                        <span v-bind:class="{ ifoffLine: offLinePrinting }" @click="offLinePrinting=true">离线打印</span>
                    </div>
                    <el-button type="primary" size="mini" @click="orderFeedingShow = true">物料上料</el-button>
                    <el-button type="primary" size="mini" @click="openCleanDialog">清线</el-button>
                    <el-button type="primary" size="mini" @click="orderPkgManuShow = true">未装满箱</el-button>
                    <el-button type="warning" size="mini" @click="onRFIDNew">RFID生成</el-button>
                    <el-button type="warning" size="mini" @click="onRFIDDelete">RFID删除</el-button>
                    <el-button type="warning" size="mini" @click="rePrint">总成补打</el-button>
                    <el-button type="warning" size="mini" @click="fpBcdReplaceShow = true">总成替换</el-button>
                </div>
            </el-col>

            <div class="content">
                <el-col :span="6">
                    <el-col :span="24" class="formleft">
                        <div class="perfromConLeftTitle" style="position: relative;">
                            <el-col :lg="24" :xl="24">
                                <h3 class="information">车间基本信息</h3>
                                <p class="line">&nbsp;</p>
                            </el-col>
                        </div>

                        <el-col :span="24">
                            <el-form label-width="100px">
                                <el-form-item label="工厂:">{{$ls.get('factory').fcName}}</el-form-item>
                            </el-form>
                        </el-col>
                        <el-col :span="24">
                            <el-form label-width="100px">
                                <el-form-item label="车间:">{{$ls.get('workshop').shopName}}</el-form-item>
                            </el-form>
                        </el-col>
                        <el-col :span="24">
                            <el-form label-width="100px">
                                <el-form-item label="生产线:">{{$ls.get('line').lineName}}</el-form-item>
                            </el-form>
                        </el-col>
                        <el-col :span="24">
                            <el-form label-width="100px">
                                <el-form-item label="班次:">{{$ls.get('shift').sfName}}</el-form-item>
                            </el-form>
                        </el-col>
                        <!-- <el-col :span="24">
                            <el-form label-width="100px">
                                <el-form-item label="线长:">{{$ls.get('user').userName}}</el-form-item>
                            </el-form>
                        </el-col> -->
                        <el-col :span="24">
                            <el-form label-width="100px">
                                <el-form-item label="操作员:">{{$ls.get('user').userName}}</el-form-item>
                            </el-form>
                        </el-col>

                        <el-col :span="24">
                            <div class="perfromConLeftTitle" style="position: relative;">
                                <el-col :lg="24" :xl="24">
                                    <h3 class="information">生产基本信息</h3>
                                    <p class="line">&nbsp;</p>
                                </el-col>
                            </div>
                        </el-col>

                        <el-col :span="24">
                            <el-form label-width="120px">
                                <el-form-item label="工单：">{{curtOrder.poCode}}</el-form-item>
                            </el-form>
                        </el-col>

                        <el-col :span="24">
                            <el-form label-width="120px">
                                <el-form-item label="实际/计划：">{{orderProdStatus.fpCount}}/{{curtOrder.poCount}}
                                </el-form-item>
                            </el-form>
                        </el-col>

                        <el-col :span="24">
                            <el-form label-width="120px">
                                <el-form-item label="当前装箱：">
                                    {{orderProdStatus.boxCount}}/{{orderProdStatus.planBoxCount}}</el-form-item>
                            </el-form>
                        </el-col>

                        <el-col :span="24">
                            <el-form label-width="120px">
                                <el-form-item label="当前装箱数：">{{fpToBoxClose.length}}/{{boxInfo.bifCount}}</el-form-item>
                            </el-form>
                        </el-col>

                        <el-col :span="24">
                            <el-form label-width="120px">
                                <el-form-item label="总成简码：">{{curtOrder.prodCode}}</el-form-item>
                            </el-form>
                        </el-col>

                        <el-col :span="24">
                            <el-form label-width="120px">
                                <el-form-item label="图号：">{{curtOrder.prodTuhao}}</el-form-item>
                            </el-form>
                        </el-col>
                        <el-col :span="24">
                            <el-form label-width="120px">
                                <el-form-item label="物料描述：">{{curtOrder.prodName}}</el-form-item>
                            </el-form>
                        </el-col>
                        <el-col :span="24">
                            <el-form label-width="120px">
                                <el-form-item label="客户：">{{curtOrder.customerCode}}</el-form-item>
                            </el-form>
                        </el-col>


                    </el-col>
                </el-col>
                <el-col :span="18">
                    <div class="perfromConRight">
                        <el-col :span="24">
                            <div class="qualified">
                                <div class="forminput">
                                    <el-col :span="6" class="rfid">
                                        <el-form ref="form" label-width="100px" @submit.native.prevent>
                                            <el-form-item label="RFID:">
                                                <el-input v-model="inputRfid" style="width:200px"></el-input>
                                            </el-form-item>
                                        </el-form>
                                    </el-col>

                                    <el-col :span="6" class="rfid">
                                        <el-form ref="form" label-width="150px" @submit.native.prevent>
                                            <el-form-item label="成品扫描:">
                                                <el-input ref="barcode" v-model="inputFPCode" style="width:200px"
                                                    :autofocus="true" maxlength="200"
                                                    @keydown.enter.native.prevent="onFPCodeScan">
                                                </el-input>、
                                            </el-form-item>
                                        </el-form>
                                    </el-col>
                                    <el-col :span="6">
                                        <el-form ref="form" label-width="10px"
                                            style="margin-top: 1vh;margin-left: 4.5vh;" @submit.native.prevent>
                                            <el-form-item label="">
                                                <span style="color: red;"> {{errMsg}}</span>
                                            </el-form-item>
                                        </el-form>
                                    </el-col>
                                    <el-col :span="6" v-if="fpPass">
                                        <div class="qualifiedIcon clear">
                                            <i class="el-icon-success"></i>
                                            <span style="line-height: 5vh;">合格</span>
                                        </div>
                                    </el-col>
                                    <el-col :span="6" v-else>
                                        <div class="qualifiedIcon clear">
                                            <i class="el-icon-error"></i>
                                            <span style="line-height: 5vh;">不合格</span>
                                        </div>
                                    </el-col>
                                </div>
                            </div>
                        </el-col>

                        <el-col :span="24" style="height: 67vh;">
                            <el-col :span="12" style=" padding-left: 0px;">
                                <el-col class="ListingbarBox" style="height:30vh">
                                    <p>待结箱栏</p>
                                    <el-table ref="toBoxCloseTable" style="width: 100%;height:34vh;overflow: scroll"
                                        :data="fpToBoxClose" highlight-current-row @row-click="handleCurrentChange">
                                        <el-table-column label="总成号" prop="fpBarcode"></el-table-column>
                                        <el-table-column label="装箱时间" prop="createTime"></el-table-column>
                                        <el-table-column label="操作">
                                            <template slot-scope="scope">
                                                <el-button type="warning" size="mini"
                                                    @click="changeStatus(scope.row,'ToBox')">跳 过</el-button>
                                            </template>
                                        </el-table-column>
                                    </el-table>
                                </el-col>
                            </el-col>
                            <el-col :span="12" style=" padding-right: 0px;">
                                <div class="ListingbarBox">
                                    <p>已结箱清单栏</p>
                                    <el-table style="width: 100%;height:34vh;overflow: scroll" :data="packages">
                                        <el-table-column label="箱条码号" prop="boxBarcode"></el-table-column>
                                        <el-table-column label="数量" prop="boxQuan"></el-table-column>
                                        <el-table-column label="结箱时间" prop="createTime"></el-table-column>
                                    </el-table>
                                </div>
                            </el-col>

                            <el-col :span="12" style=" padding-left: 0px;">
                                <div class="ListingbarBox">
                                    <p>待装箱栏</p>
                                    <el-table style="width: 100%;height:34vh;overflow: scroll" :data="fpToBox">
                                        <el-table-column label="总成号" prop="fpBarcode"></el-table-column>
                                        <el-table-column label="跳过时间" prop="createTime"></el-table-column>
                                        <el-table-column label="操作">
                                            <template slot-scope="scope">
                                                <el-button type="success" size="mini"
                                                    @click="changeStatus(scope.row,'ToBoxClose')">待装箱</el-button>
                                            </template>
                                        </el-table-column>
                                    </el-table>
                                </div>
                            </el-col>

                            <el-col :span="12" style=" padding-right: 0px;">
                                <div class="ListingbarBox">
                                    <p>物料清单栏</p>
                                    <el-table style="width: 100%;height:34vh;overflow: scroll" :data="stockBomArr"
                                        :default-sort="{prop: 'materialNumLack', order: 'descending'}"
                                        :row-class-name="tableRowClassName">
                                        <el-table-column label="物料号" prop="materialCode"></el-table-column>
                                        <el-table-column label="名称" prop="materialName"></el-table-column>
                                        <el-table-column label="上料数量" prop="materialNumLeft"></el-table-column>
                                        <el-table-column label="差额" prop="materialNumLack">
                                        </el-table-column>
                                    </el-table>
                                </div>
                            </el-col>
                        </el-col>
                    </div>
                </el-col>
            </div>
        </el-row>

        <!--物料上料弹出框-->
        <OrderFeeding :show.sync="orderFeedingShow" :order="curtOrder" @feeded="refreshStatus"></OrderFeeding>

        <!--清线弹出框 -->
        <OrderStockCline :show.sync="orderClineShow" :order="curtOrder" :initCleanLogs="cleanlogs"
            @orderClosed="queryExecutingOrder"></OrderStockCline>

        <!--箱合格证弹出框 -->
        <OrderPackageAuto :show.sync="orderPkgAutoShow" :order="curtOrder" :boxInfo="boxInfo"
            @packageDone="refreshStatus" :boxLog="boxlogToConfirm"></OrderPackageAuto>

        <!--未满装箱弹出框 -->
        <OrderPackageManu :show.sync="orderPkgManuShow" :order="curtOrder" @packageDone="refreshStatus">
        </OrderPackageManu>

        <!--总成替换弹出框 -->
        <FPBarcodeReplace :show.sync="fpBcdReplaceShow" :order="curtOrder" :fpToBoxClose="fpToBoxClose"
            @completed="refreshStatus"></FPBarcodeReplace>

    </div>
</template>

<script>
    import CardInput from "@/components/card_input.vue";
    import OrderFeeding from "@/components/order_feeding.vue";
    import OrderStockCline from "@/components/order_stock_cline.vue";
    import OrderPackageAuto from "@/components/order_package_auto.vue";
    import OrderPackageManu from "@/components/order_package_manu.vue";
    import FPBarcodeReplace from "@/components/fp_barcode_replace.vue";

    export default {
        components: {
            CardInput,
            OrderFeeding,
            OrderStockCline,
            OrderPackageAuto,
            OrderPackageManu,
            FPBarcodeReplace
        },
        data() {
            return {
                curtOrder: {},
                orderProdStatus: {},
                orderFeedingShow: false,
                orderClineShow: false,
                orderPkgAutoShow: false,
                orderPkgManuShow: false,
                fpBcdReplaceShow: false,
                boxInfo: {},
                fpToBoxClose: [],
                fpToBox: [],
                packages: [],
                stockBomArr: [],
                inputRfid: '',
                inputFPCode: '',
                errMsg: '',
                fpPass: true,
                cleanlogs: [],
                //选中的总成
                pfSelected: {},
                //条码长度
                fpbarCodeLength: '',
                //待确认物料箱体
                boxlogToConfirm: null,
                //离线打印
                offLinePrinting: false,
                //FP网络状态
                fpUploading: false
            };
        },
        created: function () {
            this.queryExecutingOrder()
        },
        watch: {
            orderFeedingShow: function (newValue, oldValue) {
                if (!newValue) {
                    this.refreshStatus();
                }
            },
            orderPkgManuShow: function (newValue, oldValue) {
                if (!newValue) {
                    this.refreshStatus();
                }
            },
            fpBcdReplaceShow: function (newValue, oldValue) {
                if (!newValue) {
                    this.refreshStatus();
                }
            },
            orderPkgAutoShow: function (newValue, oldValue) {
                if (!newValue) {
                    this.refreshStatus();
                }
            },
        },
        methods: {
            queryExecutingOrder: function () {
                var self = this
                //查询当前执行工单
                var self = this
                this.$http.request({
                    url: '/api/order',
                    method: 'GET',
                    params: {
                        line_id: this.$ls.get('line').lineId,
                        status: JSON.stringify(['Executing'])
                    }
                }).then(function (resp) {
                    if (resp.data.list.length > 0) {
                        self.curtOrder = resp.data.list[0];
                        self.initOrderExec()
                    } else {
                        //没有执行工单
                        self.$notify.error({
                            title: '错误',
                            message: '没有工单在执行!'
                        });
                        self.curtOrder = {}
                        self.fpToBoxClose = []
                        self.fpToBox = []
                        self.packages = []
                        self.stockBomArr = []
                    }
                })
            },
            // 工单执行初始化
            initOrderExec: function () {
                // 查询额定装箱数量
                this.queryBoxInfo();

                // 查询额定条码长度
                this.queryfpBarcodeLength();
                this.queryCleanlog();
                this.refreshStatus();
            },
            refreshStatus: function () {
                console.log('ll')
                if (this.curtOrder.poCode == null) {
                    return;
                }
                // 轮训工单加工状态
                this.queryOrderStatus()

                // 轮训待结箱数据
                this.queryToBoxCloseFP()

                // 轮训待装箱数据
                this.queryToBoxFP()

                // // 轮训装箱数据
                this.queryOrderPackage()

                // // 轮训物料统计
                this.queryOrderStockBomView()

                this.$refs.barcode.focus();
            },
            queryOrderStatus: function () {
                var self = this
                this.$http.request({
                    url: '/api/prod_status/order/' + this.curtOrder.poCode,
                    method: 'GET'
                }).then(function (resp) {
                    if (resp.data) {
                        self.orderProdStatus = resp.data;
                    }
                })
            },
            queryBoxInfo: function () {
                var self = this
                this.$http.request({
                    url: '/api/package/standard/' + this.curtOrder.prodCode,
                    method: 'GET',
                    params: {
                        customer_code: this.curtOrder.customerCode
                    }
                }).then(function (resp) {
                    if (resp.data) {
                        self.boxInfo = resp.data;
                        if (self.fpToBoxClose.length >= self.boxInfo.bifCount) {
                            if (self.orderPkgManuShow) {

                                self.orderPkgAutoShow = true
                                console.log(self.orderPkgManuShow)
                                console.log(self.orderPkgManuShow)
                            }
                        }
                    }
                })
            },
            queryToBoxCloseFP: function () {
                var self = this
                this.$http.request({
                    url: '/api/finished_product',
                    method: 'GET',
                    params: {
                        order_code: this.curtOrder.poCode,
                        status: JSON.stringify(['ToBoxClose']),
                        page_num: 1,
                        page_size: 500,
                    }
                }).then(function (resp) {
                    if (resp.data) {
                        self.fpToBoxClose = resp.data.list
                        // 判断是否需要装箱
                        if (self.fpToBoxClose.length >= self.boxInfo.bifCount) {
                            if (!self.orderPkgManuShow) {

                                self.orderPkgAutoShow = true
                                console.log(self.orderPkgManuShow)
                            }
                        }
                    }
                })
            },
            queryToBoxFP: function () {
                var self = this
                this.$http.request({
                    url: '/api/finished_product',
                    method: 'GET',
                    params: {
                        order_code: this.curtOrder.poCode,
                        page_size: this.boxInfo.bifCount,
                        status: JSON.stringify(['ToBox'])
                    }
                }).then(function (resp) {
                    if (resp.data) {
                        self.fpToBox = resp.data.list
                    }
                })
            },
            queryOrderPackage: function () {
                var self = this
                this.$http.request({
                    url: '/api/package/boxlog',
                    method: 'GET',
                    params: {
                        order_code: this.curtOrder.poCode
                    }
                }).then(function (resp) {
                    if (resp.data) {
                        self.packages = resp.data.list

                        //检验是否有待确认箱
                        for (var i = 0; i < self.packages.length; i++) {
                            var boxlog = self.packages[i]
                            if (!boxlog.isConfirmed) {
                                self.boxlogToConfirm = boxlog;
                                if (!self.orderPkgManuShow) {

                                    self.orderPkgAutoShow = true
                                    console.log(self.orderPkgManuShow)
                                }
                                return;
                            }
                        }
                        self.boxlogToConfirm = null
                    }
                })
            },
            queryOrderStockBomView: function () {
                var self = this
                this.$http.request({
                    url: '/api/po_stack/statistic/' + this.curtOrder.poCode,

                    method: 'GET'
                }).then(function (resp) {
                    if (resp.data) {
                        self.stockBomArr = resp.data
                    }
                })
            },
            onRFIDNew: function () {
                this.inputRfid = this.guidRFID()
                this.$refs.barcode.focus();
            },
            onRFIDDelete: function () {
                this.inputRfid = ''
            },
            guidRFID: function () {
                return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
                    var r = Math.random() * 16 | 0,
                        v = c == 'x' ? r : (r & 0x3 | 0x8);
                    return v.toString(16);
                });
            },
            onFPCodeScan: function () {
                console.log(this.inputFPCode.length + ","+this.inputFPCode)

                this.$refs.barcode.blur()
                var barcode = this.inputFPCode;
                this.inputFPCode = '';

                //网络请求状态校验
                if(this.fpUploading){
                    this.fpBarcodeScanFocus()
                    return
                }

                //条码长度校验
                if (this.fpbarCodeLength <= 0) {
                    this.fpbarCodeLength = barcode.length;
                } else if (barcode.length != this.fpbarCodeLength) {
                    console.log("barcode:", barcode, barcode.length);
                    console.log("fpbarCodeLength:" + this.fpbarCodeLength);
                    this.errMsg = '前端判断条码长度不匹配';
                    this.fpBarcodeScanFocus()
                    return;
                }
                
                //发起请求
                var self = this
                self.fpUploading = true
                var fpObj = {
                    poCode: this.curtOrder.poCode,
                    customerCode: this.curtOrder.customerCode == "" ? "NA" : this.curtOrder.customerCode,
                    lineId: this.$ls.get('line').lineId,
                    prodCode: this.curtOrder.prodCode,
                    fpRfid: this.inputRfid,
                    fpBarcode: barcode
                }
                self.errMsg = ''
                this.$http.request({
                    url: '/api/finished_product',
                    method: 'POST',
                    data: fpObj
                }).then(function (resp) {
                    self.fpUploading = false
                    self.fpBarcodeScanFocus()
                    if (resp.status == 100) {
                        //报交成功
                        self.onRFIDNew()
                        // 刷新状态
                        self.refreshStatus()
                        if (!self.offLinePrinting) {
                            self.rePrint();
                        }
                    } else {
                        // 错误提示
                        self.errMsg = resp.message
                    }
                }).catch(function (error) {
                    self.fpUploading = false
                    self.fpBarcodeScanFocus()
                })
            },

            //查询是否为做过清线
            queryCleanlog: function () {
                var self = this;
                //查询有无未做完的清线
                this.$http.request({
                    url: '/api/po_stack/cleanlog?order_code=' + self.curtOrder.poCode + '&is_confirmed=false',
                    method: 'GET'
                }).then(function (resp) {
                    self.cleanlogs = resp.data.list;
                    if (self.cleanlogs.length > 0) {
                        self.orderClineShow = true;
                    }
                })
            },
            fpDelete: function () {
                var self = this;
                //var pfData=this.$refs.toBoxCloseTable.store.states.selection;
                if (this.pfSelected == null) {
                    this.$notify.error({
                        title: '错误',
                        message: '请选择一个产品'
                    });
                    return;
                }

                this.$http.request({
                    url: '/api/finished_product/' + this.pfSelected.fpBarcode,
                    method: 'DELETE',
                }).then(function (resp) {
                    if (resp.data) {
                        self.refreshStatus();
                    }
                })
            },
            handleCurrentChange: function (pfSelect) {
                if (this.pfSelected != pfSelect) {
                    this.pfSelected = pfSelect;
                } else {
                    this.$refs.toBoxCloseTable.setCurrentRow();
                }
            },
            //改变产品状态
            changeStatus: function (productData, toStatus) {
                var self = this;
                this.$http.request({
                    url: "/api/finished_product/change_status",
                    method: 'PUT',
                    params: {
                        fp_barcode: productData.fpBarcode,
                        to_status: toStatus
                    }
                }).then(function (resp) {
                    if (resp.data) {
                        self.queryToBoxCloseFP();
                        self.queryToBoxFP();
                    }
                });
            },
            //查询条码长度
            queryfpBarcodeLength: function () {
                var self = this;
                this.$http.request({
                    url: '/api/product_info/printer_tmpl',
                    method: 'GET',
                    params: {
                        fcId: this.$ls.get('factory').fcId,
                        prod_code: self.curtOrder.prodCode,
                        customer_code: self.curtOrder.customerCode
                    }
                }).then(function (resp) {
                    if (resp.data.list && resp.data.list.length > 0) {
                        self.fpbarCodeLength = resp.data.list[0].ptmplSize;
                    }
                })
            },
            //补打
            rePrint: function () {
                var self = this;
                this.$printer.printFPBarcode(self.curtOrder.prodCode, self.curtOrder.customerCode, 
                    this.$ls.get('line').lineId, this.$ls.get('factory').fcId);
                this.$refs.barcode.focus();
            },
            //打开清线
            openCleanDialog: function () {
                if (this.fpToBoxClose.length > 0) {
                    this.$notify.error({
                        title: '错误',
                        message: '请将未结箱的产品结箱'
                    });
                    return;
                }
                if (this.fpToBox.length > 0) {
                    this.$notify.error({
                        title: '错误',
                        message: '请将未装箱的产品装箱'
                    });
                    return;
                }
                this.orderClineShow = true
            },
            tableRowClassName: function ({
                row,
                rowIndex
            }) {
                if (row.materialNumLack > 0) {
                    return 'fontred01';
                }
                return '';
            },
            //扫码聚焦
            fpBarcodeScanFocus: function(){
                var self = this
                setTimeout(function(){
                    self.inputFPCode = '';
                    self.$refs.barcode.focus()
                }, 200)
            }
        }
    }
</script>

<style>
    .errmsgbox .el-form-item__label {
        color: red !important;
    }

    .el-table .warning-row {
        background: oldlace;
    }

    .offLinePrinting {
        display: inline-block;
        line-height: 2;
        white-space: nowrap;
        cursor: pointer;
        border: 1px solid #409EFF;
        color: rgb(132, 151, 161);
        border-radius: 3px;
        transition: .1s;
        list-style-type: none;
        text-decoration: none;
    }

    .offLinePrinting span {
        display: inline-block;
        padding: 0px 10px;
        height: 28px;
        background-color: rgba(146, 146, 146, 0);
    }

    .ifoffLine {
        color: #fff !important;
        background-color: #409EFF !important;

    }
</style>


