<template>
<div>
    <el-dialog title="总成替换" :close-on-click-modal="false" :visible.sync="dialogShow" width="40%" class="dialogtitleTH" @open="onDialogOpen">
        <el-col :span="24" style="padding:4vh 1vh 0 1vh;;border-bottom: 1px solid #2B6EAB;" class="tihuan">
            <el-col :sm="20" :lg="19">
                <el-form label-width="90px" @submit.native.prevent>
                    <el-form-item label="原总成号证:">
                        <el-input placeholder="请输入旧条码或者扫描录入" v-model="oldBarcode"></el-input>
                    </el-form-item>
                </el-form>
            </el-col>

            <el-col :sm="4" :lg="2">
                <el-form label-width="40px">
                    <el-form-item>
                        <span>
                            <el-button type="primary" size="small" @click="oldBarcode=''">清空</el-button>
                        </span>
                    </el-form-item>
                </el-form>
            </el-col>

            <el-col :sm="20" :lg="19">
                <el-form label-width="90px" @submit.native.prevent>
                    <el-form-item label="新总成号证:">
                        <el-input placeholder="请输入新条码或者扫描录入" v-model="newBarcode" ref="newbarcode"></el-input>
                        <p v-if="smSusuccess.length>0" class="p_red">{{smSusuccess}}</p>
                    </el-form-item>
                </el-form>
            </el-col>

            <el-col :sm="4" :lg="2">
                <el-form label-width="40px">
                    <el-form-item>
                        <span>
                            <el-button type="primary" size="small" @click="newBarcode=''">清空</el-button>
                        </span>
                    </el-form-item>
                </el-form>
            </el-col>
            <el-col :sm="19" :lg="14" class="tishi">
                <el-form label-width="210px">
                    <el-form-item :label="errMsg"></el-form-item>
                </el-form>
            </el-col>
        </el-col>
        <div class="appdialog" style="text-align: center; padding: 10px;  background: #12599A;">
            <el-col :span="24" style="margin-bottom: 1.5vh;padding-right:1vh;" class="nextButton">
                <el-button type="warning" size="mini" @click="fpReplace">确认</el-button>
            </el-col>
        </div>
    </el-dialog>
</div>
</template>

<script>
export default {
    props: ["show", "order", "fpToBoxClose"],
    name: "FPBarcodeReplace",
    data() {
        return {
            dialogShow: this.show,
            stockLogs: [],
            scanValue: "",
            errMsg: "",
            smSusuccess: '',
            oldBarcode: '',
            newBarcode: '',
        };
    },
    watch: {
        show: function (newValue, oldValue) {
            this.dialogShow = newValue;
        },
        dialogShow: function (newValue, oldValue) {
            if (this.show != this.dialogShow)
                this.$emit("update:show", this.dialogShow);
        }
    },
    methods: {
        onDialogOpen: function () {
            this.smSusuccess=''
            this.errMsg=''
            this.oldBarcode=''
            this.newBarcode=''
        },
        fpReplace: function () {
            //判断条码是否为空
            this.errMsg = '';
            var self = this;
            if(this.newBarcode.length != this.oldBarcode.length){
                this.smSusuccess = '新旧总成条码长度不匹配，请重新扫描';
                return;
            }
            this.$http.request({
                url: '/api/finished_product/barcode_replace',
                method: 'PUT',
                params: {
                    line_id: this.$ls.get('line').lineId,
                    prod_code: this.order.prodCode,
                    customer_code: this.order.customerCode,
                    fp_barcode: this.oldBarcode,
                    fp_barcode_new: this.newBarcode
                }
            }).then(function (resp) {
                if (resp.data) {
                    self.dialogShow = false;
                    self.$emit('completed');
                } else {
                    self.errMsg = resp.message;
                }
                self.oldBarcode = '';
                self.newBarcode = '';
            })
        },
        // onFPCodeScan: function () {
        //     var result = false;
        //     for (var i = 0; i < this.fpToBoxClose.length; i++) {
        //         if (this.fpToBoxClose[i].fpBarcode == this.oldBarcode) {
        //             this.$printer.printFPBarcode(self.order.prodCode, self.order.customerCode, this.$ls.get('line').lineId);
        //             this.$refs.newbarcode.focus();
        //             result = true;
        //             this.smSusuccess = '';
        //             return;
        //         }
        //     }
        //     if (!result) {
        //         this.smSusuccess = '总成号不存在，请重新扫描';
        //         // this.oldBarcode = '';
        //     }
        // }
    }
};
</script>



