// 首页
import Home from '@/views/Home'

// 登录页
import Textlogin from '@/views/login/Textlogin'
import Cardlogin from '@/views/login/Cardlogin'

// cv装配
import CvIndex from '@/views/model_assy_cv/index'
import CvPreparation from '@/views/model_assy_cv/views/Preparation'
import CvExecution from '@/views/model_assy_cv/views/execution'
import CvUnqualified_details from '@/components/badproducts/unqualified_details'
import CvUnqualified from '@/components/badproducts/unqualified'

// 机加非一物流
import MachIndex from '@/views/model_machining/index'
import MachExecution from '@/views/model_machining/views/execution'
import MachProcessDetail from '@/views/model_machining/views/processdetail'
import MaUnqualified_details from '@/components/badproducts/unqualified_details'
import MaUnqualified from '@/components/badproducts/unqualified'

// 共用道序
import ProcIndex from '@/views/public_proc/index'
import ProcExecution from '@/views/public_proc/views/execution'
import ProcProcessDetail from '@/views/public_proc/views/processdetail'
import ProcUnqualified_details from '@/components/badproducts/unqualified_details'
import ProcUnqualified from '@/components/badproducts/unqualified'

// 节装配PNG
import SectIndex from '@/views/model_assy_sect/index'
import SectExecution from '@/views/model_assy_sect/views/execution'
import SeUnqualified_details from '@/components/badproducts/unqualified_details'
import SeUnqualified from '@/components/badproducts/unqualified'

// SafeLaunch
import SafelunchIndex from '@/views/safe_launch/index'
import SafelaunchImp from '@/views/safe_launch/views/safelaunch_imp'
import SafelaunchQuery from '@/views/safe_launch/views/safelaunch_query'

// EP
import epIndex from '@/views/model_assy_ep/index'
import epPreparation from '@/views/model_assy_ep/views/preparation'
import epExecution from '@/views/model_assy_ep/views/execution'
import epExecution_details from '@/views/model_assy_ep/views/execution_details'
import epExecution_report from '@/views/model_assy_ep/views/execution_report'
import eprework from '@/views/model_assy_ep/views/eprework'
import eprework_details from '@/views/model_assy_ep/views/eprework_details'

//不合格品
import badproducts_details from '@/components/badproducts/unqualified_details'
import badproducts from '@/components/badproducts/unqualified'
export default [
    { //账号登录
        path: '/textlogin',
        name: 'Textlogin',
        component: Textlogin,
        replace: true
    },
    { //刷卡登录
        path: '/cardlogin',
        name: 'Cardlogin',
        component: Cardlogin,
        meta: {
            keepAlive: true
        },
    },
    { //刷卡登录
        path: '/',
        name: 'Cardlogin',
        component: Cardlogin,
        meta: {
            keepAlive: true
        },
    },
    {
        path: '/',
        name: 'Home',
        component: Home,
        children: [
            {// cv装配
                path: 'cv_assy',
                name: 'CvIndex',
                component: CvIndex,
                redirect: { name: 'CvPreparation' },
                children: [
                    {
                        path: 'preparation',
                        name: 'CvPreparation',
                        component: CvPreparation
                    },
                    {
                        path: 'execution',
                        name: 'CvExecution',
                        component: CvExecution
                    },
                    {
                        path: 'badproducts_details',
                        name: 'badproducts_details',
                        component: badproducts_details
                    },
                    {
                        path: 'CvUnqualified_details',
                        name: 'CvUnqualified_details',
                        component: CvUnqualified_details
                    }, {
                        path: 'CvUnqualified',
                        name: 'CvUnqualified',
                        component: CvUnqualified
                    }
                ]
            },
            {// 机加非一物流
                path: 'machining',
                name: 'MachIndex',
                component: MachIndex,
                redirect: { name: 'MachExecution' },
                children: [
                    {
                        path: 'execution',
                        name: 'MachExecution',
                        component: MachExecution,
                        meta: {
                            keepAlive: true
                        },
                    }, {
                        path: 'processdetail',
                        name: 'MachProcessDetail',
                        component: MachProcessDetail
                    },
                    {
                        path: 'MaUnqualified_details',
                        name: 'MaUnqualified_details',
                        component: MaUnqualified_details
                    }, {
                        path: 'MaUnqualified',
                        name: 'MaUnqualified',
                        component: MaUnqualified,
                        meta: {
                            keepAlive: true
                        },
                    }
                ]
            },
            {// 共用道序
                path: 'public_proc',
                name: 'ProcIndex',
                component: ProcIndex,
                redirect: { name: 'ProcExecution' },
                children: [
                    {
                        path: 'execution',
                        name: 'ProcExecution',
                        component: ProcExecution,
                        meta: {
                            keepAlive: true
                        },
                    }, {
                        path: 'ProcProcessDetail',
                        name: 'ProcProcessDetail',
                        component: ProcProcessDetail
                    },
                    {
                        path: 'ProcUnqualified_details',
                        name: 'ProcUnqualified_details',
                        component: ProcUnqualified_details
                    },
                    {
                        path: 'ProcUnqualified',
                        name: 'ProcUnqualified',
                        component: ProcUnqualified,
                        meta: {
                            keepAlive: true
                        },
                    }
                ]
            },
            {// 节装配
                path: 'sect_assy',
                name: 'SectIndex',
                component: SectIndex,
                redirect: { name: 'SectExecution' },
                children: [
                    {
                        path: 'execution',
                        name: 'SectExecution',
                        component: SectExecution,
                        meta: {
                            keepAlive: true
                        },
                    },
                    {
                        path: 'SeUnqualified_details',
                        name: 'SeUnqualified_details',
                        component: SeUnqualified_details
                    },
                    {
                        path: 'SeUnqualified',
                        name: 'SeUnqualified',
                        component: SeUnqualified,
                        meta: {
                            keepAlive: true
                        },
                    }
                ]
            },
            {   // SafeLaunch
                path: 'safelaunch',
                name: 'SafelunchIndex',
                component: SafelunchIndex,
                redirect: { name: 'SafelaunchImp' },
                children: [
                    {
                        path: 'safelaunch_imp',
                        name: 'SafelaunchImp',
                        component: SafelaunchImp
                    },
                    {
                        path: 'safelaunch_query',
                        name: 'SafelaunchQuery',
                        component: SafelaunchQuery
                    }
                ]
            },
            {   //ep
                path: 'ep_assy',
                name: 'epIndex',
                component: epIndex,
                redirect: { name: 'epPreparation' },
                children: [
                    {
                        path: 'epPreparation',
                        name: 'epPreparation',
                        component: epPreparation
                    },
                    {
                        path: 'epExecution',
                        name: 'epExecution',
                        component: epExecution
                    },
                    {
                        path: 'epExecution_details',
                        name: 'epExecution_details',
                        component: epExecution_details
                    },
                    {
                        path: 'epExecution_report',
                        name: 'epExecution_report',
                        component: epExecution_report
                    },
                    {
                        path: 'eprework',
                        name: 'eprework',
                        component: eprework
                    },
                    {
                        path: 'eprework_details',
                        name: 'eprework_details',
                        component: eprework_details
                    },
                ]
            },
        ]
    }
]



