<template>
    <div class="appzx">
        <el-row :gutter="10">
            <el-col style="text-align: right">
                <el-button type="primary" size="mini" @click="OutsourcingShow=true">过程委外</el-button>
                <el-button type="primary" size="mini" @click="machFeedingShow=true">过程码上料</el-button>
                <el-button type="warning" size="mini" @click="onVerifyShow">开始防错</el-button>
            </el-col>
            <div class="content">
                <el-col :span="24">
                    <div class="perfromConLeft">
                        <div class="perfromConLeftTitle" style="position: relative;">
                            <el-col>
                                <h3 class="information">
                                    车间基本信息</h3>
                                <p class="line">
                                    &nbsp;</p>
                            </el-col>
                        </div>
                        <div class="perfromConLeftContent">
                            <el-col :span="5">
                                <el-form ref="form" label-width="120px">
                                    <el-form-item label="工厂：">
                                        {{$ls.get('factory').fcName}}
                                    </el-form-item>
                                </el-form>
                            </el-col>
                            <el-col :span="5">
                                <el-form ref="form" label-width="120px">
                                    <el-form-item label="车间：">
                                        {{$ls.get('workshop').shopName}}
                                    </el-form-item>
                                </el-form>
                            </el-col>
                            <el-col :span="5">
                                <el-form ref="form" label-width="120px">
                                    <el-form-item label="生产线：">
                                        {{$ls.get('line').lineName}}
                                    </el-form-item>
                                </el-form>
                            </el-col>
                            <el-col :span="5">
                                <el-form ref="form" label-width="120px">
                                    <el-form-item label="班次：">
                                        {{$ls.get('shift').sfName}}
                                    </el-form-item>
                                </el-form>
                            </el-col>
                            <el-col :span="4">
                                <el-form ref="form" label-width="120px">
                                    <el-form-item label="线长：">
                                        {{$ls.get('user').userName}}
                                    </el-form-item>
                                </el-form>
                            </el-col>

                        </div>
                    </div>
                </el-col>
            </div>
            <el-col :span="10">
                <p style="margin-bottom: 1.2vh;height: 4vh;color:#DAA651;margin-top: 1vh;" class="m_label">工单列表
                    <el-date-picker class="datepicker" :clearable="false" v-model="orderDateTime" type="date"
                        placeholder="选择日期" @change="queryOrder" format="yyyy 年 MM 月 dd 日" value-format="yyyy-MM-dd"
                        :picker-options="pickerOptions">
                    </el-date-picker>
                </p>
                <div class="GDtable" id="orderListBox">
                    <!-- tbative = 高亮class -->
                    <div class="GDtables" :id="data.poCode" v-for="data in orderArr" @click="changeOrder(data)"
                        v-bind:class="{ tbative: data.poCode==iftbative }">
                        <el-col :span="24" class="ative">
                            <p class="tbNative_p" style="margin-top: 1.5vh;margin-left: 2vh;">工单号：{{data.poCode}}
                                <span v-if="data.poStatus=='Executing'">执行</span>
                                <span v-else-if="data.poStatus=='Frozen'">冻结</span>
                                <span v-else-if="data.poStatus=='Completed'">完成</span>
                                <span v-else-if="data.poStatus=='Assigned'">下发</span>
                                <span v-else>{{data.poStatus}}</span>
                            </p>
                            <p class="tbline">
                                &nbsp;</p>
                        </el-col>
                        <el-col :span="24">
                            <el-col :span="9">
                                <el-form ref="form">
                                    <el-form-item label="物料号：" style="margin-top: .5vh;margin-left: 2vh;">
                                        {{data.prodCode}}
                                    </el-form-item>
                                </el-form>
                            </el-col>
                            <el-col :span="15">
                                <el-form ref="form">
                                    <el-form-item label="产品图号：" style="margin-top: .5vh;margin-left: 2vh;">
                                        {{data.prodTuhao}}
                                    </el-form-item>
                                </el-form>
                            </el-col>
                            <el-col :span="9">
                                <el-form ref="form">
                                    <!-- <el-form-item label="实际/计划：" style="margin-top: .5vh;margin-left: 2vh;">
                                        <span class=" shiji">0</span>/{{data.poCount}}
                                    </el-form-item> -->
                                    <el-form-item label="计划数量：" style="margin-top: .5vh;margin-left: 2vh;">
                                        {{data.poCount}}
                                    </el-form-item>
                                </el-form>
                            </el-col>
                            <el-col :span="15">
                                <el-form ref="form">
                                    <el-form-item label="计划开始时间：" style="margin-top: .5vh;margin-left: 2vh;">
                                        {{data.poStartDatetime}}
                                    </el-form-item>
                                </el-form>
                            </el-col>
                        </el-col>
                        <el-col :span=" 24">
                            <el-form ref="form" label-width="80px">
                                <el-form-item style="float: right;margin-right: 4vh;">
                                    <el-button v-if="data.poStatus == 'Executing'" class="tbNative_b" size="mini"
                                        @click="orderCompleteShow = true" type="info">工单完成
                                    </el-button>
                                    <el-button v-else class="tbNative_b" type="primary" size="mini"
                                        @click="startOrder(data)">工单开始</el-button>
                                </el-form-item>
                            </el-form>
                        </el-col>
                    </div>
                </div>
            </el-col>
            <el-col :span="14" class="tableBox">
                <p style="color:#DAA651;margin-top: 1vh;font-weight: bold" class="m_label">工序列表</p>
                <div class="perfromConRight">
                    <div class="QDLTbale">
                        <el-col :span="24" class="QDLtbale" style="height: 7vh;border-bottom:1px solid #6780AA">
                            <el-form label-width="30px">
                                <el-col :span="6">
                                    <el-form-item>
                                        工单号：<span style="color:#D9A63E;font-weight: bold">{{iftbative}}</span>
                                    </el-form-item>
                                </el-col>
                                <el-col :span="6">
                                    <el-form-item>
                                        零件名称：{{nowOrder.prodName}}
                                    </el-form-item>
                                </el-col>
                                <el-col :span="6">
                                    <el-form-item>
                                        总投料数：{{nowOrder.touliaoSum}}
                                    </el-form-item>
                                </el-col>
                                <el-col :span="6">
                                    <el-form-item>
                                        总产出数：{{nowOrder.xialiaoSum}}
                                    </el-form-item>
                                </el-col>
                            </el-form>
                        </el-col>
                        <el-col :span="24" class="QDLtbale" style="height: 65vh !important">
                            <el-table style="width: 100%;height: 60vh;overflow-y:scroll" :data="procList">
                                <el-table-column label="工序号" prop="procCode">
                                </el-table-column>
                                <el-table-column label="工序" prop="procName">
                                </el-table-column>
                                <el-table-column label="物料号" prop="prodInfo.prodCode">
                                </el-table-column>
                                <el-table-column label="工序线上库存" prop="prodInfo.opUpStockNum">
                                </el-table-column>
                                <el-table-column label="工序线下库存" prop="prodInfo.opSideStockNum">
                                </el-table-column>
                                <el-table-column label="不合格数量" prop="prodInfo.unqualifyNum">
                                </el-table-column>
                                <el-table-column width="190" label="操作">
                                    <template slot-scope="scope">
                                        <el-button type="primary" size="mini" @click="lookInfo(scope.row)">详情
                                        </el-button>
                                        <el-button type="warning" size="mini" @click="unqualified(scope.row)">不合格品
                                        </el-button>
                                    </template>
                                </el-table-column>
                            </el-table>
                        </el-col>
                    </div>
                </div>
            </el-col>
        </el-row>
        <!-- 工单完成 -->
        <orderComplete :show.sync="orderCompleteShow" :orderArr="orderArr" :order="nowOrder" :initCleanLogs="cleanlogs"
            @orderClosed="queryOrder"></orderComplete>
        <!-- 委外 -->
        <Outsourcing :show.sync="OutsourcingShow" :cleanlogs="OutsourcingLogs" @exitmodal="exitmodal"></Outsourcing>
        <!--防错验证弹出框-->
        <PreparationVerify :show.sync="verifyShow" :order="orderToExec" @pass="faileCallBack"></PreparationVerify>
        <!--插入工单弹出框-->
        <MachFeeding :show.sync="machFeedingShow" :ifInsert="true" :orders="orderArr" @exitmodal="exitmodal">
        </MachFeeding>
    </div>
</template>
<script>
    import Outsourcing from "@/components/Outsourcing.vue";
    import orderComplete from "@/components/order_complete.vue";
    import PreparationVerify from "@/components/preparation_verify.vue";
    import MachFeeding from "@/components/mach_feeding.vue";
    export default {
        data() {
            return {
                activeWZX: 0,
                ifwithdrawal: false,
                ifcharging: false,
                //提示语句
                smSusuccess: '',
                curtOrder: [],
                orderArr: [],
                OutsourcingShow: false,
                orderCompleteShow: false,
                orderPkgManuShow: false,
                //orderPkgAutoShow: false,
                fpToBoxClose: [],
                procList: [],
                cleanInfo: {},
                OutsourcingLogs: {},
                cleanlogs: {},
                iftbative: '',//工单选中
                nowOrder: '',
                prodCode: '',
                verifyShow: false,//防错
                orderToExec: '',
                machFeedingShow: false,//插入工单
                orderDateTime: '',//工单时间搜索
                start_time: '',
                end_time: '',
                pickerOptions: {
                    shortcuts: [{
                        text: '今天',
                        onClick(picker) {
                            picker.$emit('pick', new Date());
                        }
                    }, {
                        text: '昨天',
                        onClick(picker) {
                            const date = new Date();
                            date.setTime(date.getTime() - 3600 * 1000 * 24);
                            picker.$emit('pick', date);
                        }
                    }, {
                        text: '明天',
                        onClick(picker) {
                            const date = new Date();
                            date.setTime(date.getTime() + 3600 * 1000 * 24);
                            picker.$emit('pick', date);
                        }
                    }, {
                        text: '所有工单',
                        onClick(picker) {
                            const date = new Date();
                            date.setTime(0);
                            picker.$emit('pick', date);
                        }
                    }]
                },
            };
        },
        components: {
            Outsourcing,
            orderComplete,
            PreparationVerify,
            MachFeeding: MachFeeding,
        },
        created: function () {
            this.queryOrder();
        },
        watch: {
            '$route'() {
                if (this.$router.history.current.name == 'MachExecution') {
                    this.exitmodal()
                }
            }
        },
        methods: {
            queryOrder: function () {
                if (this.orderDateTime && this.orderDateTime != '1970-01-01') {
                    this.start_time = this.orderDateTime + ' 00:00:00'
                    this.end_time = this.orderDateTime + ' 23:59:59'
                } else {
                    this.orderDateTime = null
                    this.start_time = null
                    this.end_time = null
                }
                //查询工单
                var self = this
                this.$http.request({
                    url: '/api/order',
                    method: 'GET',
                    params: {
                        line_id: this.$ls.get('line').lineId,
                        status: JSON.stringify(['Executing', 'Assigned']),
                        start_time: this.start_time,
                        end_time: this.end_time
                    }
                }).then(function (resp) {
                    self.orderArr = resp.data.list;
                    self.ifhavaOrderTbative()
                    for (var i = 0; i < self.orderArr.length; i++) {
                        if (self.orderArr[i].poStatus == "Assigned") {
                            self.orderToExec = self.orderArr[i]
                        }
                    }
                });
            },
            ifhavaOrderTbative() {
                for (var i = 0; i < this.orderArr.length; i++) {
                    if (this.orderArr[i].poCode == this.iftbative) {
                        return
                    }
                }
                this.iftbative = ''
                this.nowOrder = ''
                this.procList = null //清空工序列表
            },
            gotolink() {
                this.$router.replace('/Processdetails')
            },
            next() {
                if (this.activeWZX++ > 2) this.activeWZX = 0;
            },
            exit() {
                this.ifwithdrawal = false;
            },
            exitcharging() {
                this.ifcharging = false;
            },
            exitmodal(nowOrder) {
                this.machFeedingShow = false;
                if (nowOrder) {
                    this.orderDateTime = '1970-01-01'
                    this.queryOrder();
                    this.iftbative = nowOrder.poCode
                    this.nowOrder = nowOrder
                    this.changeOrder(nowOrder)
                } else {
                    for (var i = 0; i < this.orderArr.length; i++) {
                        if (this.orderArr[i].poCode == this.iftbative) {
                            this.changeOrder(this.orderArr[i])
                            break
                        }
                    }
                }
            },
            lookInfo(info) {
                this.$router.push({
                    name: 'MachProcessDetail',
                    query: {
                        procCode: info.procCode,
                        procName: info.procName,
                        poCode: this.iftbative,
                        prodCode: this.prodCode
                    }
                })
            },
            //工单开始
            startOrder: function (data) {
                var self = this
                this.$http.request({
                    url: '/api/order/' + data.poCode + '/start',
                    method: 'POST',
                    params: {
                        checkBoxInfo: false
                    }
                }).then(function (resp) {
                    if (resp.status == 100) {
                        //self.jump('CvExecution')
                        self.queryOrder();
                    } else {
                        self.$notify.error({
                            title: '错误',
                            message: resp.message
                        });
                    }
                })
            },
            changeOrder(data) {
                this.nowOrder = data
                this.prodCode = data.prodCode
                this.iftbative = data.poCode//高亮
                var self = this
                this.$http.request({
                    url: '/api/process_info/' + data.prodCode,
                    method: 'GET',
                    params: {
                        line_id: this.$ls.get('line').lineId
                    }
                }).then(function (resp) {
                    if (resp.status == 100) {
                        self.procList = resp.data
                        document.getElementById(self.iftbative).scrollIntoView({ behavior: "smooth" });
                        self.queryStock(self.procList[0].procCode)//第一道工序查询上料总数
                        self.queryProcPackage(self.procList[self.procList.length - 1].procCode)//最后一道工序查询下料总数
                        self.queryProcess(data)
                        self.queryCleanlogWX()
                    } else {
                        self.$notify.error({
                            title: '错误',
                            message: resp.message
                        });
                    }
                })
            },
            //上料总数查询
            queryStock: function (procCode) {
                var self = this;
                this.$http.request({
                    url: '/api/proc_stock/' + self.nowOrder.poCode + '/' + self.nowOrder.lineId + '/' + procCode,
                    method: 'GET',
                }).then(function (resp) {
                    self.nowOrder.touliaoSum = 0
                    for (var i = 0; i < resp.data.length; i++) {
                        self.nowOrder.touliaoSum += resp.data[i].boxQuan
                    }
                })
            },
            //下料总数查询
            queryProcPackage: function (procCode) {
                var self = this
                this.$http.request({
                    url: '/api/package/boxlog',
                    method: 'GET',
                    params: {
                        line_id: this.nowOrder.lineId,
                        order_code: this.nowOrder.poCode,
                        op_code: procCode,
                    }
                }).then(function (resp) {
                    if (resp.status == 100) {
                        self.nowOrder.xialiaoSum = 0
                        for (var i = 0; i < resp.data.list.length; i++) {
                            self.nowOrder.xialiaoSum += resp.data.list[i].boxQuan
                        }
                    }
                })
            },
            queryProcess(data) {
                var self = this
                var lineId = this.$ls.get('line').lineId
                for (var i = 0; i < self.procList.length; i++) {
                    (function (index) {
                        self.$http.request({
                            url: '/api/prod_status/process/' + data.poCode + '/' + lineId + '/' + self.procList[index].procCode,
                            method: 'GET',
                        }).then(function (resp) {
                            if (resp.status == 100) {
                                self.procList[index]['prodInfo'] = resp.data
                                self.$set(self.procList, index, self.procList[index])
                            } else {
                                self.$notify.error({
                                    title: '错误',
                                    message: resp.message
                                });
                            }
                        })
                    })(i);
                }
            },
            onVerifyShow: function () {
                if (!this.orderToExec) {
                    this.$notify.error({
                        title: "错误",
                        message: "没有可执行的工单!"
                    });
                    return;
                }
                this.verifyShow = true;
            },
            //防错回调
            faileCallBack: function (value) {
                if (value) {
                    // this.isBegin = true;
                    this.queryOrder()
                }
            },
            //不合格品录入
            unqualified: function (data) {
                this.$router.push({
                    name: 'MaUnqualified_details',
                    params: {
                        poCode: this.iftbative,
                        procCode: data.procCode,
                        procName: data.procName
                    }
                })
            },
            //查询是否为做过清线
            queryCleanlogWX: function () {
                var self = this;
                self.OutsourcingLogs = ''
                //查询有无未做完的清线
                this.$http.request({
                    url: '/api/po_stack/cleanlog?order_code=' + self.nowOrder.poCode + '&is_confirmed=false',
                    method: 'GET'
                }).then(function (resp) {
                    self.cleanlogs = resp.data.list;
                    if (self.cleanlogs.length > 0) {
                        var reg = new RegExp(/WX/)
                        for (var i = 0; i < self.cleanlogs.length; i++) {
                            if (reg.test(self.cleanlogs[i].boxBarcodeNew)) {
                                self.OutsourcingLogs = self.cleanlogs[i]
                                self.OutsourcingShow = true
                                break
                            }
                        }
                    }
                })
            },
        }
    }
</script>


