<template>
  <div class="work">
    <el-row :gutter="10">
      <el-col :span="24">
        <div class="ButtonTitle">
          <el-col :span="24">
            <el-button type="primary" style="width:80px;" size="mini" @click="go">新增</el-button>
          </el-col>
        </div>
      </el-col>

      <el-col :span="24" class="whead">
        <el-col :span="6">
          <el-form ref="form" label-width="160px" style="margin-top:2vh;">
            <el-form-item label="记录编号：">
              <el-input v-model="bianhao" placeholder="请输入"></el-input>
            </el-form-item>
          </el-form>
        </el-col>

        <el-col :span="6">
          <el-form ref="form" label-width="160px" style="margin-top:2vh;">
            <el-form-item label="产品简码：">
              <el-input v-model="jianma" placeholder="请输入"></el-input>
            </el-form-item>
          </el-form>
        </el-col>

        <el-col :span="6">
          <el-form ref="form" label-width="160px" style="margin-top:2vh;">
            <el-form-item label="时间：">
              <el-select v-model="value" clearable placeholder="请选择" popper-class="select01">
                <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-form>
        </el-col>

        <el-col :span="6">
          <el-form ref="form" label-width="160px" style="margin-top:2vh;">
            <el-form-item>
              <el-button type="warning" size="mini" style="width:80px">查询</el-button>
            </el-form-item>
          </el-form>


        </el-col>
      </el-col>

      <el-col :span="24">
        <div>
            <p style="color:#DAA651;margin-top: 1vh;float:left" class="m_label">返工记录</p>
          </div>

          <el-col :span="24">
             <div class="qualified">
            <el-table  stripe style="width: 100%" height="725">
              <el-table-column  label="记录编号">
              </el-table-column>
              <el-table-column label="产品简码">
              </el-table-column>
              <el-table-column label="时间">
              </el-table-column>
              <el-table-column  label="返工人员">
              </el-table-column>
              <el-table-column  label="操作">
                <template>
                  <el-button type="primary" size="mini">详  情</el-button>
                </template>
              </el-table-column>
            </el-table>
            </div>
          </el-col>
      </el-col>

    </el-row>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        bianhao: '',
        valuetime: '',
        jianma: '',
         options: [{
          value: '选项1',
          label: '黄金糕'
        }, {
          value: '选项2',
          label: '双皮奶'
        }, {
          value: '选项3',
          label: '蚵仔煎'
        }, {
          value: '选项4',
          label: '龙须面'
        }, {
          value: '选项5',
          label: '北京烤鸭'
        }],
        value: ''

      }
    },
    mounted: function () {
    },
    methods:{
        go(){
         this.$router.push({ path:'eprework_details'  })
      }
    }
  }

</script>
<style>

</style>



