<template>
<div class="login">
    <el-col :span="24">
        <div class="logincore">
            <ul>
                <li class="listyle liColorOne">
                    <div class="mesLogo"><img src="@/assets/images/TextLoginLogo.png"></div>
                </li>

                <li class="listyle liColorTwo">
                    <h1>WELCOME</h1>
                    <div class="loginFrom1">
                        <el-col :span="22">
                            <el-form ref="form" class="loginForm">
                                <el-form-item>
                                    <i class="iconStyle el-icon-user"></i>
                                    <input type="text" placeholder="请输入用户名" class="inputuser" v-model="username" />

                                </el-form-item>
                            </el-form>
                        </el-col>

                        <el-col :span="22">
                            <el-form ref="form" class="loginForm">
                                <el-form-item>
                                    <i class="iconStyle el-icon-unlock"></i>
                                    <input type="password" placeholder="请输入密码" class="inputuser" v-model="password" />
                                </el-form-item>
                            </el-form>
                        </el-col>

                        <el-col :span="24">
                            <el-button @click="login" type="primary">登录</el-button>
                        </el-col>

                        <el-col :span="24">
                            <el-button type="primary" class="kaLoginButton" @click="jump('Cardlogin')">刷卡登录</el-button>
                        </el-col>
                    </div>
                </li>
            </ul>

        </div>
    </el-col>
</div>
</template>

<script>
const qs = require('qs');

export default {
    name: 'Textlogin',
    data() {
        return {
            password: '',
            username: '',
        }
    },
    methods: {
        login: function () {
            var self = this
            this.$http.request({
                url: '/api/user/user_login',
                method: 'POST',
                data: qs.stringify({
                    'account': this.username,
                    password: this.password
                })
            }).then(function (resp) {
                if (resp.status == 100) {
                    var userInfo = resp.data
                    localStorage.authToken = resp.data.token
                    //存储user对象
                    self.$ls.set('user', userInfo)

                    //主页跳转
                    self.jump('Home')
                } else {
                    alert("用户名或密码错误！");
                }
            })
        }
    }
}
</script>

<style>
.login .logincore {
    width: 1000px;
    margin: 17vh auto;
    height: 60vh;
}

.login .logincore ul {
    overflow: hidden;
}

.login .logincore ul .listyle {
    float: left;
    width: 500px;
    height: 60vh;
    position: relative;
}

.login .logincore ul .liColorOne {
    background: #0879D7;
    /* padding: 18vh 0; */
}

.login .logincore ul .listyle .mesLogo {
    margin: 20vh 0;
}

.login .logincore ul .liColorTwo {
    background: #fff;
}

.login .logincore ul .listyle h1 {
    color: #0879d7;
    font-weight: bold;
    margin-top: 50px;
    margin-left: 180px;
    font-size: 30px !important;
}

.login .logincore ul .listyle .mesLogo img {
    display: block;
    margin: 0 auto;
    width: 470px;
    height: 154px;
}

img {
    border-style: none;
}

ul .listyle .loginForm {

    width: 100%;
    margin: 3vh 0;
}

ul .listyle .loginFrom2 {
    padding: 0 20px;

}

ul .listyle .loginFrom1 {
    width: 100%;
    padding: 0 20px;
    margin-top: 72px;
}

.iconStyle {
    position: absolute;
    font-size: 25px !important;
    color: #0879d7;
    top: 5px;
    left: 5px;
}

.el-input__prefix {
    left: 0px;
    -webkit-transition: all .3s;
    transition: all .3s;

}

.login .el-button--primary {
    width: 94%;
    margin: 10px 0;
    height: 36px;
}

ul .listyle .kaLoginButton {
    background: none;
    color: #0879d7;
}

.login input:focus {
    outline: none !important;
    border-bottom: 1px solid #0879d7 !important;
    box-sizing: border-box;
    background-color: #e8f0fe !important;
}

.login input {
    width: 100% !important;
    display: block;
    height: 4vh !important;
    line-height: 4vh;
    border: none;
    background: none !important;
    border-bottom: 1px solid #0879d7 !important;
    -webkit-box-sizing: border-box !important;
    box-sizing: border-box !important;
    border-radius: 0 !important;
    overflow: visible;

}

input::-webkit-input-placeholder {
    color: #C0C4CC
}

.inputuser {
    text-indent: 4.5vh;
    margin-top: 2px;

}

.login input {
    color: #303133;
}
</style>



