<template>
    <div class="unqualified">
        <el-row :gutter="10">
            <el-col :span="24">
                <el-col :span="18">
                    <div class="ButtonTitle">
                        <el-button type="warning" size="mini" @click="goHold()">hold库</el-button>
                        <el-button type="success" size="mini" @click="details()">新增</el-button>
                    </div>
                </el-col>
                <div class="content">
                    <el-col :span="24">
                        <div class="perfromConLeft">
                            <el-col :span="20">
                                <el-col :span="6">
                                    <el-form label-width="100px" style="margin-top: 2vh;">
                                        <el-form-item label="审核状态：">
                                            <el-select v-model="select_type_value" placeholder="请选择"
                                                @change="queryProduct">
                                                <el-option label="全部" value=""></el-option>
                                                <el-option label="录入" value="Entering"></el-option>
                                                <el-option label="隔离" value="Isolation"></el-option>
                                                <el-option label="作废" value="Cancel"></el-option>
                                            </el-select>
                                        </el-form-item>
                                    </el-form>
                                </el-col>

                                <el-col :span="6">
                                    <el-form label-width="100px" style="margin-top: 2vh;">
                                        <el-form-item label="开始时间：">
                                            <el-date-picker v-model="startTime" type="date" format="yyyy 年 MM 月 dd 日"
                                                value-format="yyyy-MM-dd" placeholder="选择日期" @change="queryProduct()">
                                            </el-date-picker>
                                        </el-form-item>
                                    </el-form>
                                </el-col>
                                <el-col :span="6">
                                    <el-form label-width="100px" style="margin-top: 2vh;">
                                        <el-form-item label="结束时间：">
                                            <el-date-picker v-model="endTime" type="date" format="yyyy 年 MM 月 dd 日"
                                                value-format="yyyy-MM-dd" placeholder="选择日期" @change="queryProduct()">
                                            </el-date-picker>
                                        </el-form-item>
                                    </el-form>
                                </el-col>
                                <!-- <el-col :span="8">
                                        <el-form label-width="100px" style="margin-top: 2vh;">
                                            <el-form-item label="工单号：">
                                                <el-select v-model="orderCode" placeholder="请选择">
                                                    <el-option label="0" value="0">
                                                    </el-option>
                                                </el-select>
                                            </el-form-item>
                                        </el-form>
                                    </el-col> -->

                                <el-col :span="16">
                                    <el-form label-width="100px" style="margin-top: 2vh;">
                                        <el-form-item label="生产线：">
                                            <el-col :sm="8" :xl="7">
                                                <el-select v-model="factory" placeholder="请选择"
                                                    @change="changeFactory()">
                                                    <el-option v-for="(item,index) in FactoryInfo" :label="item.fcName"
                                                        :key="index" :value="item.fcId">
                                                    </el-option>
                                                </el-select>
                                            </el-col>

                                            <el-col :sm="8" :xl="7">
                                                <el-select v-model="workshop" placeholder="请选择"
                                                    @change="changeWorkshop()">
                                                    <el-option v-for="(item,index) in WorkshopInfo"
                                                        :label="item.shopName" :key="index" :value="item.shopId">
                                                    </el-option>
                                                </el-select>
                                            </el-col>

                                            <el-col :sm="8" :xl="7">
                                                <el-select v-model="Line" placeholder="请选择" @change="queryProduct()">
                                                    <el-option v-for="(item,index) in LineInfo" :label="item.lineName"
                                                        :key="index" :value="item.lineId">
                                                    </el-option>
                                                </el-select>
                                            </el-col>
                                        </el-form-item>
                                    </el-form>
                                </el-col>
                                <!-- <el-col :span="8">
                                        <el-form label-width="100px" style="margin-top: 2vh;">
                                            <el-form-item label="零件简码：">
                                                <el-select v-model="materialCode" placeholder="请选择">
                                                    <el-option label="0" value="0">
                                                    </el-option>
                                                </el-select>
                                            </el-form-item>
                                        </el-form>
                                    </el-col> -->
                            </el-col>
                            <el-col :span="4">
                                <el-form label-width="100px">
                                    <div class="button">
                                        <el-form-item>
                                            <el-button type="warning" size="mini" @click="queryProduct">
                                                查询
                                            </el-button>
                                        </el-form-item>
                                    </div>
                                </el-form>
                            </el-col>
                            <el-col :span="24">
                                <div style="float:left;margin-top: 0.5vh;margin-bottom: 0.5vh;">
                                    不合格记录
                                </div>
                                <div class="qualified">
                                    <el-table stripe :data="nonconformProductArr"
                                        style="width: 100%;height:67vh;overflow: scroll">
                                        <el-table-column prop="factorys[0].fcName" label="工厂">
                                        </el-table-column>
                                        <el-table-column prop="workshops[0].shopName" label="车间">
                                        </el-table-column>
                                        <el-table-column prop="lines[0].lineName" label="产线">
                                        </el-table-column>
                                        <el-table-column prop="shifts[0].sfName" label="班次">
                                        </el-table-column>
                                        <el-table-column prop="nplBarcode" label="不合格品编号">
                                        </el-table-column>
                                        <el-table-column prop="prodCode" label="零件简码">
                                        </el-table-column>
                                        <el-table-column prop="prodDate" label="生产日期">
                                        </el-table-column>
                                        <el-table-column prop="nplStatus" label="审核状态">
                                            <template slot-scope="scope">
                                                <span v-if="scope.row.nplStatus=='Entering'">录入</span>
                                                <span v-else-if="scope.row.nplStatus=='Isolation'"
                                                    style="color: rgb(153, 247, 47)">隔离</span>
                                                <span v-else-if="scope.row.nplStatus=='Cancel'"
                                                    style="color: rgb(243, 77, 77)">作废</span>
                                                <span v-else>{{scope.row.nplStatus}}</span>
                                            </template>
                                        </el-table-column>
                                        <el-table-column prop="createPersonName" label="录入人员">
                                        </el-table-column>
                                        <el-table-column prop="createTime" label="录入时间">
                                        </el-table-column>
                                        <el-table-column label="操作">
                                            <template slot-scope="scope">
                                                <el-button type="primary" size="mini" @click="details(scope.$index)">详情
                                                </el-button>
                                            </template>
                                        </el-table-column>
                                    </el-table>
                                </div>
                            </el-col>
                        </div>
                    </el-col>
                </div>
            </el-col>
        </el-row>
    </div>
</template>


<script>
    import moment from 'moment'
    export default {
        data() {
            return {
                orderCode: '',
                select_type_value: '',
                startTime:'',
                endTime:'',
                materialCode: '',
                nonconformProductArr: [],
                factory: '',
                workshop: '',
                Line: '',
                FactoryInfo: [],
                WorkshopInfo: [],
                LineInfo: [],
            };
        },
        mounted: function () {
            this.getSeachData()
            this.startTime = moment().format("YYYY-MM-DD")
            this.endTime = moment().format("YYYY-MM-DD")
        },
        activated: function () {
            this.queryProduct()
        },
        methods: {
            details(index) {
                var urlPath = this.$route.name + '_details'
                if (this.nonconformProductArr[index]) {
                    this.$router.push({
                        name: urlPath,
                        params: {
                            ifIsolation: this.nonconformProductArr[index].nplStatus,
                            // ifIsolation:'Cancel',
                            routerAlldatas: this.nonconformProductArr[index],
                        }
                    })
                } else {
                    this.$router.push({
                        name: urlPath,
                    })
                }
            },
            //4.1.4不合格品记录查询
            queryProduct: function () {
                var self = this
                this.$http.request({
                    url: '/api/nonconform_product/record',
                    method: 'GET',
                    showLoading: true,
                    params: {
                        fcId: self.factory,
                        shopId: self.workshop,
                        lineId: self.Line,
                        status: self.select_type_value,
                        start_time: self.startTime + ' 00:00:00',
                        end_time: self.endTime + ' 23:59:59',
                    }
                }).then(function (resp) {
                    self.nonconformProductArr = resp.data.list
                })
            },
            changeFactory: function () {
                for (var i = 0; i < this.FactoryInfo.length; i++) {
                    if (this.FactoryInfo[i].fcId == this.factory) {
                        this.WorkshopInfo = this.FactoryInfo[i].workshopVOs
                        this.workshop = ''
                        this.Line = ''
                        break
                    }
                }
            },
            changeWorkshop: function () {
                for (var i = 0; i < this.WorkshopInfo.length; i++) {
                    if (this.WorkshopInfo[i].shopId == this.workshop) {
                        this.LineInfo = this.WorkshopInfo[i].sysLinePOs
                        this.Line = ''
                        break
                    }
                }
            },

            getSeachData: function () {
                //获取产线结构
                var self = this
                this.$http.request({
                    url: '/api/department/workline/tree_novsm',
                    method: 'GET'
                }).then(function (resp) {
                    self.FactoryInfo = resp.data
                    // 工厂车间产线默认值
                    self.factory = self.$ls.get('factory').fcId
                    self.changeFactory()
                    self.workshop = self.$ls.get('workshop').shopId
                    self.changeWorkshop()
                    // self.Line = self.$ls.get('line').lineId
                    self.queryProduct()
                })
            },
            goHold:function(){
                var href = 'http://10.50.60.174:9092/#/?account=' + this.$ls.get('user').userAccount + '&isuser=0&factoryid=' + this.$ls.get('factory').fcId
                window.open(href)
            }
        }
    };

</script>

<style>
    .unqualified .el-select .el-input.is-focus .el-input__inner {
        border-color: #C0C4CC;
    }

    .unqualified .el-input__inner {
        background-color: transparent;
        color: #fff;
        height: 3.5vh;
    }

    .unqualified .el-select .el-input__inner:focus {
        border-color: #C0C4CC;
    }

    .unqualified .button .el-form-item__content {
        line-height: 15vh;
    }

    .unqualified .el-button--mini,
    .unqualified .button .el-button--mini.is-round {
        padding: 7px 25px;
    }

    .unqualified .el-table {
        border: 1px solid #5a92c7;
        background-color: #0B3E7C;
    }

    .unqualified .el-table th>.cell {
        background-color: #12599A;
        line-height: 4vh;
    }

    .unqualified .el-table--striped .el-table__body tr.el-table__row--striped td {
        background-color: #0F4C8B !important;
    }

    .unqualified .el-button--primary {
        color: #FFF;
        background-color: #2C77BC;
        border-color: #2C77BC;
    }

    .unqualified .el-table .el-button--mini {
        padding: 5px 20px;
    }

    .unqualified .ButtonTitle {
        top: 0.5vh;
    }
</style>


