<template>
    <div>
        <el-dialog title="工序下料" :close-on-click-modal="false" :visible.sync="dialogShow" width="40%"
            class="dialogtitleZX" @open="openDialog" :before-close="handleClose">
            <el-col :span="24">

                <el-col :span="24" class="dialogtitleSL" style="height:100px">
                    <!-- <el-steps :active="active" finish-status="success" simple style="margin-top: 20px">
            <el-step title="扫描箱条码"></el-step>
            <el-step title="扫描验证"></el-step>
          </el-steps> -->
                    <!-- 步骤条   s_finish为高亮 -->
                    <el-col class="stepBox">
                        <span class="stepLine s_finish"></span>
                        <div class="stepNum s_finish">
                            <p>1</p><span>扫描箱条码</span>
                        </div>
                        <span class="stepLine s_finish"></span>
                        <div v-bind:class="{ s_finish: active>0}" class="stepNum">
                            <p>2</p><span>扫描验证</span>
                        </div>
                        <span v-bind:class="{ s_finish: active>0}" class="stepLine"></span>
                    </el-col>
                </el-col>

                <el-col :span="24" style="border-bottom: 1px solid #2B6EAB;">
                    <p v-if="smSusuccess.length>0" class="p_red">{{smSusuccess}}</p>
                    <div class="Boxing" v-if="active==0">
                        <el-col :span="18">
                            <el-form ref="form" label-width="140px" @submit.native.prevent>
                                <el-form-item label="输入下料数量：">
                                    <el-input v-model="count" placeholder="请输入"></el-input>
                                </el-form-item>
                            </el-form>
                        </el-col>
                        <el-col :span="18">
                            <el-form ref="form" label-width="140px">
                                <el-form-item label="档位：">
                                    <el-select v-model="glevel" placeholder="请选择" class="select_pla">
                                        <el-option label="蓝" value="蓝"></el-option>
                                        <el-option label="绿" value="绿"></el-option>
                                        <el-option label="紫" value="紫"></el-option>
                                        <el-option label="金" value="金"></el-option>
                                        <el-option label="白" value="白"></el-option>
                                        <el-option label="无" value=""></el-option>
                                    </el-select>
                                    <el-input v-model="glevel" class="select_pla" style="margin-left: 7%">
                                    </el-input>
                                </el-form-item>
                            </el-form>
                        </el-col>
                        <!-- <el-col :span="18">
                            <el-form ref="form" label-width="140px" @submit.native.prevent>
                                <el-form-item label="炉号：">
                                    <el-input v-model="FurnaceNo" placeholder="请输入"></el-input>
                                </el-form-item>
                            </el-form>
                        </el-col> -->
                    </div>

                    <div v-if="active==2||active==1">
                        <el-col :span="18" class="Boxing1">
                            <el-form ref="form" label-width="140px" @submit.native.prevent>
                                <el-form-item label="过程码扫描：">
                                    <el-input v-model="wipCode" @keyup.enter.native="confirm" placeholder="请扫描录入">
                                    </el-input>
                                </el-form-item>
                            </el-form>
                        </el-col>

                        <el-col :span="2" style="padding-top:2vh;">
                            <el-form ref="form" label-width="40px">
                                <el-form-item>
                                    <span><img src="@/assets/images/sm.png"></span>
                                </el-form-item>
                            </el-form>
                        </el-col>
                        <el-col :span="24" style="position: relative;padding-bottom: 10px;">
                            <h3 class="tbinformation">
                                物料信息</h3>
                            <p class="line">
                                &nbsp;</p>

                        </el-col>

                        <el-col :span="24" style="height: 120px;" class="Boxing1">
                            <el-col :span="8">
                                <el-form ref="form" label-width="140px">
                                    <el-form-item label="物料名称：">
                                        {{wipInfo.prodName}}
                                    </el-form-item>
                                </el-form>
                            </el-col>
                            <el-col :span="8">
                                <el-form ref="form" label-width="140px">
                                    <el-form-item label="物料简码：">
                                        {{wipInfo.prodCode}}
                                    </el-form-item>
                                </el-form>
                            </el-col>
                            <el-col :span="8">
                                <el-form ref="form" label-width="140px">
                                    <el-form-item label="物料数量：">
                                        {{wipInfo.boxQuan}}
                                    </el-form-item>
                                </el-form>
                            </el-col>
                            <el-col :span="8">
                                <el-form ref="form" label-width="115px">
                                    <el-form-item label="档位：">
                                        {{wipInfo.prodGlevel}}
                                    </el-form-item>
                                </el-form>
                            </el-col>
                            <el-col :span="8">
                                <el-form ref="form" label-width="115px">
                                    <el-form-item label="炉号：">
                                        {{wipInfo.prodFurnaceNo}}
                                    </el-form-item>
                                </el-form>
                            </el-col>
                        </el-col>

                        <el-col :span="12" class="Boxing2">
                            <el-form ref="form" label-width="20px">
                                <el-form-item>
                                    <img src="@/assets/icon/refresh.png">
                                    <span @click="reprint">补打过程码</span>
                                </el-form-item>
                            </el-form>
                        </el-col>

                    </div>
                </el-col>

            </el-col>
            <div class="appdialog" style="text-align: center; padding: 10px;  background: #12599A;">
                <el-col :span="24" class="nextButton" v-if="active==0">
                    <el-button type="warning" @click="next" size="mini">过程码打印</el-button>
                </el-col>
                <!-- <el-col :span="24" class="nextButton" v-if="active==1">
                    <el-button type="warning" @click="confirm" size="mini">确 认</el-button>
                </el-col> -->

            </div>
        </el-dialog>
    </div>
</template>

smSusuccess

<script>
    export default {
        props: ["show", "poCode", "procCode","boxLog","matBatchNo", "lineId"],
        name: "Modal",
        data() {
            return {
                dialogShow: this.show,
                active: 0,
                scanning: '',
                count: '',
                glevel: '',
                FurnaceNo: '',
                wipCode: '',
                smSusuccess: '',
                wipInfo: {},
                nowOrder:[],
            };
        },
        watch: {
            show(newValue, oldValue) {
                this.dialogShow = this.show
            },
            dialogShow: function (newValue, oldValue) {
                if (this.show != this.dialogShow)
                    this.$emit("update:show", this.dialogShow);
            }
        },
        created: function () {
        },
        methods: {
            handleClose(done) {
                this.exitmodal(), done();
            },
            //关闭时触发
            exitmodal() {
                this.$emit("exitblanking");
            },
            next() {
                if (this.count <= 0) {
                    this.smSusuccess = '请输入数量';
                    return;
                }
                var self = this;
                var lineId= this.$ls.get('line').lineId
                if(this.lineId)
                    lineId = this.lineId
                this.$http.request({
                    url: '/api/package/proc_wip/' + self.poCode + '/'+lineId+'/' + self.procCode,
                    method: 'POST',
                    params: {
                        box_quan: this.count,
                        glevel: this.glevel,
                        furnace_no: this.FurnaceNo,
                        prod_batch_code:self.matBatchNo,
                    }
                }).then(function (resp) {
                    if (resp.status == 100) {
                        self.wipInfo = resp.data;
                        self.reprint()
                        self.active = 1;
                        self.smSusuccess=""
                        self.$emit("exitblanking");
                    }else{
                        self.smSusuccess=resp.message
                    }
                })

            },
            confirm: function () {
                var self = this;
                var scanValueTmp = this.wipCode
				if (scanValueTmp.indexOf("|") > 0) {
                    scanValueTmp = scanValueTmp.split('|')[0];
                }
                this.wipCode=''
                if (scanValueTmp != this.wipInfo.boxBarcode) {
                    self.smSusuccess="扫码错误"
                    return;
                }
                this.$http.request({
                    url: '/api/package/box_confirm/' + scanValueTmp,
                    method: 'POST',
                }).then(function (resp) {
                    if (resp.status == 100) {
                        self.$emit("exitblanking");
                        self.dialogShow = false;
                        self.smSusuccess=""
                    }else{
                        self.smSusuccess=resp.message
                    }
                })
            },
            //查询工单
			queryOrder: function (poCode) {
				var self = this
				this.$http.request({
					url: '/api/order',
					method: 'GET',
					params: {
						order_code: poCode
					}
				}).then(function (resp) {
					if (resp.status != 100) {
						return
					}
					self.nowOrder = resp.data.list[0]
				});
			},
            openDialog: function () {
                this.queryOrder(this.poCode)
                if(this.boxLog){
                    this.wipInfo=this.boxLog
                    this.active=1
                    this.reprint()
                }else{
                    this.active = 0;
                }
            },
            reprint: function () {
                this.$printer.printWipBox(this.wipInfo);
            },
        }
    };
</script>


