<template>
    <div class="details">
        <el-row :gutter="10">
            <el-col :span="24">
                <div class="planMaintenanceTitle">
                    <el-col :lg="6" :xl="4">
                        <el-breadcrumb separator=">">
                            <el-breadcrumb-item :to="{ path: '/' }"><i class="el-icon-s-home"></i>生产执行
                            </el-breadcrumb-item>
                            <el-breadcrumb-item><span href="/">工序详情</span></el-breadcrumb-item>
                        </el-breadcrumb>
                    </el-col>
                </div>
                <div class="ButtonTitle" style="position: absolute; right:0.5vh;float: right;">
                    <el-col :lg="18" :xl="20" style="width: 100%;">
                        <el-button type="primary" size="mini" @click="ifchargingt = true">上料</el-button>
                        <el-button type="primary" size="mini" @click="ifblanking = true">下料</el-button>
                        <el-button type="primary" size="mini" @click="">返回</el-button>
                    </el-col>
                </div>
            </el-col>
            <el-col :span="24">
                <div class="perfromConLeft">
                    <div class="perfromConLeftContent">
                        <el-col :span="24">
                            <div class="perfromConLeftTitle" style="position: relative;">
                                <el-col :span="24">
                                    <h3 class="information">车间基本信息</h3>
                                    <p class="line">&nbsp;</p>
                                </el-col>
                            </div>
                            <el-col :span="6">
                                <el-form ref="form" label-width="100px" style="margin-top: 1vh;">
                                    <el-form-item label="工厂：">
                                        {{$ls.get('factory').fcName}}
                                    </el-form-item>
                                </el-form>
                            </el-col>
                            <el-col :span="6">
                                <el-form ref="form" label-width="100px" style="margin-top: 1vh;">
                                    <el-form-item label="车间：">
                                        {{$ls.get('workshop').shopName}}
                                    </el-form-item>
                                </el-form>
                            </el-col>
                            <el-col :span="6">
                                <el-form ref="form" label-width="100px" style="margin-top: 1vh;">
                                    <el-form-item label="线长：">
                                        {{$ls.get('user').userName}}
                                    </el-form-item>
                                </el-form>
                            </el-col>
                            <el-col :span="6">
                                <el-form ref="form" label-width="100px" style="margin-top: 1vh;">
                                    <el-form-item label="班次：">
                                        {{$ls.get('shift').sfName}}
                                    </el-form-item>
                                </el-form>
                            </el-col>
                        </el-col>
                        <el-col :span="24">
                            <div class="perfromConLeftTitle" style="position: relative;">
                                <el-col :span="24">
                                    <h3 class="information">工单信息</h3>
                                    <p class="line">&nbsp;</p>
                                </el-col>
                            </div>
                            <el-col :span="6">
                                <el-form ref="form" label-width="100px" style="margin-top: 1vh;">
                                    <el-form-item label="工单号：">{{poCode}}</el-form-item>
                                </el-form>
                            </el-col>
                            <el-col :span="6">
                                <el-form ref="form" label-width="100px" style="margin-top: 1vh;">
                                    <el-form-item label="产品简码：">{{prodCode}}</el-form-item>
                                </el-form>
                            </el-col>
                            <el-col :span="6">
                                <el-form ref="form" label-width="100px" style="margin-top: 1vh;">
                                    <el-form-item label="状态库存：">{{procStatus.opStateStockNum}}</el-form-item>
                                </el-form>
                            </el-col>
                            <el-col :span="6">
                                <el-form ref="form" label-width="100px" style="margin-top: 1vh;">
                                    <el-form-item label="">{{}}</el-form-item>
                                </el-form>
                            </el-col>
                        </el-col>
                    </div>
                </div>
            </el-col>
            <el-col :lg="24" :xl="24" class="tableBox">
                <p style="color: rgb(253, 190, 69);margin-top: 1vh;">{{procName}}</p>
                <div class="perfromConRight">
                    <el-col :span="24" class="shebei">
                        <el-col :span="24">
                            <el-col :span="6">
                                <el-form ref="form" label-width="100px" style="margin-top: 2vh;">
                                    <el-form-item label="工序号：">
                                        {{procCode}}
                                    </el-form-item>
                                </el-form>
                            </el-col>
                            <el-col :span="6">
                                <el-form ref="form" label-width="100px" style="margin-top: 2vh;">
                                    <el-form-item label="投料数量：">
                                        {{procStatus.materialNum}}
                                    </el-form-item>
                                </el-form>
                            </el-col>
                            <el-col :span="6">
                                <el-form ref="form" label-width="100px" style="margin-top: 2vh;">
                                    <el-form-item label="产出数量：">
                                        {{procStatus.outputNum}}
                                    </el-form-item>
                                </el-form>
                            </el-col>
                            <el-col :span="6">
                                <el-form ref="form" label-width="100px" style="margin-top: 2vh;">
                                    <el-form-item label="不合格品：">
                                        <span class="xcolor">{{procStatus.unqualifyNum}}</span>
                                    </el-form-item>
                                </el-form>
                            </el-col>
                        </el-col>
                    </el-col>
                    <div class="QDLTbale">
                        <el-col :span="10" class="QDLtbale" style="padding: 2vh;">
                            <p style="margin-bottom: 1vh;margin-left:1vh;color:#F4B746;">上料记录</p>
                            <el-table style="width: 100%" height="460" :data="ProdProcessUp">
                                <el-table-column label="上料时间" prop="createTime">
                                </el-table-column>
                                <el-table-column label="物料名称" prop="matName">
                                </el-table-column>
                                <el-table-column label="原箱合格证" prop="boxBarcode">
                                </el-table-column>
                                <el-table-column label="箱合格证" prop="matHuident">
                                </el-table-column>
                                <el-table-column label="上料数量" prop="boxQuan">
                                </el-table-column>
                                <el-table-column label="剩余数量" prop="boxQuanLeft">
                                </el-table-column>
                            </el-table>
                        </el-col>
                        <el-col :span="14" class="QDLtbale" style="padding: 2vh;">
                            <p style="margin-bottom: 1vh;margin-left:1vh;color:#F4B746;">线边库存</p>
                            <el-table style="width: 100%" height="460">
                                <el-table-column label="物料简码">
                                </el-table-column>
                                <el-table-column label="物料名称">
                                </el-table-column>
                                <el-table-column label="上料数量">
                                </el-table-column>
                                <el-table-column label="剩余数量">
                                </el-table-column>
                                <el-table-column label="箱合格证号">
                                </el-table-column>
                            </el-table>
                        </el-col>
                    </div>
                </div>
            </el-col>
        </el-row>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                //route
                lineId: '',
                poCode: '',
                prodCode: '',
                procCode: '',
                procName: '',

                //APIdata
                procStatus: [],
                ProdProcessUp: [],

            }
        },
        created: function () {
            this.lineId = this.$route.query.lineId
            this.procCode = this.$route.query.procCode;
            this.procName = this.$route.query.procName;
            this.poCode = this.$route.query.poCode;
            this.prodCode = this.$route.query.prodCode;
            this.queryProcStatus()
            this.queryStock()
        },
        methods: {
            //查询加工状态
            queryProcStatus() {
                var self = this
                self.$http.request({
                    url: '/api/prod_status/process/' + self.poCode + '/' + self.lineId + '/' + self.procCode,
                    method: 'GET',
                }).then(function (resp) {
                    if (resp.status == 100) {
                        self.procStatus = resp.data
                    } else {
                        self.$notify.error({
                            title: '错误',
                            message: resp.message
                        });
                    }
                })
            },
            // 上料记录
            queryStock: function () {
                var self = this;
                this.$http.request({
                    url: '/api/proc_stock/' + self.poCode + '/' + self.lineId + '/' + self.procCode,
                    method: 'GET',
                }).then(function (resp) {
                    self.ProdProcessUp = resp.data
                })
            },
        },
    }

</script>

<style>

</style>


