<template>
    <div class="cardlogin">
        <el-col :span="24">
            <div class="logincore">
                <ul>
                    <li class="listyle liColorTwo">
                        <p class="TextP">1.请先选择登录条件</p>
                        <div class="loginFrom1">
                            <el-col :span="22" class="selectBox">
                                <el-col :span="4">
                                    <el-form>
                                        <el-form-item>
                                            <p class="pStyle">登录到</p>
                                        </el-form-item>
                                    </el-form>
                                </el-col>

                                <el-col :span="18">
                                    <el-form>
                                        <el-form-item>
                                            <el-select v-model="loginTargetSelected" placeholder="请选择"
                                                style="text-centen;" value-key="code">
                                                <el-option v-for="item in loginTarget" :key="item.code"
                                                    :label="item.name" :value="item.code"></el-option>
                                            </el-select>
                                        </el-form-item>
                                    </el-form>
                                </el-col>
                            </el-col>
                            <el-col :span="22" class="selectBox">
                                <el-col :span="4">
                                    <el-form>
                                        <el-form-item>
                                            <p class="pStyle">工厂</p>
                                        </el-form-item>
                                    </el-form>
                                </el-col>

                                <el-col :span="18">
                                    <el-form>
                                        <el-form-item>
                                            <el-select v-model="factorySelected" placeholder="请选择" style="text-centen;"
                                                value-key="fcId" @change="lineSelected={},workshopSelected={}">
                                                <el-option v-for="item in lineTree" :key="item.fcId"
                                                    :label="item.fcName" :value="item"></el-option>
                                            </el-select>
                                        </el-form-item>
                                    </el-form>
                                </el-col>
                            </el-col>

                            <el-col :span="22" class="selectBox" v-if="loginTargetSelected == 'workline'">
                                <el-col :span="4">
                                    <el-form>
                                        <el-form-item>
                                            <p class="pStyle">车间</p>
                                        </el-form-item>
                                    </el-form>
                                </el-col>

                                <el-col :span="18">
                                    <el-form>
                                        <el-form-item>
                                            <el-select v-model="workshopSelected" placeholder="请选择" style="text-centen;"
                                                value-key="shopId" @change="lineSelected={}">
                                                <el-option v-for="item in factorySelected.workshopVOs"
                                                    :key="item.shopId" :label="item.shopName" :value="item"></el-option>
                                            </el-select>
                                        </el-form-item>
                                    </el-form>
                                </el-col>
                            </el-col>

                            <el-col :span="22" class="selectBox" v-if="loginTargetSelected == 'workline'">
                                <el-col :span="4">
                                    <el-form>
                                        <el-form-item>
                                            <p class="pStyle">产线</p>
                                        </el-form-item>
                                    </el-form>
                                </el-col>

                                <el-col :span="18">
                                    <el-form>
                                        <el-form-item>
                                            <el-select v-model="lineSelected" placeholder="请选择" style="text-centen;"
                                                value-key="lineId">
                                                <el-option v-for="item in workshopSelected.sysLinePOs"
                                                    :key="item.lineId" :label="item.lineName" :value="item"></el-option>
                                            </el-select>
                                        </el-form-item>
                                    </el-form>
                                </el-col>
                            </el-col>

                            <el-col :span="22" class="selectBox" v-if="loginTargetSelected == 'workline'">
                                <el-col :span="4">
                                    <el-form>
                                        <el-form-item>
                                            <p class="pStyle">班次</p>
                                        </el-form-item>
                                    </el-form>
                                </el-col>

                                <el-col :span="18">
                                    <el-form>
                                        <el-form-item>
                                            <el-select v-model="shiftSelected" placeholder="请选择" style="text-centen;"
                                                value-key="sfId">
                                                <el-option v-for="item in shiftArr" :key="item.sfId"
                                                    :label="item.sfName" :value="item"></el-option>
                                            </el-select>
                                        </el-form-item>
                                    </el-form>
                                </el-col>
                            </el-col>

                            <el-col :span="22" class="selectBox" v-if="loginTargetSelected == 'safelunch'">
                                <el-col :span="4">
                                    <el-form>
                                        <el-form-item>
                                            <p class="pStyle">产线</p>
                                        </el-form-item>
                                    </el-form>
                                </el-col>

                                <el-col :span="18">
                                    <el-form>
                                        <el-form-item>
                                            <el-select v-model="safelunchLineSelected" placeholder="请选择"
                                                style="text-centen;" value-key="saflineId">
                                                <el-option v-for="item in safelunchLineArr" :key="item.saflineId"
                                                    :label="item.saflineName" :value="item"></el-option>
                                            </el-select>
                                        </el-form-item>
                                    </el-form>
                                </el-col>
                            </el-col>
                        </div>
                    </li>
                    <li class="listyle liColorThree">
                        <input type="search" v-model="cardId" autofocus="autofocus" class="card-input"
                            v-on:keyup.enter="onCardLogin" />
                        <p class="TExtPTwo">2.请刷卡登录</p>
                        <div class="mesLogo02">
                            <img src="@/assets/images/card.png" />
                        </div>
                    </li>
                </ul>
            </div>
        </el-col>
    </div>
</template>
<script>
    const qs = require('qs');

    export default {
        name: "Cardlogin",
        data() {
            return {
                loginTarget: [
                    { name: '生产线', code: 'workline' },
                    { name: 'SafeLunch', code: 'safelunch' }
                ],
                loginTargetSelected: 'workline',
                lineTree: [],
                shiftArr: [],
                factorySelected: {},
                workshopSelected: {},
                lineSelected: {},
                shiftSelected: {},
                safelunchLineArr: [],
                safelunchLineSelected: {},
                cardId: "",
            };
        },
        watch: {
            factorySelected: function (val, oldVal) {
                // 查询班次
                var self = this
                this.$http.request({
                    url: '/api/work_shift',
                    method: 'GET',
                    params: {
                        fcId: this.factorySelected.fcId
                    }
                }).then(function (resp) {
                    self.shiftArr = resp.data
                })

                // 查询SafeLunch产线
                var self = this
                this.$http.request({
                    url: '/api/safelunch/workline',
                    method: 'GET',
                    showLoading: true,
                    params: {
                        fcId: this.factorySelected.fcId
                    }
                }).then(function (resp) {
                    self.safelunchLineArr = resp.data.list
                })
            }
        },
        created: function () {
            //获取产线结构
            var self = this
            this.$http.request({
                url: '/api/department/workline/tree_novsm',
                method: 'GET',
                showLoading: true,
            }).then(function (resp) {
                if(resp.status!=100){
                    self.$message.error(resp.message)
                    return
                }
                self.lineTree = resp.data
                if (self.$ls.get('prodModel')) {
                    if (self.$ls.get('loginType') == 'safelunch')
                        self.loginTargetSelected = 'safelunch' // safelunch
                    else
                        self.loginTargetSelected = 'workline'  //生产线
                    self.factorySelected = self.$ls.get('factory')
                    self.lineSelected = self.$ls.get('line')
                    self.shiftSelected = ''
                    self.factorySelected = self.$ls.get('factory')
                    self.workshopSelected = self.$ls.get('workshop')
                    self.safelunchLineSelected = self.$ls.get('safeluncLine')
                }
            })
        },
        methods: {
            onCardLogin: function () {
                var cardIdTmp = this.cardId
                this.cardId = ""

                //检查产线选择
                if (this.loginTargetSelected == 'workline') {
                    if (this.factorySelected.fcId == undefined || this.workshopSelected.shopId == undefined
                        || this.lineSelected.lineId == undefined || this.shiftSelected.sfId == undefined) {
                        this.$notify.error({
                            title: '错误',
                            message: '请选择产线和班次'
                        });
                        return
                    }
                }
                if (this.loginTargetSelected == 'safelunch') {
                    if (this.factorySelected.fcId == undefined || this.safelunchLineSelected.saflineId == undefined) {
                        this.$notify.error({
                            title: '错误',
                            message: '请选择SafeLunch产线'
                        });
                        return
                    }
                }

                //登录验证
                var self = this
                this.$http.request({
                    url: '/api/user/card_login',
                    method: 'POST',
                    data: qs.stringify({ 'cardId': cardIdTmp })
                }).then(function (resp) {
                    if (resp.status == 100) {
                        var userInfo = resp.data
                        localStorage.authToken = resp.data.token
                        console.log(localStorage.authToken)
                        //存储user对象
                        self.$ls.set('user', userInfo)

                        //主页跳转
                        self.judgeHome()
                    }
                    else {
                        self.$notify.error({
                            title: '错误',
                            message: '卡号不存在'
                        });
                    }
                })
            },
            judgeHome: function () {
                var self = this
                if (this.loginTargetSelected == 'safelunch') {
                    self.$ls.set('loginType', 'safelunch')
                    self.$ls.set('factory', self.factorySelected)
                    self.$ls.set('safeluncLine', self.safelunchLineSelected)
                    self.jump('SafelunchIndex')
                    return
                }
                //判断产线生产模式
                this.$http.request({
                    url: '/api/department/workline/prodmodel/' + this.lineSelected.lineId,
                    method: 'GET',
                }).then(function (resp) {
                    var model = resp.data
                    //存储产线数据
                    self.$ls.set('loginType', 'workLine')
                    self.$ls.set('factory', self.factorySelected)
                    self.$ls.set('workshop', self.workshopSelected)
                    self.$ls.set('line', self.lineSelected)
                    self.$ls.set('shift', self.shiftSelected)
                    self.$ls.set('prodModel', model.prodModel)
                    self.$ls.set('opNo', model.opNo)
                    if (model.prodModel == 'cv_assy') {
                        self.jump('CvIndex')
                    }
                    else if (model.prodModel == 'sec_assy') {
                        self.jump('SectIndex')
                    }
                    else if (model.prodModel == 'machining') {
                        self.jump('MachIndex')
                    }
                    else if (model.prodModel == 'public_proc') {
                        self.jump('ProcIndex')
                    }
                    else if (model.prodModel == 'ep_assy') {
                        self.jump('epIndex')
                    }
                })
            }
        }
    };
</script>


<style>
    .cardlogin .logincore {
        width: 1000px;
        margin: 17vh auto;
        height: 60vh;
    }

    .cardlogin .logincore ul {
        overflow: hidden;
    }

    .cardlogin .logincore ul .listyle {
        float: left;
        width: 500px;
        height: 60vh;
        position: relative;
    }

    .cardlogin .logincore ul .liColorOne {
        background: #0879d7;
        /* padding: 18vh 0; */
    }

    .cardlogin .logincore ul .listyle .mesLogo {
        margin: 20vh 0;
    }

    .cardlogin .logincore ul .liColorTwo {
        background: #fff;
    }

    .cardlogin .logincore ul .listyle h1 {
        color: #0879d7;
        font-weight: bold;
        margin-top: 50px;
        margin-left: 180px;
        font-size: 30px;
    }

    .cardlogin .logincore ul .listyle .mesLogo img {
        display: block;
        margin: 0 auto;
        width: 470px;
        height: 154px;
    }

    img {
        border-style: none;
    }

    ul .listyle .loginForm {
        width: 100%;
        margin: 3vh 0;
    }

    ul .listyle .loginFrom2 {
        padding: 0 20px;
    }

    ul .listyle .loginFrom1 {
        width: 100%;
        padding: 0 20px;
        margin-top: 12px;
    }

    .iconStyle {
        position: absolute;
        font-size: 25px;
        color: #0879d7;
    }

    .el-input__prefix {
        left: 0px;
        -webkit-transition: all 0.3s;
        transition: all 0.3s;
    }

    .cardlogin .el-button--primary {
        width: 94%;
        margin: 10px 0;
        height: 36px;
    }

    ul .listyle .kaLoginButton {
        background: none;
        color: #0879d7;
    }

    .cardlogin ul .liColorThree {
        background: #0879d7;
    }

    .cardlogin ul .listyle .TExtPTwo {
        color: #fff;
        font-size: 20px !important;
        font-weight: bold;
        margin-top: 50px;
        text-align: center;
    }

    .cardlogin ul .listyle .mesLogo02 {
        margin: 2vh 0;
    }

    .cardlogin ul .listyle .mesLogo02 img {
        display: block;
        margin: 0 auto;
        width: 289px;
        height: 400px;
    }

    .cardlogin ul .listyle .TextP {
        color: #0879d7;
        font-size: 20px !important;
        font-weight: bold;
        margin-top: 20px;
        text-align: center;
    }

    .cardlogin .selectBox {
        padding: 5px 0;
        border-bottom: 1px solid #bdbdbd;
        -webkit-box-sizing: border-box;
        box-sizing: border-box;
        margin: 10px 0;
    }

    .cardlogin .selectBox .pStyle {
        border-right: 1px solid #bdbdbd;
        -webkit-box-sizing: border-box;
        box-sizing: border-box;
    }

    .cardlogin .el-input__inner {
        border: none;
        background: none;
        color: #0879d7;
        border-radius: 0 !important;
    }

    .cardlogin .el-select .el-input.is-focus .el-input__inner {
        border-color: transparent;
    }

    .cardlogin .el-select .el-input__inner:focus {
        border-color: transparent;
    }

    .cardlogin .el-select .el-input__inner:focus {
        border-color: transparent;
    }

    .cardlogin .el-form-item {
        margin-bottom: 0px;
    }

    .cardlogin .el-select {
        padding-left: 35%;
        margin: 0;
    }

    .cardlogin .el-option {
        width: 20px;
    }

    .cardlogin .el-select-dropdown__item.hover,
    .el-select-dropdown__item:hover {
        background-color: #4297f1;
    }

    .cardlogin .el-select-dropdown__list {
        text-align: center;
    }

    .el-select-dropdown__item {
        font-size: 14px;
        position: relative;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        color: #606266;
        height: 34px;
        line-height: 34px;
        -webkit-box-sizing: border-box;
        box-sizing: border-box;
        cursor: pointer;
    }

    .el-select-selected-value {
        display: block;
        height: 30px;
        line-height: 30px;
        font-size: 12px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        padding-left: 8px;
        padding-right: 24px;
    }

    .card-input {
        width: 100%;
        height: 100%;
        z-index: 99;
        position: absolute;
        top: 0;
        left: 0;
        background: none;
        color: #fff;
    }
</style>


