<template>
    <div class="badproducts">
        <el-row :gutter="10">
            <el-col :span="24">
                <div class="planMaintenanceTitle">
                    <el-col :lg="6" :xl="4">
                        <el-breadcrumb separator=">">
                            <el-breadcrumb-item>
                                <span @click="jump('back')">
                                    <i class="el-icon-s-home"></i>不合格品
                                </span>
                            </el-breadcrumb-item>
                            <el-breadcrumb-item v-if="tabsNum=='message'">
                                <span>新增</span>
                            </el-breadcrumb-item>
                            <el-breadcrumb-item v-if="tabsNum=='loss'">
                                <span>详情</span>
                            </el-breadcrumb-item>
                        </el-breadcrumb>
                    </el-col>
                </div>
                <el-col :span="18">
                    <div class="ButtonTitle">
                        <el-button v-if="ifIsolation!='Isolation' && ifIsolation!='Cancel'" type="success" size="mini"
                            @click="saveDatas()">保存
                        </el-button>
                        <el-button v-if="ifIsolation!='Isolation' && ifIsolation!='Cancel' && ifIsolation!='add'"
                            type="warning" size="mini" @click="ifmonitorConfirm(true)">
                            班长确认</el-button>
                        <el-button v-if="ifIsolation!='Isolation' && ifIsolation!='Cancel' && ifIsolation!='add'"
                            type="warning" size="mini" @click="ifmonitorConfirm(false)">
                            作废</el-button>
                        <el-button v-if="ifIsolation=='Isolation'" type="warning" size="mini" @click="Print">打印
                        </el-button>
                    </div>
                </el-col>
                <el-col :span="24" class="badactive">
                    <el-tabs v-model="tabsNum">
                        <el-tab-pane label="基本信息" name="message">
                            <el-col :span="24">
                                <div style="position: relative;">
                                    <el-col :span="24">
                                        <h3 class="information">1.&nbsp;开班信息</h3>
                                        <p class="line">&nbsp;</p>
                                    </el-col>
                                    <el-col :span="12" class="inputsc">
                                        <el-form :label-position="labelPosition" label-width="80px">
                                            <el-form-item label="生产线:">
                                                <el-col :sm="7" :xl="8">
                                                    <el-input readonly :value="$ls.get('factory').fcName"></el-input>
                                                </el-col>
                                                <el-col :sm="7" :xl="8">
                                                    <el-input readonly :value="$ls.get('workshop').shopName"></el-input>
                                                </el-col>
                                                <el-col :sm="7" :xl="8">
                                                    <el-input readonly :value="$ls.get('line').lineName"></el-input>
                                                </el-col>
                                            </el-form-item>
                                        </el-form>
                                    </el-col>
                                    <el-col :span="6" class="inputtext">
                                        <el-form :label-position="labelPosition" label-width="80px">
                                            <el-form-item label="班次:">
                                                <el-input readonly :value="$ls.get('shift').sfName"></el-input>
                                            </el-form-item>
                                        </el-form>
                                    </el-col>
                                    <el-col :span="6" class="inputtext">
                                        <el-form :label-position="labelPosition" label-width="80px">
                                            <el-form-item label="生产日期:">
                                                <el-date-picker v-model="mainDatas.prodDate" format="yyyy 年 MM 月 dd 日"
                                                    readonly value-format="yyyy-MM-dd" placeholder="选择日期">
                                                </el-date-picker>
                                            </el-form-item>
                                        </el-form>
                                    </el-col>
                                </div>
                            </el-col>
                            <el-form :label-position="labelPosition" label-width="100px" class="form01" ref="mainDatas"
                                :model="mainDatas" :rules="rules" @submit.native.prevent>
                                <el-col :span="24">
                                    <div style="position: relative;margin-top:0.5vh;">
                                        <el-col :span="24">
                                            <h3 class="information">2.&nbsp;工单信息</h3>
                                            <p class="line">&nbsp;</p>
                                        </el-col>
                                    </div>
                                    <el-col :span="8">
                                        <el-form-item label="工单号:" prop="poCode">
                                            <el-select v-if="ifIsolation=='add'" v-model="mainDatas.poCode"
                                                placeholder="请选择" @change="OrderChange($event)">
                                                <el-option v-for="(item,index) in orderArr" :label="item.poCode"
                                                    :key="index" :value="item.poCode">
                                                </el-option>
                                            </el-select>
                                            <el-input v-else readonly v-model="mainDatas.poCode"></el-input>
                                        </el-form-item>
                                    </el-col>
                                    <el-col :span="8">
                                        <el-form-item label="零件简码:">
                                            <el-input readonly v-model="mainDatas.prodCode"></el-input>
                                        </el-form-item>
                                    </el-col>
                                    <el-col :span="8">
                                        <el-form-item label="零件图号:">
                                            <el-input readonly v-model="mainDatas.prodTuhao"></el-input>
                                        </el-form-item>
                                    </el-col>
                                    <el-col :span="8">
                                        <el-form-item label="不合格品编号:">
                                            <el-input readonly v-model="mainDatas.nplBarcode"></el-input>
                                        </el-form-item>
                                    </el-col>
                                    <el-col :span="8">
                                        <el-form-item label="SAP物料号:">
                                            <el-input readonly v-model="mainDatas.prodCode"></el-input>
                                        </el-form-item>
                                    </el-col>
                                    <el-col :span="8">
                                        <el-form-item label="零件名称:">
                                            <el-input readonly v-model="mainDatas.prodName"></el-input>
                                        </el-form-item>
                                    </el-col>
                                </el-col>
                                <el-col>
                                    <div style="position: relative;margin-top:0.5vh;">
                                        <el-col :span="24">
                                            <h3 class="information">2.&nbsp;质量信息</h3>
                                            <p class="line">&nbsp;</p>
                                        </el-col>
                                    </div>
                                    <el-col :span="8">
                                        <el-form-item label="产生工序:" prop="nplProdOp">
                                            <el-select v-if="ifIsolation!='Isolation' && ifIsolation!='Cancel'"
                                                v-model="mainDatas.nplProdOp" placeholder="请选择"
                                                @change="getProcessName($event,false)">
                                                <el-option v-for="(item,index) in ProcessArr" :label="item.procName"
                                                    :key="index" :value="item.procCode">
                                                </el-option>
                                            </el-select>
                                            <el-select v-else disabled v-model="mainDatas.nplProdOp">
                                                <el-option v-for="(item,index) in ProcessArr" :label="item.procName"
                                                    :key="index" :value="item.procCode">
                                                </el-option>
                                            </el-select>
                                        </el-form-item>
                                    </el-col>
                                    <el-col :span="8">
                                        <el-form-item label="发现工序:" prop="nplFindOp">
                                            <el-select v-if="ifIsolation!='Isolation' && ifIsolation!='Cancel'"
                                                v-model="mainDatas.nplFindOp" placeholder="请选择"
                                                @change="getProcessName($event,true)">
                                                <el-option v-for="(item,index) in ProcessArr" :label="item.procName"
                                                    :key="index" :value="item.procCode">
                                                </el-option>
                                            </el-select>
                                            <el-select v-else disabled v-model="mainDatas.nplFindOp">
                                                <el-option v-for="(item,index) in ProcessArr" :label="item.procName"
                                                    :key="index" :value="item.procCode">
                                                </el-option>
                                            </el-select>
                                        </el-form-item>
                                    </el-col>
                                    <el-col :span="8">
                                        <el-form-item label="缺陷模式:" prop="nplDefect">
                                            <el-select v-if="ifIsolation!='Isolation' && ifIsolation!='Cancel'"
                                                v-model="mainDatas.nplDefect" placeholder="请选择"
                                                @change="getNplDefectDesc($event)">
                                                <el-option v-for="(item,index) in TroubleArr"
                                                    :label="item.trcTroubleName" :key="index"
                                                    :value="item.trcTroubleCode">
                                                </el-option>
                                            </el-select>
                                            <el-select v-else disabled v-model="mainDatas.nplDefect" placeholder="请选择"
                                                @change="getNplDefectDesc($event)">
                                                <el-option v-for="(item,index) in TroubleArr"
                                                    :label="item.trcTroubleName" :key="index"
                                                    :value="item.trcTroubleCode">
                                                </el-option>
                                            </el-select>
                                        </el-form-item>
                                    </el-col>
                                    <el-col :span="8">
                                        <el-form-item label="是否调试件:" prop="nplIsTestPart">
                                            <el-select v-if="ifIsolation!='Isolation' && ifIsolation!='Cancel'"
                                                v-model="mainDatas.nplIsTestPart" placeholder="请选择">
                                                <el-option label="是" :value="true"></el-option>
                                                <el-option label="否" :value="false"></el-option>
                                            </el-select>
                                            <el-select v-else disabled v-model="mainDatas.nplIsTestPart"
                                                placeholder="请选择">
                                                <el-option label="是" :value="true"></el-option>
                                                <el-option label="否" :value="false"></el-option>
                                            </el-select>
                                        </el-form-item>
                                    </el-col>
                                    <!-- <el-col :span="8">
                                        <el-form-item label="责任类型:">
                                            <el-select v-if="ifIsolation!='Isolation' && ifIsolation!='Cancel'"
                                                v-model="mainDatas.nplDutyType" placeholder="请选择">
                                                <el-option v-for="(item,index) in DutyTypeArr" :label="item.ddName"
                                                    :key="index" :value="item.ddCode">
                                                </el-option>
                                            </el-select>
                                            <el-select v-else disabled v-model="mainDatas.nplDutyType">
                                                <el-option v-for="(item,index) in DutyTypeArr" :label="item.ddName"
                                                    :key="index" :value="item.ddCode">
                                                </el-option>
                                            </el-select>
                                        </el-form-item>
                                    </el-col> -->
                                    <el-col :span="8">
                                        <el-form-item label="批产状态:" prop="nplBatchStatus">
                                            <el-select v-if="ifIsolation!='Isolation' && ifIsolation!='Cancel'"
                                                v-model="mainDatas.nplBatchStatus" placeholder="请选择">
                                                <el-option v-for="(item,index) in BatchStatusArr" :label="item.ddName"
                                                    :key="index" :value="item.ddCode">
                                                </el-option>
                                            </el-select>
                                            <el-select v-else disabled v-model="mainDatas.nplBatchStatus"
                                                placeholder="请选择">
                                                <el-option v-for="(item,index) in BatchStatusArr" :label="item.ddName"
                                                    :key="index" :value="item.ddCode">
                                                </el-option>
                                            </el-select>
                                        </el-form-item>
                                    </el-col>
                                    <el-col :span="8">
                                        <el-form-item label="缺陷数量:" prop="nplQty">
                                            <el-input v-if="ifIsolation!='Isolation' && ifIsolation!='Cancel'"
                                                v-model="mainDatas.nplQty" placeholder="请输入">
                                            </el-input>
                                            <el-input v-else readonly v-model="mainDatas.nplQty"></el-input>
                                        </el-form-item>
                                    </el-col>
                                    <el-col :span="8">
                                        <!-- <el-form-item label="设备编号:" prop="nplDeviceCode"> -->
                                        <el-form-item label="设备编号:">
                                            <el-input v-if="ifIsolation!='Isolation' && ifIsolation!='Cancel'"
                                                v-model="mainDatas.nplDeviceCode" placeholder="请输入">
                                            </el-input>
                                            <el-input v-else readonly v-model="mainDatas.nplDeviceCode">
                                            </el-input>
                                        </el-form-item>
                                    </el-col>
                                    <el-col :span="8">
                                        <el-form-item label="缺陷模式采集:">
                                            <el-input v-if="ifIsolation!='Isolation' && ifIsolation!='Cancel'"
                                                v-model="mainDatas.defectCollection" placeholder="请扫描"
                                                @keyup.enter.native="getDefectCollection()">
                                            </el-input>
                                            <el-input v-else readonly v-model="mainDatas.defectCollection">
                                            </el-input>
                                        </el-form-item>
                                    </el-col>
                                    <el-col :span="8" class="inputtext">
                                        <el-form-item label="输入者" class="textuser" prop="createPersonName">
                                            <el-input ref="inputer"
                                                v-if="ifIsolation!='Isolation' && ifIsolation!='Cancel'"
                                                @keyup.enter.native="onCardScan" @focus="inputFocus"
                                                v-model="mainDatas.createPersonName" placeholder="请输入"></el-input>
                                            <el-input v-else readonly v-model="mainDatas.createPersonName"
                                                placeholder="请输入"></el-input>
                                        </el-form-item>
                                    </el-col>
                                    <el-col :span="8">
                                        <el-form-item label="缺陷备注:">
                                            <el-input v-if="ifIsolation!='Isolation' && ifIsolation!='Cancel'"
                                                v-model="mainDatas.nplNote" class="textarea_box" type="textarea"
                                                :autosize="{ minRows: 5, maxRows: 8}" placeholder="请输入内容">
                                            </el-input>
                                            <el-input v-else readonly v-model="mainDatas.nplNote" class="textarea_box"
                                                type="textarea" :autosize="{ minRows: 5, maxRows: 8}"
                                                placeholder="请输入内容">
                                            </el-input>
                                        </el-form-item>
                                    </el-col>
                                    <el-col :span="8">
                                        <p class="shuoming">
                                            说明:缺陷模式采集卡格式【发现工序-缺陷代码-产生工序号】
                                        </p>
                                    </el-col>
                                </el-col>
                            </el-form>
                        </el-tab-pane>
                        <el-tab-pane label="零件损耗" name="loss">
                            <el-col :span="24">
                                <div>
                                    <el-table class="materialPOs" style="width: 100%;height:74vh;overflow:scroll"
                                        :data="mainDatas.nonconformProductMaterialPOs"
                                        @selection-change="handleSelectionChange">
                                        <el-table-column type="selection" width="55">
                                        </el-table-column>
                                        <el-table-column prop="materialName" label="零件名称">
                                        </el-table-column>
                                        <el-table-column prop="materialCode" label="零件简码">
                                        </el-table-column>
                                        <el-table-column prop="nplmBarcode" label="不合格品编号">
                                        </el-table-column>
                                        <el-table-column prop="materialBoxCode" label="来料箱条码">
                                        </el-table-column>
                                        <el-table-column prop="materialBatchCode" label="生产批号">
                                        </el-table-column>
                                        <el-table-column prop="materialFurnaceNo" label="炉号">
                                        </el-table-column>
                                        <el-table-column prop="materialQty" label="消耗数量">
                                        </el-table-column>
                                        <el-table-column width="200" label="操作">
                                            <template slot-scope="scope">
                                                <el-button disabled v-if="ifIsolation=='Isolation'" type="primary"
                                                    size="mini">
                                                    已隔离
                                                </el-button>
                                                <el-button disabled v-if="ifIsolation=='Cancel'" type="primary"
                                                    size="mini">
                                                    已作废
                                                </el-button>
                                                <el-button v-if="ifIsolation!='Isolation' && ifIsolation!='Cancel'"
                                                    type="primary" size="mini"
                                                    @click="MaterialEditBoxShow=true,editBox(scope.$index)">
                                                    编辑
                                                </el-button>
                                                <el-button v-if="ifIsolation!='Isolation' && ifIsolation!='Cancel'"
                                                    type="danger" size="mini" @click="Deletematerials(scope.$index)">
                                                    删除
                                                </el-button>
                                            </template>
                                        </el-table-column>
                                    </el-table>
                                </div>
                            </el-col>
                            <el-col style="margin-top:.5vh;text-align:right">
                                <el-button disabled v-if="ifIsolation=='Isolation'" type="danger" size="small">
                                    已隔离
                                </el-button>
                                <el-button disabled v-if="ifIsolation=='Cancel'" type="danger" size="small">
                                    已作废
                                </el-button>
                                <el-button v-if="ifIsolation!='Isolation' && ifIsolation!='Cancel'" type="danger"
                                    size="small" @click="DeleteMore">
                                    批量删除
                                </el-button>
                                <el-button style="float: left" v-if="ifIsolation!='Isolation' && ifIsolation!='Cancel'"
                                    type="warning" size="small" @click="queryMaterial">
                                    零件重新计算
                                </el-button>
                            </el-col>
                        </el-tab-pane>
                    </el-tabs>
                </el-col>
            </el-col>
            <!--班长确认-->
            <inputCard :show.sync="ifcardShow" @pass="cardCallBack"></inputCard>
            <inputCard :show.sync="ifcardShow2" @pass="cardCallBack2"></inputCard>
            <el-dialog title="编辑" :visible.sync="MaterialEditBoxShow" width="30%">
                <el-form :label-position="labelPosition1" label-width="120px">
                    <el-form-item label="零件名称:">
                        <el-input disabled v-model="edit_materialName"></el-input>
                    </el-form-item>
                    <el-form-item label="不合格品编号:">
                        <el-input disabled v-model="edit_nplmBarcode"></el-input>
                    </el-form-item>
                    <el-form-item label="来料箱条码:">
                        <el-input disabled v-model="edit_materialBoxCode"></el-input>
                    </el-form-item>
                    <el-form-item label="生产批号:">
                        <el-input disabled v-model="edit_materialBatchCode"></el-input>
                    </el-form-item>
                    <el-form-item label="炉号:">
                        <el-input disabled v-model="edit_materialFurnaceNo"></el-input>
                    </el-form-item>
                    <el-form-item label="零件简码">
                        <el-input disabled v-model="edit_materialCode"></el-input>
                    </el-form-item>
                    <el-form-item label="消耗数量:">
                        <el-input v-model="edit_materialQty"></el-input>
                    </el-form-item>
                </el-form>
                <span slot="footer" class="dialog-footer">
                    <el-button type="warning" size="small" @click="MaterialEditBoxShow = false,editboxConfirm()">确 定
                    </el-button>
                    <el-button size="small" @click="MaterialEditBoxShow = false">取 消</el-button>
                </span>
            </el-dialog>
        </el-row>
    </div>
</template>
<script>
    import inputCard from "@/components/card_input.vue";
    import moment from 'moment'
    export default {
        components: {
            inputCard: inputCard,
        },
        data() {
            return {
                // 表单验证
                rules: {
                    poCode: [
                        { required: true, message: '请选择工单号', trigger: 'change' }
                    ],
                    nplFindOp: [
                        { required: true, message: '请选择发现工序', trigger: 'change' }
                    ],
                    nplProdOp: [
                        { required: true, message: '请选择产生工序', trigger: 'change' }
                    ],
                    nplQty: [
                        { required: true, message: '请输入缺陷数量', trigger: 'change' }
                    ],
                    nplIsTestPart: [
                        { required: true, message: '请选择调试件', trigger: 'change' }
                    ],
                    nplBatchStatus: [
                        { required: true, message: '请选择批产状态', trigger: 'change' }
                    ],
                    nplDefect: [
                        { required: true, message: '请选择缺陷模式', trigger: 'change' }
                    ],
                    createPersonName: [
                        { required: true, message: '请刷卡或手动输入', trigger: 'change' }
                    ]
                },
                tabsNum: 'message',//tabs
                MaterialEditBoxShow: false,//修改box
                tableIndex: '', // tableINdex
                ifcardShow: false,//确认刷卡
                ifcardShow2: false,//作废刷卡
                labelPosition: 'top', //css
                labelPosition1: 'right', //css
                nowLineId: this.$ls.get('line').lineId,//当前lineId
                //route
                ifIsolation: true,

                mainDatas: {
                    nonconformProductMaterialPOs: [],//零件
                    fcId: this.$ls.get('factory').fcId, // 工厂id
                    fcCode: this.$ls.get('factory').fcCode, // 工厂编码
                    shopId: this.$ls.get('workshop').shopId, // 车间ID
                    shopCode: this.$ls.get('workshop').shopName, // 车间编码
                    lineId: this.$ls.get('line').lineId, // 产线ID
                    lineCode: this.$ls.get('line').lineName, // 产线编码	
                    shiftId: this.$ls.get('shift').sfId, // 班次ID
                    shiftCode: this.$ls.get('shift').sfName, // 班次编码
                    prodDate: '',//生产日期
                    createTime: '',//发生时间
                    poCode: '',//工单号
                    prodOrderCode: '',//工单号
                    prodCode: '',//sap物料号
                    prodNumber: '',//零件简码
                    prodTuhao: '',//零件图号
                    prodName: '',//零件名称
                    nplBarcode: '',//不合格品编号
                    ////
                    nplId: '',//id
                    nplProdOp: '',//产生工序
                    nplProdOpName: '',//产生工序
                    nplFindOp: '',//发现工序
                    nplFindOpName: '',//发现工序
                    ////
                    nplQty: '',//缺陷数量
                    nplIsTestPart: '',//是否调试件
                    nplDutyType: '',//责任类型
                    nplBatchStatus: '', // 批产状态
                    nplDefect: '',//缺陷模式
                    nplDefectDesc: '',//缺陷模式
                    nplDeviceCode: '',//设备编号
                    createPersonName: '',//输入者
                    createPersonNo: '',//输入者id     null
                    ////
                    defectCollection: '',//缺陷模式采集卡
                    // nplStatus 录入状态
                    inputCardInfo: []
                },
                //接口
                orderArr: [],//工单
                ProcessArr: [],//工序
                DutyTypeArr: [],//责任类型
                BatchStatusArr: [],//批次状态
                TroubleArr: [],//缺陷模式
                //编辑box
                edit_nplmBarcode: '',
                edit_materialName: '',
                edit_materialBoxCode: '',
                edit_materialBatchCode: '',
                edit_materialFurnaceNo: '',
                edit_materialCode: '',
                edit_materialQty: '',

                tableDeleteMore: [], //零件损耗批量删除
            }
        },
        mounted() {
            this.queryTprDutyType()
            this.queryTprBatchStatus()
            this.queryTrouble()
            if (this.$route.params.ifIsolation) {
                //未确认 || 隔离 || 作废
                this.ifIsolation = this.$route.params.ifIsolation
                this.mainDatas = this.$route.params.routerAlldatas
                this.queryProcess()
            } else {
                //新增
                this.ifIsolation = 'add'
                this.mainDatas.prodDate = moment().format("YYYY-MM-DD");
                this.mainDatas.createTime = moment().format("YYYY-MM-DD");
                this.mainDatas.createPersonName = this.$ls.get('user').userName
                if (this.$route.params.poCode) {
                    this.mainDatas.poCode = this.$route.params.poCode
                    this.mainDatas.nplFindOp = this.$route.params.procCode
                    this.mainDatas.nplFindOpName = this.$route.params.procName
                    if (this.$route.params.order) {
                        this.nowLineId = this.$route.params.lineId
                        this.mainDatas.lineId = this.$route.params.order.lineId, // 产线ID
                            this.mainDatas.lineCode = '' // 产线编码	
                    }
                    this.queryOrder(this.$route.params.poCode)
                    return
                }
                this.queryOrder()
            }
        },

        methods: {
            //新增- 工单
            queryOrder: function (poCode) {
                var self = this
                this.$http.request({
                    url: '/api/order',
                    method: 'GET',
                    params: {
                        order_code: poCode,
                        line_id: this.nowLineId,
                        status: JSON.stringify(['Executing'])
                    }
                }).then(function (resp) {
                    if (resp) {
                        self.orderArr = resp.data.list;
                        if (self.ifIsolation == 'add') {
                            if (self.$route.params.poCode) {
                                if (self.orderArr.length < 1) {
                                    self.$notify.error({
                                        title: '查询工单失败',
                                        message: '该工单非 执行 状态'
                                    })
                                }
                            } else {
                                self.mainDatas.poCode = self.orderArr[0].poCode
                            }
                            self.OrderChange(self.mainDatas.poCode)
                        }
                    } else {
                        self.$notify.error({
                            title: '错误',
                            message: '查询工单：失败,' + ',原因：' + resp.message
                        })
                    }
                })
            },
            //新增-工单改变
            OrderChange: function (value) {
                for (var i = 0; i < this.orderArr.length; i++) {
                    if (this.orderArr[i].poCode == value) {
                        this.mainDatas.prodCode = this.orderArr[i].prodCode
                        this.mainDatas.prodOrderCode = this.orderArr[i].prodCode
                        this.mainDatas.prodName = this.orderArr[i].prodName
                        this.mainDatas.prodTuhao = this.orderArr[i].prodTuhao
                        this.mainDatas.prodNumber = this.orderArr[i].prodNumber
                        this.queryProcess() // 当前简码查询工序
                        break;
                    }
                }
            },
            //保存
            saveDatas: function () {
                if (this.submitForm('mainDatas')) {
                    if (this.mainDatas.nonconformProductMaterialPOs.length == 0) {//零件损耗为空
                        this.queryMaterial()
                        this.$message({
                            message: '零件查询成功!',
                            type: 'success'
                        });
                    } else {
                        if (!this.mainDatas.nplId) {
                            //新增
                            this.mainDatas.prodDate = moment().format("YYYY-MM-DD");
                            this.mainDatas.createTime = moment().format("YYYY-MM-DD");
                            this.inputRecord(false)
                        } else {
                            this.inputRecord(true)
                        }
                    }
                    this.tabsNum = 'loss'
                } else {
                    this.tabsNum = 'message'
                }
            },
            //查询工序
            queryProcess: function () {
                var self = this
                this.$http.request({
                    url: '/api/process_info/' + self.mainDatas.prodCode,
                    method: 'GET',
                    params: {
                        line_id: this.nowLineId,
                    }
                }).then(function (resp) {
                    //错误提示
                    if (resp) {
                        self.ProcessArr = resp.data
                    } else {
                        self.$notify.error({
                            title: '错误',
                            message: '查询工序：原因：' + resp.message
                        })
                    }
                })
            },
            // 零件查询 
            queryMaterial: function () {
                var self = this
                this.$http.request({
                    url: '/api/nonconform_product/material/compute',
                    method: 'POST',
                    showLoading: true,
                    data: self.mainDatas
                }).then(function (resp) {
                    //错误提示
                    if (resp.status == 100) {
                        self.savaNum = false;
                        self.mainDatas.nonconformProductMaterialPOs = resp.data
                        self.reRecord()
                    } else {
                        self.$notify.error({
                            title: '错误',
                            message: '零件查询：失败,' + ',原因：' + resp.message
                        })
                    }
                })
            },
            // 不合格品录入
            inputRecord: function (ifadd) {
                var self = this
                this.$http.request({
                    url: '/api/nonconform_product/record',
                    method: 'POST',
                    showLoading: true,
                    data: self.mainDatas
                }).then(function (resp) {
                    //错误提示
                    if (resp.status == 100) {
                        if (!ifadd) {
                            self.$notify({
                                title: '成功',
                                message: '成功录入',
                                type: 'success',
                            });
                            self.mainDatas = resp.data
                        } else {
                            self.$notify({
                                title: '成功',
                                message: '保存成功',
                                type: 'success',
                            });
                        }
                        self.queryProduct() //刷新数据
                    } else {
                        self.$notify.error({
                            title: '错误',
                            message: '录入：失败,' + ',原因：' + resp.message
                        })
                    }
                })
            },
            // 不合格品录入
            reRecord: function () {
                var self = this
                this.$http.request({
                    url: '/api/nonconform_product/record',
                    method: 'POST',
                    data: self.mainDatas
                }).then(function (resp) {
                    //错误提示
                    if (resp.status == 100) {
                        self.mainDatas = resp.data
                        self.$message({
                            message: '保存成功!',
                            type: 'success'
                        });
                        self.queryProduct() //刷新数据
                    } else {
                        self.$notify.error({
                            title: '错误',
                            message: '保存：失败,' + ',原因：' + resp.message
                        })
                    }
                })
            },
            // 确认刷卡回调
            cardCallBack: function (userData) {
                if (userData != null) {
                    this.mainDatas.confirmPersonNo = userData.empNumber
                    this.mainDatas.confirmPersonName = userData.userName
                    this.monitorConfirm()
                }
                return;
            },
            // 作废刷卡回调
            cardCallBack2: function (userData) {
                if (userData != null) {
                    this.mainDatas.confirmPersonNo = userData.empNumber
                    this.mainDatas.confirmPersonName = userData.userName
                    this.monitorCancel()
                }
                return;
            },
            // 作废确认
            monitorCancel: function () {
                var self = this
                this.$http.request({
                    url: '/api/nonconform_product/record/cancel/' + self.mainDatas.nplId,
                    method: 'PUT',
                    showLoading: true,
                }).then(function (resp) {
                    //错误提示
                    if (resp.status == 100) {
                        self.queryProduct()
                        self.$notify({
                            title: '成功',
                            message: '该条已作废',
                            type: 'success',
                        });
                    } else {
                        self.$notify.error({
                            title: '错误',
                            message: '作废：失败,' + ',原因：' + resp.message
                        })
                    }
                })
            },
            //是否可以班长确认&&作废
            ifmonitorConfirm: function (value) {
                if (this.mainDatas.nplId) {
                    if (value) {
                        this.ifcardShow = true
                    } else {
                        this.ifcardShow2 = true
                    }
                } else {
                    this.$message({
                        type: 'warning',
                        message: '请先保存！'
                    });
                }
            },
            // 班长确认
            monitorConfirm: function () {
                var self = this
                this.$http.request({
                    url: '/api/nonconform_product/record/confirm',
                    method: 'POST',
                    showLoading: true,
                    data: self.mainDatas,
                    params: {
                        lineId: this.mainDatas.lineId,
                    }
                }).then(function (resp) {
                    //错误提示
                    if (resp.status == 100) {
                        self.queryProduct()
                        self.$notify({
                            title: '成功',
                            message: '班长确认成功',
                            type: 'success',
                        });
                    } else {
                        self.$notify.error({
                            title: '错误',
                            message: '班长确认：失败,' + ',原因：' + resp.message
                        })
                    }
                })
            },
            // 重新获取当前记录信息
            queryProduct: function () {
                var self = this
                this.$http.request({
                    url: '/api/nonconform_product/record/' + self.mainDatas.nplId,
                    method: 'GET',
                    showLoading: true,
                }).then(function (resp) {
                    if (resp.status == 100) {
                        self.mainDatas = resp.data
                        self.ifIsolation = resp.data.nplStatus
                    }
                })
            },
            //责任类型
            queryTprDutyType: function () {
                var self = this
                this.$http.request({
                    url: ' /api/dict_data/tpr_duty_type',
                    method: 'GET',
                }).then(function (resp) {
                    //错误提示
                    if (resp.status == 100) {
                        self.DutyTypeArr = resp.data
                    } else {
                        self.$notify.error({
                            title: '错误',
                            message: '责任类型查询：失败,' + ',原因：' + resp.message
                        })
                    }
                })
            },
            //批次状态
            queryTprBatchStatus: function () {
                var self = this
                this.$http.request({
                    url: '/api/dict_data/tpr_batch_status',
                    method: 'GET',
                }).then(function (resp) {
                    //错误提示
                    if (resp.status == 100) {
                        self.BatchStatusArr = resp.data
                    } else {
                        self.$notify.error({
                            title: '错误',
                            message: '批次状态查询：失败,' + ',原因：' + resp.message
                        })
                    }
                })
            },
            //缺陷模式
            queryTrouble: function () {
                var self = this
                this.$http.request({
                    url: '/api/nonconform_product/trouble_code/' + self.$ls.get('factory').fcSapCompany,
                    method: 'GET',
                }).then(function (resp) {
                    //错误提示
                    if (resp.status == 100) {
                        self.TroubleArr = resp.data
                    } else {
                        self.$notify.error({
                            title: '错误',
                            message: '缺陷模式查询：失败,' + ',原因：' + resp.message
                        })
                    }
                })
            },
            //发现工序名&&发现工序名
            getProcessName: function (value, ifFind) {
                for (var i = 0; i < this.ProcessArr.length; i++) {
                    if (this.ProcessArr[i].procCode == value) {
                        if (ifFind) {
                            this.mainDatas.nplFindOpName = this.ProcessArr[i].procName
                            break
                        } else {
                            this.mainDatas.nplProdOpName = this.ProcessArr[i].procName
                            break
                        }
                    }
                }
            },
            //缺陷模式名称
            getNplDefectDesc: function (value) {
                for (var i = 0; i < this.TroubleArr.length; i++) {
                    if (this.TroubleArr[i].trcTroubleCode == value) {
                        this.mainDatas.nplDefectDesc = this.TroubleArr[i].trcTroubleName
                        break
                    }
                }
            },
            // 表单验证
            submitForm(formName) {
                let r = false;
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        r = true
                    } else {
                        r = false
                    }
                });
                return r
            },
            // 零件损耗>table>编辑
            editBox: function (index) {
                this.tableIndex = index // 表格index
                // 编辑框input框赋值....
                this.edit_nplmBarcode = this.mainDatas.nonconformProductMaterialPOs[index].nplmBarcode
                this.edit_materialName = this.mainDatas.nonconformProductMaterialPOs[index].materialName
                this.edit_materialBoxCode = this.mainDatas.nonconformProductMaterialPOs[index].materialBoxCode
                this.edit_materialBatchCode = this.mainDatas.nonconformProductMaterialPOs[index].materialBatchCode
                this.edit_materialFurnaceNo = this.mainDatas.nonconformProductMaterialPOs[index].materialFurnaceNo
                this.edit_materialCode = this.mainDatas.nonconformProductMaterialPOs[index].materialCode
                this.edit_materialQty = this.mainDatas.nonconformProductMaterialPOs[index].materialQty

            },
            // 零件损耗>table>编辑>确定
            editboxConfirm: function () {
                // 保存编辑框input值...
                // this.mainDatas.nonconformProductMaterialPOs[this.tableIndex].nplBarcode = this.edit_materialName
                // this.mainDatas.nonconformProductMaterialPOs[this.tableIndex].materialBoxCode = this.edit_materialBoxCode
                // this.mainDatas.nonconformProductMaterialPOs[this.tableIndex].materialBatchCode = this.edit_materialBatchCode
                // this.mainDatas.nonconformProductMaterialPOs[this.tableIndex].materialFurnaceNo = this.edit_materialFurnaceNo
                // this.mainDatas.nonconformProductMaterialPOs[this.tableIndex].materialCode = this.edit_materialCode
                this.mainDatas.nonconformProductMaterialPOs[this.tableIndex].materialQty = this.edit_materialQty
                this.reRecord() //保存
            },
            //零件损耗>删除
            Deletematerials: function (index) {
                if (this.mainDatas.nonconformProductMaterialPOs.length == 1) {//零件损耗为空
                    this.$confirm('无零件损耗信息记录将作为作废记录，是否作废?', '提示', {
                        confirmButtonText: '作废',
                        cancelButtonText: '取消',
                        confirmButtonClass: 'el-button--danger',
                        type: 'warning'
                    }).then(() => {
                        this.ifcardShow2 = true
                    }).catch(() => {
                        this.$message({
                            type: 'info',
                            message: '已取消删除'
                        });
                    });
                } else {
                    this.$confirm('此操作将删除该数据, 是否继续?', '提示', {
                        confirmButtonText: '删除',
                        cancelButtonText: '取消',
                        confirmButtonClass: 'el-button--danger',
                        type: 'warning'
                    }).then(() => {
                        this.mainDatas.nonconformProductMaterialPOs.splice(index, 1)
                        this.reRecord()//删除后保存
                    }).catch(() => {
                        this.$message({
                            type: 'info',
                            message: '已取消删除'
                        });
                    });
                }

            },
            //批量删除
            DeleteMore: function () {
                if(this.tableDeleteMore.length == 0)
                    return
                this.$confirm('此操作将删除这些数据, 是否继续?', '提示', {
                    confirmButtonText: '删除',
                    cancelButtonText: '取消',
                    confirmButtonClass: 'el-button--danger',
                    type: 'warning'
                }).then(() => {
                    for (var i = 0; i < this.tableDeleteMore.length; i++) {
                        for (var t = 0; t < this.mainDatas.nonconformProductMaterialPOs.length; t++) {
                            if (this.tableDeleteMore[i].nplmId == this.mainDatas.nonconformProductMaterialPOs[t].nplmId) {
                                this.mainDatas.nonconformProductMaterialPOs.splice(t, 1)
                                continue;
                            }
                        }
                    }
                    this.reRecord()//删除后保存
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            handleSelectionChange(val) {
                this.tableDeleteMore = val
            },
            //缺陷模式采集卡
            getDefectCollection: function () {
                let Collection = this.mainDatas.defectCollection.split('-')
                this.mainDatas.nplFindOp = Collection[0]
                this.mainDatas.nplDefect = Collection[1]
                this.mainDatas.nplProdOp = Collection[2]
                this.getProcessName(this.mainDatas.nplProdOp, true)
                this.getProcessName(this.mainDatas.nplFindOp, false)
            },
            //打印
            Print: function () {
                var self = this;
                this.$printer.printNonconform(self.mainDatas);
            },

            //输入者刷卡
            inputFocus: function () {
                this.mainDatas.createPersonName = '',
                    this.$message('请刷卡')
            },
            onCardScan: function () {
                var qs = require('qs');
                var self = this
                if (!this.mainDatas.createPersonName) {
                    this.$notify.error({
                        title: '错误',
                        message: '刷卡未成功'
                    });
                    return
                }
                this.$http.request({
                    url: '/api/user/card_login',
                    method: 'POST',
                    showLoading: true,
                    data: qs.stringify({ 'cardId': this.mainDatas.createPersonName })
                }).then(function (resp) {
                    if (resp.status == 100) {
                        self.mainDatas.createPersonName = resp.data.userName
                        self.$notify({
                            title: '成功',
                            message: '刷卡成功',
                            type: 'success'
                        });
                        self.$refs.inputer.blur()//失去焦点
                    }
                    else {
                        self.mainDatas.createPersonName = ""
                        self.$notify.error({
                            title: '错误',
                            message: resp.message
                        });
                    }
                })
            }
        }
    }
</script>
<style>
