<template>
	<div class="app1">
		<el-row :gutter="10">
			<el-col :span="24">
				<div class="ButtonTitle">
					<el-col :span="24">
						<el-button type="success" size="mini" @click="assignedBtn">工单解冻</el-button>
						<el-button type="primary" size="mini" @click="frozenBtn">工单冻结</el-button>
					</el-col>
				</div>
			</el-col>
			<div class="content">
				<el-col :lg="24" :xl="24">
					<div class="perfromConLeft">
						<div class="perfromConLeftTitle" style="position: relative;">
							<el-col :lg="24" :xl="24">
								<h3 class="information">车间基本信息</h3>
								<p class="line">&nbsp;</p>
							</el-col>
						</div>

                    <div class="perfromConLeftContent">
                        <el-col :span="20">
                            <el-col :span="8">
                                <el-form ref="form" style="margin-left: 5vh;">
                                    <el-form-item label="工厂：">{{$ls.get('factory').fcName}}</el-form-item>
                                </el-form>
                            </el-col>
                            <el-col :span="8">
                                <el-form ref="form" style="margin-left: 5vh;">
                                    <el-form-item label="车间：">{{$ls.get('workshop').shopName}}</el-form-item>
                                </el-form>
                            </el-col>
                            <el-col :span="8">
                                <el-form ref="form" label-width="180px">
                                    <el-form-item label="生产线：">{{$ls.get('line').lineName}}</el-form-item>
                                </el-form>
                            </el-col>
                            <el-col :span="8">
                                <el-form ref="form" style="margin-left: 5vh;">
                                    <el-form-item label="班次：">{{$ls.get('shift').sfName}}</el-form-item>
                                </el-form>
                            </el-col>
                            <el-col :span="8">
                                <el-form ref="form" style="margin-left: 5vh;">
                                    <el-form-item label="线长：">{{$ls.get('user').userName}}</el-form-item>
                                </el-form>
                            </el-col>
                        </el-col>

							<el-col :span="4" style="margin-top:5vh;text-align: right; padding-right: 5vh;">
								<el-button type="warning" size="small" @click="onVerifyShow">开始防错</el-button>
							</el-col>
						</div>
					</div>
				</el-col>
			</div>

			<el-col :lg="24" :xl="24" class="tableBox">
				<div class="perfromConRight">
					<el-table
						ref="multipleTable"
						style="width: 100%;height: 62vh;overflow: scroll;"
						:data="orderArr"
						@selection-change="handleSelectionChange"
						:row-class-name="order_row_class"
					>
						<el-table-column width="55" type="selection"></el-table-column>
						<el-table-column prop="poCode" label="工单号"></el-table-column>
						<el-table-column prop="prodCode" label="产品简码"></el-table-column>
						<el-table-column prop="prodTuhao" label="图号"></el-table-column>
						<el-table-column prop="poCount" label="数量"></el-table-column>
						<el-table-column prop="poStartDatetime" label="计划开始"></el-table-column>
						<el-table-column prop="poEndDatetime" label="计划结束"></el-table-column>
						<el-table-column prop="poStatus" label="状态">
							<template slot-scope="scope">
								<span v-if="scope.row.poStatus==='Frozen'" style="color: rgb(179, 205, 255) !important">冻结</span>
								<span
									v-else-if="scope.row.poStatus==='Assigned'"
									style="color: rgb(255, 251, 0) !important"
								>下发</span>
								<span v-else-if="scope.row.poStatus==='Executing'">执行</span>
								<span v-else>{{scope.row.poStatus}}</span>
							</template>
						</el-table-column>
					</el-table>
					<el-col :span="24" class="nextButton" style="padding-top: 0px; margin-top:0px;height:3vh;">
						<el-button :type="isBegin ? 'warning':'info' " size="small" @click="startProduction">开始生产</el-button>
					</el-col>
				</div>
			</el-col>
		</el-row>

		<!--防错验证弹出框-->
		<PreparationVerify :show.sync="verifyShow" :order="orderToExec" @pass="faileCallBack"></PreparationVerify>

		<!--工单解冻弹出框-->
		<inputCard :show.sync="ifcardShow" @pass="exitCard"></inputCard>

		<!--工单冻结弹出框-->
		<div>
			<el-dialog
				:close-on-click-modal="false"
				title="工单冻结"
				:visible.sync="dialogVisible2"
				width="30%"
				class="dialogtitleTL"
			>
				<el-col :span="24" style="padding:4vh;border-bottom: 1px solid #2B6EAB;">
					<el-col :span="24">
						<el-form ref="form" label-width="120px">
							<el-form-item label="冻结原因：">
								<el-select v-model="ChoseReason" placeholder="请选择" popper-class="select01">
									<el-option
										v-for="item in options"
										:key="item.value"
										:label="item.label"
										:value="item.value"
									></el-option>
								</el-select>
							</el-form-item>
						</el-form>
					</el-col>
				</el-col>
				<div class="appdialog" style="text-align: center;padding: 10px;  background: #12599A;">
					<span slot="footer" class="dialog-footer">
						<el-button style=" margin-top: 1vh;" type="warning" @click="frozenConfirm">确 定</el-button>
						<el-button style=" margin-top: 1vh;" type="info" @click="dialogVisible2 = false">取 消</el-button>
					</span>
				</div>
			</el-dialog>
		</div>
	</div>
</template>

<script>
const qs = require("qs");
import PreparationVerify from "@/components/preparation_verify.vue";
import inputCard from "@/components/card_input.vue";
export default {
	components: {
		inputCard: inputCard,
		PreparationVerify
	},
	data() {
		return {
			orderArr: [],
			orderSelected: [],
			verifyShow: false,

			dialogVisible2: false,
			ifcardShow: false,
			ifGcard: Boolean,
			//是否需要人员防错  true 需要
			isneesSkill: true,
			//是否需要设备防错  true 需要
			isNeedDevice: true,
			//是否有正在执行中的工单
			isHaveExecuting: false,
			options: [
				{
					value: "1",
					label: "物料不足"
				},
				{
					value: "2",
					label: "工装不足"
				},
				{
					value: "3",
					label: "换班"
				},
				{
					value: "4",
					label: "其他"
				}
			],
			ChoseReason: "",
			//true 可以生产、false 不可以
			isBegin: false,
			//orderToExec:{},
			freezeReason: []
		};
	},
	computed: {
		orderToExec: function(val, oldVal) {
			for (var i = 0; i < this.orderArr.length; i++) {
				if (this.orderArr[i].poStatus == "Executing") {
					this.isHaveExecuting = true;
					return;
				}
				if (this.orderArr[i].poStatus == "Assigned") {
					return this.orderArr[i];
				}
			}
		}
	},
	created: function() {
		this.queryOrder();
	},
	mounted: function() {
		this.queryFroenCase();
	},
	methods: {
		order_row_class({ row }) {
			if (row.poStatus === "Executing") {
				return "T_yellow_row";
			}
		},
		queryOrder: function() {
			//查询工单
			var self = this;
			this.$http
				.request({
					url: "/api/order",
					method: "GET",
					params: {
						line_id: this.$ls.get("line").lineId,
						status: JSON.stringify([
							"Frozen",
							"Executing",
							"Assigned"
						])
					}
				})
				.then(function(resp) {
					self.orderArr = resp.data.list;
					self.queryIsbeginProduct();
				});
		},
		handleSelectionChange: function(val) {
			this.orderSelected = val;
		},
		startProduction: function(val) {
			//如果不可以生产
			if (!this.isBegin) {
				return;
			}

			var orderToExec = null;
			for (var i = 0; i < this.orderArr.length; i++) {
				if (
					this.orderArr[i].poStatus == "Assigned" &&
					orderToExec == null
				)
					orderToExec = this.orderArr[i];
				if (this.orderArr[i].poStatus == "Executing") {
					this.$notify.error({
						title: "错误",
						message: "已经有工单在执行!"
					});
					return;
				}
			}

			if (orderToExec == null) {
				this.$notify.error({
					title: "错误",
					message: "没有可执行的工单!"
				});
				return;
			}

			var self = this;
			this.$http
				.request({
					url: "/api/order/" + orderToExec.poCode + "/start",
					method: "POST"
				})
				.then(function(resp) {
					if (resp.status == 100) {
						self.jump("CvExecution");
					} else {
						self.$notify.error({
							title: "错误",
							message: resp.message
						});
					}
				});
		},
		onVerifyShow: function() {
			if (!this.orderToExec) {
				this.$notify.error({
					title: "错误",
					message: "没有可执行的工单!"
				});
				return;
			}
			this.verifyShow = true;
		},
		//查询是否可以生产
		queryIsbeginProduct: function() {
			var order = this.orderToExec;
			if (this.isHaveExecuting) {
				return;
			}
			var self = this;
			//查询是否做过人员防错
			this.$http
				.request({
					url: "/api/skill_verify/" + order.poCode + "/isneeded",
					method: "POST"
				})
				.then(function(resp) {
					if (!resp.data) {
						self.isneesSkill = false;
					} else {
						self.isneesSkill = true;
					}
					self.execIsNeedFail();
				});

			//查询是否做过设备防错
			this.$http
				.request({
					url: "/api/device_verify/" + order.poCode + "/isneeded",
					method: "POST"
				})
				.then(function(resp) {
					if (!resp.data) {
						self.isNeedDevice = false;
					} else {
						self.isNeedDevice = true;
					}
					self.execIsNeedFail();
				});
		},
		//判断是否需要做防错
		execIsNeedFail: function() {
			//如果有一个没有做过，这需要做防错
			if (this.isneesSkill || this.isNeedDevice) {
				this.isBegin = false;
			} else {
				this.isBegin = true;
			}
		},

		//防错回调
		faileCallBack: function(value) {
			if (value) {
				this.isBegin = true;
			}
		},
		exitCard() {
			var self = this;
			for (var i = 0; i < this.orderSelected.length; i++) {
				var data = this.orderSelected[i];
				this.$http
					.request({
						url: "/api/order/" + data.poCode + "/unfreeze",
						method: "POST"
					})
					.then(function(resp) {
						if (resp.status != 100) {
							self.$notify.error({
								title: "错误",
								message:
									"工单：" +
									data.poCode +
									"解冻失败，原因：" +
									resp.message
							});
						} else {
							self.queryOrder();
						}
					});
			}
		},
		assignedBtn: function() {
			if (this.orderSelected.length <= 0) {
				this.$notify.error({
					title: "错误",
					message: "请先选择要解冻的工单"
				});
				return;
			}
			this.ifcardShow = true;
		},
		//查询工单冻结原因
		queryFroenCase: function() {
			var self = this;
			this.$http
				.request({
					url: "/api/dict_data/freeze_reason",
					method: "GET"
				})
				.then(function(resp) {
					if (resp.status == 100) {
						self.freezeReason = resp.data;
					}
				});
		},
		frozenBtn: function() {
			if (this.orderSelected.length <= 0) {
				this.$notify.error({
					title: "错误",
					message: "请先选择要解冻的工单"
				});
				return;
			}
			this.dialogVisible2 = true;
		},
		frozenConfirm: function() {
			if (this.ChoseReason == null || this.ChoseReason == "") {
				this.$notify.error({
					title: "错误",
					message: "请先选择冻结原因"
				});
				return;
			}
			var self = this;
			var reasonName = this.options.find(item => {
				return item.value === self.ChoseReason;
			});

			for (var i = 0; i < this.orderSelected.length; i++) {
				var data = this.orderSelected[i];
				this.$http
					.request({
						url:
							"/api/order/" +
							data.poCode +
							"/freeze?freeze_reason=" +
							reasonName.label +
							"&freeze_id=" +
							this.ChoseReason,
						method: "POST"
					})
					.then(function(resp) {
						if (resp.status != 100) {
							self.$notify.error({
								title: "错误",
								message:
									"工单：" +
									data.poCode +
									"冻结失败，原因：" +
									resp.message
							});
						} else {
							self.queryOrder();
						}
						self.dialogVisible2 = false;
					});
			}
		}
	}
};
</script>



