<template>
    <div>
        <el-dialog title="过程委外" :visible.sync="dialogShow" width="35%" class="dialogtitleTL" @open="open()"
            :before-close="handleClose" :close-on-click-modal="false">
            <div>
                <el-col :span="22" style="padding-top:6vh;" :offset="2">
                    <el-col :sm="20" :lg="20">
                        <el-form label-width="120px" @submit.native.prevent>
                            <el-form-item label="扫描原过程码：">
                                <el-col :span="18">
                                    <el-input v-model="oldProcessCode" placeholder="请扫描录入" @keyup.enter.native="onScan">
                                    </el-input>
                                </el-col>
                                <el-col :span="4" :offset="2">
                                    <el-button size="small" type="info" @click="oldProcessCode=''">清空</el-button>
                                </el-col>
                            </el-form-item>
                        </el-form>
                    </el-col>
                    <el-col :sm="20" :lg="20" style="padding-top:2vh;">
                        <el-form label-width="120px" @submit.native.prevent>
                            <el-form-item label="扫描委外码：">
                                <el-col :span="18">
                                    <el-input v-model="newProcessCode" placeholder="请扫描录入" @keyup.enter.native="onScan">
                                    </el-input>
                                </el-col>
                                <el-col :span="4" :offset="2">
                                    <el-button size="small" type="info" @click="newProcessCode=''">清空</el-button>
                                </el-col>
                                <p v-if="smSusuccess.length>0" class="p_red">{{smSusuccess}}</p>
                            </el-form-item>
                        </el-form>
                    </el-col>
                </el-col>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    export default {
        props: ["show", "order", 'procCode'],
        name: "OutsourcingCharging",
        data() {
            return {
                newProcessCode: '',
                oldProcessCode: '',
                dialogShow: this.show,
                smSusuccess: '',
                huInfo: [],
            };
        },
        watch: {
            show: function (newValue, oldValue) {
                this.dialogShow = newValue;
            },
            dialogShow: function (newValue, oldValue) {
                if (this.show != this.dialogShow)
                    this.$emit("update:show", this.dialogShow);
            }
        },
        methods: {
            onScan: function () {
                if (this.oldProcessCode.indexOf("|") > 0) {
                    this.oldProcessCode = this.oldProcessCode.split('|')[0];
                }
                if (this.newProcessCode.indexOf("|") > 0) {
                    this.newProcessCode = this.newProcessCode.split('|')[0];
                }
                if (!this.oldProcessCode || !this.newProcessCode)
                    return
                this.smSusuccess = ''
                var self = this
                //获取HU信息
                this.$http.request({
                    url: '/api/package/' + this.newProcessCode,
                    method: 'GET',
                    showLoading: true
                }).then(function (resp) {
                    if (resp.status != 100) {
                        self.smSusuccess = resp.message
                        return
                    }
                    if (resp.data == null || resp.data.length == 0) {
                        self.smSusuccess = "未查询到合格证信息"
                        return
                    }
                    self.huInfo = resp.data[0];
                    self.comfirm()
                });
            },
            comfirm() {
                var self = this
                this.$http.request({
                    url: '/api/prod_wip/wip_in/' + this.order.poCode + '/' + this.order.lineId + '/' + this.procCode,
                    method: 'POST',
                    showLoading: true,
                    data: this.huInfo,
                    params: {
                        wipBarcode: this.oldProcessCode,
                    }
                }).then(function (resp) {
                    if (resp.status == 100) {
                        self.$message({
                            message: '成功',
                            type: 'success'
                        });
                    } else {
                        self.$message.error(resp.message);
                    }
                    self.exitmodal()
                });
            },
            open() {
                this.oldProcessCode = ''
                this.newProcessCode = ''
                this.smSusuccess = ''
                this.huInfo = {}
            },
            //关闭时触发
            exitmodal() {
                this.dialogShow = false;
                this.$emit("exitOutsourcing");
            },
            handleClose(done) {
                this.smSusuccess = ''
                this.exitmodal(), done();
            },
        }
    };
</script>

<style>
</style>


