var VueUtils = {}
import { showLoading, hideLoading } from './loading';
//公共处理方法
VueUtils.install = function (Vue, option) {

	Vue.prototype.formatDateTime = function (inputTime, fommat) {
		if (!inputTime) {
			return '';
		}
		var date = new Date(inputTime);
		var y = date.getFullYear();
		var m = date.getMonth() + 1;
		m = m < 10 ? ('0' + m) : m;
		var d = date.getDate();
		d = d < 10 ? ('0' + d) : d;
		var h = date.getHours();
		h = h < 10 ? ('0' + h) : h;
		var minute = date.getMinutes();
		var second = date.getSeconds();
		minute = minute < 10 ? ('0' + minute) : minute;
		second = second < 10 ? ('0' + second) : second;
		if (fommat)
			return y + '-' + m + '-' + d + '';
		return y + '-' + m + '-' + d + ' ' + h + ':' + minute + ':' + second;
	}

	Vue.prototype.fileUp = function (formId, callback) {
		var formData = new FormData(document.getElementById(formId));
		$.ajax({
			url: '/api/file/upload',
			type: 'post',
			async: false,
			data: formData,
			cache: false,
			contentType: false,
			processData: false,
			success: function (resp) {
				callback(resp.data)
			}
		})
	}

	Vue.prototype.guid = function () {
		return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
			var r = Math.random() * 16 | 0, v = c == 'x' ? r : (r & 0x3 | 0x8);
			return v.toString(16);
		});
	}

	//页面跳转
	Vue.prototype.jump = function (pagename) {
		if (pagename == 'back') {
			this.$router.go(-1)
		} else {
			this.$router.push({ name: pagename })
		}
    }
    
    //打开Loading界面
	Vue.prototype.showLoading = function () {
		showLoading()
    }
    
    //关闭Loading界面
	Vue.prototype.hideLoading = function () {
		hideLoading()
	}
}

export default VueUtils;


