<template>
  <div id="app">
    <keep-alive>
        <router-view v-if="$route.meta.keepAlive"></router-view>
    </keep-alive>
    <router-view v-if="!$route.meta.keepAlive"></router-view>
  </div>
</template>

<script>
export default {
  name: 'App'
}
</script>
<style>
  *{
    font-size: 14px;
    list-style-type: none;
    text-decoration: none;
    transition: all .3s;
    margin: 0;
    padding: 0;
  }
  body,html{
    background-color: #12599A;
    width: 100%;
    height: 100%;
  }
</style>



