<template>
    <div>
        <el-dialog title="工序上料" :visible.sync="dialogShow" width="35%" class="dialogtitleTL" :before-close="handleClose" @open="onDialogOpen" :close-on-click-modal="false">
            <el-col :span="24" style="padding-top:4vh;">
    
                <el-col :sm="20" :lg="20">
                    <el-form ref="form" label-width="120px" @submit.native.prevent>
                        <el-form-item label="扫描合格证：">
                            <el-input v-model="scanValue" placeholder="请扫描录入" @keyup.enter.native="onScan"></el-input>
                            <p v-if="smSusuccess.length>0" class="p_red">{{smSusuccess}}</p>
                        </el-form-item>
                    </el-form>
                </el-col>
    
                <el-col :sm="4" :lg="2">
                    <el-form ref="form" label-width="20px">
                        <el-form-item>
                            <span><img src="@/assets/images/sm.png"></span>
                        </el-form-item>
                    </el-form>
                </el-col>
            </el-col>
    
            <el-col :span="24" style="position: relative;padding-bottom: 10px;">
                <h3 class="tbinformation">
                    物料信息</h3>
                <p class="line">
                    &nbsp;</p>
    
            </el-col>
    
            <el-col :span="24" class="Boxing1">
                <el-col :span="12">
                    <el-form ref="form" label-width="140px">
                        <el-form-item label="物料名称：">
                            {{stock.matName}}
                        </el-form-item>
                    </el-form>
                </el-col>
                <el-col :span="12">
                    <el-form ref="form" label-width="140px">
                        <el-form-item label="物料简码：">
                            {{stock.matCode}}
                        </el-form-item>
                    </el-form>
                </el-col>
                <el-col :span="12">
                    <el-form ref="form" label-width="140px">
                        <el-form-item label="物料数量：">
                            {{stock.boxQuan}}
                        </el-form-item>
                    </el-form>
                </el-col>
            </el-col>
            <div class="appdialog" style="text-align: center;padding: 10px;  background: #12599A;">
                <span slot="footer" class="dialog-footer">
                    <el-button style=" margin-top: 2vh;" type="warning" size="mini" @click="deviceStockConfirm">确 定
                    </el-button>
                    <el-button style=" margin-top: 2vh;" type="info" size="mini" @click="exitmodal">取 消</el-button>
                </span>
            </div>
        </el-dialog>
    </div>
    </template>
    
    <script>
    export default {
        props: ["show", "poCode","procCode", "lineId"],
        name: "Modal",
        data() {
            return {
                dialogShow: this.show,
                activeWZX: 0,
                scanning: '',
                number: '',
                hege: '',
                smSusuccess: '',
                scanValue: '',
                stock: {},
            };
        },
        watch: {
            show: function (newValue, oldValue) {
                this.dialogShow = newValue;
            },
            dialogShow: function (newValue, oldValue) {
                if (this.show != this.dialogShow)
                    this.$emit("update:show", this.dialogShow);
            }
        },
        methods: {
            onDialogOpen: function () {
                this.stock = {};
            },
            handleClose(done) {
                this.errMsg=''
                this.exitmodal(), done();
            },
            onScan: function () {
                var scanValueTmp = this.scanValue
                this.scanValue = ''
                if (scanValueTmp.indexOf("|") > 0) {
                    scanValueTmp = scanValueTmp.split('|')[0];
                }
    
                this.errMsg = ''
                var self = this
                //获取HU信息
                this.$http.request({
                    url: '/api/package/' + scanValueTmp,
                    method: 'GET',
                    showLoading: true
                }).then(function (resp) {
                    var pckgs = resp.data
                    if (resp.status != 100) {
                        self.smSusuccess = resp.message
                        return
                    }
                    if (resp.data == null || resp.data.length==0) {
                        self.smSusuccess = "未查询到合格证信息"
                        return
                    }
                    self.stock = resp.data[0];
                });
            },
            deviceStockConfirm: function () {
                var self = this;
                var lineId= this.$ls.get('line').lineId
                if(this.lineId)
                    lineId = this.lineId
                this.$http.request({
                    url: '/api/proc_stock/' + self.poCode + "/"+lineId+'/' + self.procCode,
                    method: 'POST',
                    showLoading: true,
                    data: [this.stock]
                }).then(function (resp) {
                    if (resp.status == 100) {
                        self.exitmodal();
                        self.dialogShow = false;
                    }
                    else{
                        self.smSusuccess = resp.message
                    }
                })
            },
            //关闭时触发
            exitmodal() {
                this.dialogShow = false;
                this.$emit("exitcharging");
            },
        }
    };
    </script>
    
    <style>
    </style>
    


