const defaults = {
    isAuth: false,
}

import CardInput from './card_input.vue';

const CARDSCAN = {
    install(Vue) {
        Vue.component('CardScan', CardInput)
        const CardScanConstructor = Vue.extend(CardInput);

        function cardScan(isAuth, doClose) {
            let instance = new CardScanConstructor({
                el: document.createElement('div')
            })

            instance.show = true
            instance.isAuth = isAuth
            instance.doClose = function(userInfo) {
                doClose(userInfo)
                document.body.removeChild(instance.$el)
            }

            document.body.appendChild(instance.$el);
        }

        Vue.prototype.$cardScan = {
            scan(onClose) {
                cardScan(false, onClose)
            },
            auth(onClose) {
                cardScan(true, onClose)
            },
        }
    }
}

export default CARDSCAN


