<template>
	<div class="app3">
		<el-row :gutter="10">
			<el-col :span="24">
				<div class="ButtonTitle">
					<el-col :lg="18" :xl="18">
						<el-button :type="iftbative? 'primary':'info'" style="width:70px;" size="mini"
							@click="OrderFeeding">
							上料
						</el-button>
						<el-button :type="iftbative? 'primary':'info'" style="width:70px;" size="mini"
							@click="cleanLine">
							清线
						</el-button>
						<el-button :type="iftbative? 'primary':'info'" style="width:70px;" size="mini" @click="pkgManu">
							报交
						</el-button>
					</el-col>
				</div>
			</el-col>
			<div class="content">
				<el-col :span="24">
					<div class="perfromConLeft">
						<div class="perfromConLeftTitle" style="position: relative;">
							<el-col :span="24">
								<h3 class="information">车间基本信息</h3>
								<p class="line">&nbsp;</p>
							</el-col>
						</div>
						<div class="perfromConLeftContent">
							<el-col :span="6">
								<el-form label-width="80px" style="margin-top: 1vh;margin-left: 4.5vh;">
									<el-form-item label="工厂：">{{$ls.get('factory').fcName}}</el-form-item>
								</el-form>
							</el-col>
							<el-col :span="6">
								<el-form label-width="80px" style="margin-top: 1vh;margin-left: 4.5vh;">
									<el-form-item label="车间：">{{$ls.get('workshop').shopName}}</el-form-item>
								</el-form>
							</el-col>
							<el-col :span="6">
								<el-form label-width="80px" style="margin-top: 1vh;">
									<el-form-item label="产线：">
										{{$ls.get('line').lineName}}
									</el-form-item>
								</el-form>
							</el-col>
							<el-col :span="6" style="text-align: right;">
								<el-form label-width="80px" style="margin-right: 3vh;">
									<el-form-item>
										<el-button :type="iftbative? 'warning':'info'" size="small"
											@click="errorProofing">开始防错</el-button>
									</el-form-item>
								</el-form>
							</el-col>
						</div>
					</div>
				</el-col>
			</div>
			<el-col :span="10">
				<p style="margin-bottom: 1.2vh;height: 4vh;color:#DAA651;margin-top: 1vh;" class="m_label">工单列表
					<el-date-picker class="datepicker" :clearable="false" v-model="orderDateTime" type="date"
						placeholder="选择日期" @change="queryOrder" format="yyyy 年 MM 月 dd 日" value-format="yyyy-MM-dd"
						:picker-options="pickerOptions">
					</el-date-picker>
				</p>
				<div class="GDtable" id="orderListBox">
					<!-- tbative = 高亮class -->
					<div class="GDtables" v-for="data in orderArr" :id="data.poCode" @click="changeOrder(data)"
						v-bind:class="{ tbative: data.poCode==iftbative }">
						<el-col :span="24" class="ative">
							<p class="tbNative_p" style="margin-top: 1.5vh;margin-left: 2vh;">工单号：{{data.poCode}}
								<span v-if="data.poStatus=='Executing'">执行</span>
								<span v-else-if="data.poStatus=='Frozen'">冻结</span>
								<span v-else-if="data.poStatus=='Completed'">完成</span>
								<span v-else-if="data.poStatus=='Assigned'">下发</span>
								<span v-else>{{data.poStatus}}</span>
							</p>
							<p class="tbline">
								&nbsp;</p>
						</el-col>
						<el-col :span="24">
							<el-col :span="9">
								<el-form ref="form">
									<el-form-item label="物料号：" style="margin-top: .5vh;margin-left: 2vh;">
										{{data.prodCode}}
									</el-form-item>
								</el-form>
							</el-col>
							<el-col :span="15">
								<el-form ref="form">
									<el-form-item label="产品图号：" style="margin-top: .5vh;margin-left: 2vh;">
										{{data.prodTuhao}}
									</el-form-item>
								</el-form>
							</el-col>
							<el-col :span="9">
								<el-form ref="form">
									<!-- <el-form-item label="实际/计划：" style="margin-top: .5vh;margin-left: 2vh;">
                                        <span class=" shiji">0</span>/{{data.poCount}}
                                    </el-form-item> -->
									<el-form-item label="计划数量：" style="margin-top: .5vh;margin-left: 2vh;">
										{{data.poCount}}
									</el-form-item>
								</el-form>
							</el-col>
							<el-col :span="15">
								<el-form ref="form">
									<el-form-item label="计划开始时间：" style="margin-top: .5vh;margin-left: 2vh;">
										{{data.poStartDatetime}}
									</el-form-item>
								</el-form>
							</el-col>
						</el-col>
						<el-col :span=" 24">
							<el-form ref="form" label-width="80px">
								<el-form-item style="float: right;margin-right: 4vh;">
									<el-button v-if="data.poStatus == 'Frozen'" class="tbNative_b" size="mini"
										@click="thawModelShow=true" type="info">解冻
									</el-button>
									<el-button v-if="data.poStatus == 'Executing'" class="tbNative_b" size="mini"
										@click="closeOrder(data)" type="info">工单关闭
									</el-button>
									<el-button v-else class="tbNative_b" type="primary" size="mini"
										@click="startOrder(data)">工单开始</el-button>
								</el-form-item>
							</el-form>
						</el-col>
					</div>
				</div>
			</el-col>
			<el-col :span="14" class="tableBox">
				<p style="color:#DAA651;margin-top: 1vh;">设备</p>
				<div class="perfromConRight">
					<el-col :span="24" class="shebei">
						<el-col :span="24">
							<el-col :span="24">
								<el-col :span="24">
									<el-col :span="8">
										<el-form label-width="80px">
											<el-form-item label="产品简码：">{{nowOrder.prodCode}}</el-form-item>
										</el-form>
									</el-col>
									<el-col :span="10">
										<el-form label-width="80px">
											<el-form-item label="图号：">
												{{nowOrder.prodTuhao}}
											</el-form-item>
										</el-form>
									</el-col>
									<el-col :span="6">
										<el-form label-width="80px">
											<el-form-item label="物料描述：">
												{{nowOrder.prodName}}</el-form-item>
										</el-form>
									</el-col>
								</el-col>
							</el-col>
						</el-col>
					</el-col>

					<div class="QDLTbale">
						<el-col :span="24" class="QDLtbale">
							<p style="margin-bottom: 0.5vh;margin-left:1vh;">已装箱清单栏</p>
							<el-table style="width: 100%;height: 25vh;overflow-y: scroll;" :data="boxlogArr">
								<el-table-column label="箱条码号" prop="boxBarcode"></el-table-column>
								<el-table-column label="数量" prop="boxQuan"></el-table-column>
								<el-table-column label="结箱时间" prop="createTime"></el-table-column>
							</el-table>
							<p style="margin-bottom: 0.5vh;margin-left:1vh;margin-top: 1vh;">物料清单栏</p>
							<el-table style="width: 100%;height:26vh;overflow-y: scroll;" :data="stockBomArr"
								:default-sort="{prop: 'materialNumLack', order: 'descending'}"
								:row-class-name="tableRowClassName">
								<el-table-column label="物料号" prop="materialCode"></el-table-column>
								<el-table-column label="名称" prop="materialName"></el-table-column>
								<el-table-column label="上料数量" prop="materialNumLeft"></el-table-column>
								<el-table-column label="差额" prop="materialNumLack"></el-table-column>
							</el-table>
						</el-col>
					</div>
				</div>
			</el-col>
		</el-row>
		<!--防错验证弹出框-->
		<PreparationVerify :show.sync="verifyShow" :order="nowOrder" @pass=""></PreparationVerify>
		<!-- 上料 -->
		<OrderFeeding :show.sync="orderFeedingShow" :order="nowOrder" @feeded="changeOrder(nowOrder)"></OrderFeeding>
		<!--清线弹出框 -->
		<OrderStockCline :show.sync="orderClineShow" :order="nowOrder" @orderClosed="queryOrder(nowOrder)">
		</OrderStockCline>
		<!--未满装箱弹出框 -->
		<OrderPackageManu :show.sync="orderPkgManuShow" :order="nowOrder" stockResource="ORDER_SUB"
			:boxLog="boxlogToConfirm" :isMatBox="true" @packageDone="changeOrder(nowOrder)">
		</OrderPackageManu>
		<!-- 工单关闭 -->
		<closeModel :show.sync="cardScanShow" @pass="cardCallBack"></closeModel>
		<!--工单解冻弹出框-->
		<thawModel :show.sync="thawModelShow" @pass="thawExit"></thawModel>


	</div>
</template>

<script>
	import thawModel from "@/components/card_input.vue";
	import closeModel from "@/components/card_input.vue";
	import OrderFeeding from "@/components/order_feeding.vue";
	import PreparationVerify from "@/components/preparation_verify.vue";
	import OrderStockCline from "@/components/order_stock_cline.vue";
	import OrderPackageManu from "@/components/order_package_manu.vue"
	export default {
		components: {
			thawModel,
			closeModel,
			OrderFeeding,
			PreparationVerify,
			OrderStockCline,
			OrderPackageManu
		},
		data() {
			return {
				isBegin: false,//是否已防错
				cleanlogs: [],//清线
				boxlogToConfirm: '',//装箱未完
				//model
				thawModelShow: false,//解冻
				verifyShow: false,//防错
				orderFeedingShow: false,//上料
				orderClineShow: false,//清线
				orderPkgManuShow: false,//报交
				cardScanShow: false,//关闭刷卡
				//APIdata
				orderArr: [],//工单
				boxlogArr: [],//装箱清单
				stockBomArr: [],//物料清单x
				iftbative: '',//当前选中工单号
				nowOrder: '',//当前工单

				lineId: this.$ls.get('line').lineId,
				orderDateTime: '',//日期查询
				pickerOptions: {
					shortcuts: [{
						text: '今天',
						onClick(picker) {
							picker.$emit('pick', new Date());
						}
					}, {
						text: '昨天',
						onClick(picker) {
							const date = new Date();
							date.setTime(date.getTime() - 3600 * 1000 * 24);
							picker.$emit('pick', date);
						}
					}, {
						text: '明天',
						onClick(picker) {
							const date = new Date();
							date.setTime(date.getTime() + 3600 * 1000 * 24);
							picker.$emit('pick', date);
						}
					}, {
						text: '所有工单',
						onClick(picker) {
							const date = new Date();
							date.setTime(0);
							picker.$emit('pick', date);
						}
					}]
				},

			};
		},
		mounted: function () {
			this.queryOrder()
		},
		methods: {
			//工单查询
			queryOrder: function () {
				var start_time
				var end_time
				if (this.orderDateTime && this.orderDateTime != '1970-01-01') {
					start_time = this.orderDateTime + ' 00:00:00'
					end_time = this.orderDateTime + ' 23:59:59'
				} else {
					this.orderDateTime = null
					start_time = null
					end_time = null
				}
				//查询工单
				var self = this
				this.$http.request({
					url: '/api/order',
					method: 'GET',
					params: {
						line_id: this.lineId,
						status: JSON.stringify(['Executing', 'Assigned','Frozen']),
						start_time: start_time,
						end_time: end_time
					}
				}).then(function (resp) {
					self.orderArr = resp.data.list;
					self.ifhavaOrderTbative()
				});
			},
			//是否包含此工单
			ifhavaOrderTbative() {
				if (this.iftbative) {
					for (var i = 0; i < this.orderArr.length; i++) {
						if (this.orderArr[i].poCode == this.iftbative) {
							return
						}
					}
				}
				this.iftbative = ''
				this.nowOrder = []
				this.boxlogArr = []
				this.stockBomArr = []
			},

			//工单选择
			changeOrder: function (data) {
				this.iftbative = data.poCode
				this.nowOrder = data
				document.getElementById(this.iftbative).scrollIntoView({ behavior: "smooth" });
				this.queryboxlog()//已装箱刷新
				this.queryOrderStockBomView()//物料清单刷新
				this.queryCleanlog()//是否有清线
				this.queryboxlog()//是否已装箱
			},

			//已装箱
			queryboxlog: function () {
				var self = this
				this.$http.request({
					url: '/api/package/boxlog',
					method: 'GET',
					params: {
						order_code: this.nowOrder.poCode
					}
				}).then(function (resp) {
					self.boxlogArr = resp.data.list
					//检验是否有待确认箱
					for (var i = 0; i < self.boxlogArr.length; i++) {
						if (!self.boxlogArr[i].isConfirmed) {
							self.boxlogToConfirm = self.boxlogArr[i];
							self.orderPkgManuShow = true
							return;
						}
					}
					self.boxlogToConfirm = null
				});
			},
			//物料清单
			queryOrderStockBomView: function () {
				var self = this
				this.$http.request({
					url: '/api/po_stack/statistic/' + this.nowOrder.poCode,
					method: 'GET'
				}).then(function (resp) {
					if (resp.data) {
						self.stockBomArr = resp.data
					}
				})
			},
			//开始防错
			errorProofing: function () {
				if (!this.iftbative) {
					this.$message('请先选择工单!');
					return
				}
				this.verifyShow = true
			},

			//上料
			OrderFeeding: function () {
				if (!this.iftbative) {
					this.$message('请先选择工单!');
					return
				}
				this.orderFeedingShow = true
			},

			//查询是否为做过清线
			queryCleanlog: function () {
				var self = this;
				//查询有无未做完的清线
				this.$http.request({
					url: '/api/po_stack/cleanlog?order_code=' + self.nowOrder.poCode + '&is_confirmed=false',
					method: 'GET'
				}).then(function (resp) {
					self.cleanlogs = resp.data.list;
					if (self.cleanlogs.length > 0) {
						self.orderClineShow = true;
					}
				})
			},
			//清线
			cleanLine: function () {
				if (!this.iftbative) {
					this.$message('请先选择工单!');
					return
				}
				this.orderClineShow = true
			},

			//报交
			pkgManu: function () {
				if (!this.iftbative) {
					this.$message('请先选择工单!');
					return
				}
				this.orderPkgManuShow = true
			},

			//工单开始
			startOrder: function (data) {
				var self = this
				this.$http.request({
					url: '/api/order/' + data.poCode + '/start',
					method: 'POST',
					params: {
						checkBoxInfo: false
					}
				}).then(function (resp) {
					if (resp.status == 100) {
						self.queryOrder();
					} else {
						self.$notify.error({
							title: '错误',
							message: resp.message
						});
					}
				})
			},
			//工单关闭
			closeOrder: function (data) {
				//关闭工单
				this.$message({
					message: '暂未开启关闭通道',
					type: 'info'
				});
				// this.cardScanShow = true
			},
			//工单关闭刷卡回调
			cardCallBack: function (userData) {
				if (userData == null) {
					this.$notify.error({
						title: '错误',
						message: '没有查到此人'
					});
					return;
				}
				var self = this
				this.$http.request({
					url: '/api/order/' + this.nowOrder.poCode + '/close',
					method: 'POST',
					showLoading: true
				}).then(function (resp) {
					if (resp.status == 100) {
						self.$notify({
							title: '成功',
							message: '关闭工单成功',
							type: 'success'
						});
					} else {
						self.$notify.error({
							title: '错误',
							message: resp.message
						});
					}
					self.queryOrder()
				})
			},
			//解冻回调
			thawExit: function (userData) {
				var self = this;
				this.$http.request({
					url: "/api/order/" + this.nowOrder.poCode + "/unfreeze",
					method: "POST"
				}).then(function (resp) {
					if (resp.status != 100) {
						self.$notify.error({
							title: "错误",
							message:
								"工单：" +
								self.nowOrder.poCode +
								"解冻失败，原因：" +
								resp.message
						});
					} else {
						self.queryOrder();
						self.$notify({
							title: '成功',
							message: '工单解冻成功',
							type: 'success'
						});
					}
				});
			},
            tableRowClassName: function ({
                row,
                rowIndex
            }) {
                if (row.materialNumLack > 0) {
                    return 'fontred01';
                }
                return '';
            },
		}
	};
</script>

<style>
</style>


