<template>
    <div class="modal cardModal">
        <el-dialog :visible.sync="dialogShow" title width="30%" @open="onDialogOpen" @closed="onClosed">
            <el-col :span="24" style="height: auto">
                <input
                    type="search"
                    v-model="cardId"
                    autofocus="autofocus"
                    class="card-input2"
                    v-on:keyup.enter="onCardScan"
                />
                <img v-if="!isAuth" src="@/assets/images/Gcard.png" />
                <img v-else src="@/assets/images/Hcard.png" />
            </el-col>
        </el-dialog>
    </div>
</template>

<script>
const qs = require('qs');

export default {
    props: ["show", "isAuth"],
    name: "CardScan",
    data() {
        return {
            dialogShow: this.show,
            cardId: '',
            errMsg: '',
            userInfo: null
        };
    },
    watch: {
        show: function(newValue, oldValue) {
            this.dialogShow = newValue;
        },
        dialogShow: function(newValue, oldValue) {
            if (this.show != this.dialogShow)
                this.$emit("update:show", this.dialogShow);
        }
    },
    methods: {
        onDialogOpen: function() {
            this.cardId = ""
            this.errMsg = ""
        },
        exitmodal() {
            this.dialogShow = false;
        },
        onClosed() {
            if(this.doClose)
                this.doClose(this.userInfo)
        },
        onCardScan: function() {
            var cardIdTmp = this.cardId
            this.cardId = ""
            this.errMsg = ""

            var self = this
            this.$http.request({
                url: '/api/user/card_login',
                method: 'POST',
                showLoading: true,
                data: qs.stringify({ 'cardId': cardIdTmp})
            }).then(function(resp){
                if(resp.status == 100){
                    var userInfo = resp.data

                    // TODO 判断是否有班长角色
                    self.userInfo = userInfo
                    self.$emit("pass", userInfo)
                    self.exitmodal()
                }
                else{
                    self.errMsg = resp.message
                    self.$notify.error({
                            title: '错误',
                            message: resp.message
                        });
                }
            })
        }
    }
};
</script>

<style>
.cardModal .el-dialog__header {
    display: none;
}

.cardModal .el-dialog__body {
    width: auto;
    height: auto;
}

.cardModal img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    margin: 0 !important;
}
.cardModal .el-dialog,
.el-pager li {
    background: none;
}
.card-input2 {
    width: 100%;
    height: 350px;
    z-index: 99;
    position: absolute;
    top: -175px;
    border: none;
    left: 0;
    background: none;
    color: #fff;
}
.card-input2:focus{
outline: none !important;
}
</style>



