<template>
    <div>
        <el-dialog title="过程委外" :visible.sync="dialogShow" width="35%" class="dialogtitleTL" @open="open()"
            :before-close="handleClose" :close-on-click-modal="false">
            <el-col :span="24" class="dialogtitleSL" style="height:100px">
                <el-col class="stepBox">
                    <span class="stepLine s_finish"></span>
                    <div class="stepNum s_finish">
                        <p>1</p><span>过程委外</span>
                    </div>
                    <span class="stepLine s_finish"></span>
                    <div v-bind:class="{ s_finish: active>0}" class="stepNum">
                        <p>2</p><span>扫描验证</span>
                    </div>
                    <span v-bind:class="{ s_finish: active>0}" class="stepLine"></span>
                </el-col>
            </el-col>
            <div v-if="active==0">
                <el-col :span="24" style="padding-top:4vh;">
                    <el-col :sm="20" :lg="20">
                        <el-form label-width="120px" @submit.native.prevent>
                            <el-form-item label="扫描过程码：">
                                <el-input v-model="scanValue" placeholder="请扫描录入" @keyup.enter.native="onScan">
                                </el-input>
                                <p v-if="smSusuccess.length>0" class="p_red">{{smSusuccess}}</p>
                            </el-form-item>
                        </el-form>
                    </el-col>
                    <el-col :sm="4" :lg="2">
                        <el-form label-width="20px">
                            <el-form-item>
                                <span><img src="@/assets/images/sm.png"></span>
                            </el-form-item>
                        </el-form>
                    </el-col>
                </el-col>
                <el-col :span="24" style="position: relative;padding-bottom: 10px;">
                    <h3 class="tbinformation">
                        物料信息</h3>
                    <p class="line">
                        &nbsp;</p>
                </el-col>
                <el-col :span="24" class="Boxing1">
                    <el-col :span="12">
                        <el-form label-width="140px">
                            <el-form-item label="物料名称：">
                                {{stock.matName}}
                            </el-form-item>
                        </el-form>
                    </el-col>
                    <el-col :span="12">
                        <el-form label-width="140px">
                            <el-form-item label="物料简码：">
                                {{stock.matCode}}
                            </el-form-item>
                        </el-form>
                    </el-col>
                    <el-col :span="12">
                        <el-form label-width="140px">
                            <el-form-item label="物料数量：">
                                {{stock.boxQuan}}
                            </el-form-item>
                        </el-form>
                    </el-col>
                </el-col>
            </div>
            <div v-if="active>0">
                <el-col :span="24" style="margin-bottom: 3vh;">
                    <el-col :sm="20" :lg="20">
                        <el-form label-width="120px" @submit.native.prevent>
                            <el-form-item label="扫描委外码：">
                                <el-input v-model="scanValue2" placeholder="请扫描录入" @keyup.enter.native="onScan2">
                                </el-input>
                                <p v-if="smSusuccess.length>0" class="p_red">{{smSusuccess}}</p>
                            </el-form-item>
                        </el-form>
                    </el-col>
                    <el-col :sm="4" :lg="2">
                        <el-form label-width="20px">
                            <el-form-item>
                                <span><img src="@/assets/images/sm.png"></span>
                            </el-form-item>
                        </el-form>
                    </el-col>
                        <el-col :span="12" class="Boxing2">
                            <el-form label-width="20px">
                                <el-form-item>
                                    <img src="@/assets/icon/refresh.png" />
                                    <span style="color: rgb(240, 156, 29);" @click="$printer.printCleanLog(cleanlogs)">补打委外码</span>
                                </el-form-item>
                            </el-form>
                        </el-col>
                </el-col>
            </div>
            <div class="appdialog" style="text-align: right;padding: 10px;  background: #12599A;">
                <span slot="footer" class="dialog-footer">
                    <el-button v-if="active==0" style=" margin-top: 2vh;" type="warning" size="mini"
                        @click="deviceStockConfirm">下一步
                    </el-button>
                    <!-- <el-button style=" margin-top: 2vh;" type="info" size="mini" @click="exitmodal">取 消</el-button> -->
                </span>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    export default {
        props: ["show", "cleanlogs"],
        name: "Outsourcing",
        data() {
            return {
                scanValue: '',
                scanValue2: '',
                dialogShow: this.show,
                smSusuccess: '',
                stock: {},
                active: 0,
            };
        },
        watch: {
            show: function (newValue, oldValue) {
                this.dialogShow = newValue;
            },
            dialogShow: function (newValue, oldValue) {
                if (this.show != this.dialogShow)
                    this.$emit("update:show", this.dialogShow);
            }
        },
        methods: {
            handleClose(done) {
                this.smSusuccess = ''
                this.exitmodal(), done();
            },
            onScan: function () {
                var scanValueTmp = this.scanValue
                this.scanValue = ''
                if (scanValueTmp.indexOf("|") > 0) {
                    scanValueTmp = scanValueTmp.split('|')[0];
                }

                this.smSusuccess = ''
                var self = this
                //获取HU信息
                this.$http.request({
                    url: '/api/package/' + scanValueTmp,
                    method: 'GET',
                    showLoading: true
                }).then(function (resp) {
                    var pckgs = resp.data
                    if (resp.status != 100) {
                        self.smSusuccess = resp.message
                        return
                    }
                    if (resp.data == null || resp.data.length == 0) {
                        self.smSusuccess = "未查询到合格证信息"
                        return
                    }
                    self.stock = resp.data[0];
                });
            },
            onScan2() {
                var self = this
                this.$http.request({
                    url: '/api/po_stack/clean_confirm/' + this.scanValue2,
                    method: 'POST'
                }).then(function (resp) {
                    if (resp.status == 100) {
                        self.exitmodal();
                        self.dialogShow = false;
                        self.$message({
                            message: '成功',
                            type: 'success'
                        });
                    } else {
                        self.smSusuccess = resp.message
                    }
                })
                this.scanValue2 = ''
            },
            deviceStockConfirm: function () {
                if (!this.stock.boxQuan) {
                    this.smSusuccess = '请先扫描过程码?'
                    return
                }
                var self = this;
                var lineId = this.$ls.get('line').lineId
                if (this.lineId)
                    lineId = this.lineId
                this.$http.request({
                    url: '/api/prod_wip/wip_out',
                    method: 'POST',
                    showLoading: true,
                    data: self.stock
                }).then(function (resp) {
                    if (resp.status == 100) {
                        self.$printer.printCleanLog(resp.data)
                        self.active++
                    }
                    else {
                        self.smSusuccess = resp.message
                    }
                })
            },
            open() {
                this.smSusuccess = ''
                this.stock = {}
                this.active = 0
                if (this.cleanlogs.boxBarcodeNew) {
                    this.active++
                }
            },
            //关闭时触发
            exitmodal() {
                this.dialogShow = false;
                this.$emit("exitmodal");
            },
        }
    };
</script>

<style>
</style>


